<template>
    <div class="cell-config-list">
        <div>
            <div class="custom-select" tabindex="1">
                <div class="custom-select-selected">Show/Hide Details</div>
                <div class="custom-select-items">
                    <div
                        class="custom-select-item"
                        v-for="(option, name) of cellSettings"
                        :key="name"
                        @click="checkOptions(option)"
                    >
                        <div class="cell-config">
                            <label>{{ option.name }}</label>
                            <input
                                type="checkbox"
                                v-model="option.value"
                                @change="optionChanged"
                            />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script lang="ts">
import { defineComponent, ref } from "vue";
import { useStore } from "vuex";
import { FlexScheduleCellSetting } from "@/store/flexScheduleModule";

export default defineComponent({
    name: "FlexScheduleCellControl",
    setup() {
        const store = useStore();
        const open = ref(false);
        const optionSelected = ref([]);

        const cellSettings: FlexScheduleCellSetting =
            store.state.FlexSchedule.flexScheduleCellSetting;
        return { open, cellSettings, optionSelected };
    },
    methods: {
        optionChanged() {
            // update user config in local storage
            localStorage.setItem(
                "flexScheduleCellSetting",
                JSON.stringify(this.cellSettings)
            );
        },

        checkOptions(option: any) {
            this.$emit('checkOptions', option)
            // todo: trigger when user click on the row
        },
    },
});
</script>

<style scoped lang="scss">
@use "../../style/_colors";
@use "../../style/layouts";

.custom-select:focus-within {
    .custom-select-selected {
        border: none;
        border-radius: 0;
        border-bottom: 1px solid #c8d1db;
    }

    .custom-select-selected:after {
        transform: rotate(180deg);
        top: 8px;
    }
}

.custom-select-selected {
    background: transparent;
    border: none;
    border-radius: 0;
    border-bottom: 1px solid #c8d1db;
}

.custom-select-selected:after {
    top: 14px;
    right: 0;
}

.cell-config {
    justify-content: space-between;
    padding: 0;
}

.cell-config-list {
    font-size: 14px;

    .custom-select {
        @media screen and (min-width: 768px) {
            width: 180px;
        }
        @media screen and (max-width: 767px) {
            width: 150px;
        }
    }

    @media screen and (max-width: layouts.$xs-screen) {
        font-size: 12px;
    }
}
</style>
