<template>
    <div class="mt-4">
        <div class="left-box">
            <div class="background-legend">
                <div>
                    <div class="flexi-legend"></div>
                    <div class="legend-label">Flexi Schedule</div>
                </div>
                <div>
                    <div class="team-legend"></div>
                    <div class="legend-label">Team Schedule</div>
                </div>
                <div>
                    <div class="personal-legend"></div>
                    <div class="legend-label">Personal Schedule</div>
                </div>
            </div>
        </div>
        <div class="weekDetail d-flex align-center justify-center">
            <span class="arrow-height mr-4" @click="getLastDate()">
                <Icon :icon="$icons.chevronLeft" height="30" :style="{ color: '#0000008a' }"></Icon>
            </span>
            <span @click="chooseCalendarDate">{{ $filters.ordinalSuffix(dayFocus.week()) }} Week,
                {{ dayFocus.format("DD/MM/YYYY") }}</span>
            <span class="arrow-height ml-4" @click="getNextDate()">
                <Icon :icon="$icons.chevronRight" height="30" :style="{ color: '#0000008a' }"></Icon>
            </span>
        </div>
    </div>

    <div class="mt-4" style="overflow: auto">
        <div class="flex-sche-table">
            <div class="table-head">
                <div class="schedule-head-grid">
                    <div class="table-cell">Employee</div>
                    <div class="table-cell">Schedule</div>
                </div>
            </div>
            <perfect-scrollbar>
                <div class="table-body">
                    <div class="schedules-arr" v-for="empNo in scheduleWeekMap.keys()">
                        <div class="content-cell content-employee-name" @click="openEmployeeDetail(empNo)">
                            <span>{{scheduleWeekMap.get(empNo).employeeName}}</span>
                            <span v-if="scheduleWeekMap.get(empNo)
                    .empSchePublishState === 'published'
                " class="published-icon">
                                <span>P</span>
                            </span>
                        </div>
                        <div class="content-cell">
                            <day-cell 
                             :scheDay="scheduleWeekMap.get(empNo).sche"
                             :employeeNo="scheduleWeekMap.get(empNo).employeeNo"
                             :employeeName="scheduleWeekMap.get(empNo).employeeName"
                             :allowEdit="true"
                             :allowCreate="true"
                             :isEmployee="false"
                             @openEditDialog="openEditDialog">
                            </day-cell>
                        </div>
                    </div>
                </div>
            </perfect-scrollbar>
        </div>
    </div>
    <!--    popups -->
    <div>
        <edit-flex-schedule ref="EditModalRef"
            :isEdit="isEditingDialog"
            :weekStatus="weekStatus"
            :modalIndex="1999"
            @swapSche="openSwapSche"
            @reload="reloadAfterEdit"></edit-flex-schedule>
        <date-picker ref="DatePickerRef" :top="datePickerTop" :left="datePickerLeft"></date-picker>
        <extra-emp-detail ref="ExtraEmpDetailRef" :externalDetail="externalDetail"></extra-emp-detail>
        <swap-schedule ref="SwapScheduleRef" :modalIndex="1999" @reload="reloadAfterEdit"></swap-schedule>

    </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref } from "vue";
import moment, { Moment } from "moment";
import { ScheduleDay } from "../FlexScheduleInterface";
import DatePicker from "@/components/common/DatePicker.vue";
import EditFlexSchedule from "@/components/FlexSchedule/EditFlexSchedule.vue";
import DayCell from "@/components/FlexSchedule/DayCell.vue";
import {
    FlexScheduleAction,
    FlexScheduleMutation,
} from "@/store/flexScheduleModule";
import FlexScheduleCellControl from "@/components/FlexSchedule/FlexScheduleCellControl.vue";
import TimePicker from "@/components/common/timePicker/TimePicker.vue";

import { Icon } from "@iconify/vue";
import AutoComplete from "@/components/common/AutoComplete.vue";
import Remote from "@/remote/scheduleRequest";
import { LookupActions } from "@/store/lookupModule";
import DraftCreationDialog from "@/components/FlexSchedule/DraftCreationDialog.vue";
import Dialog from "@/components/dialog";
import {
    EmployeeActions,
    EmployeeGetters,
    ManagedEmployee,
} from "@/store/employeeModule";
import {
    CFRemoteData,
    FlexScheduleRequest,
    FlexScheduleRequestCap,
} from "@/components/FlexSchedule/FlexScheduleInterface";
import ExtraEmpDetail from "@/components/FlexSchedule/ExtraEmpDetail.vue";
import PrintableView from "@/components/FlexSchedule/Print/PrintableView.vue";
import AnalysisChart from "@/components/FlexSchedule/AnalysisChart.vue";
import SetupGridData from "@/components/FlexSchedule/setupGridData";
import PublishDialog from "@/components/FlexSchedule/PublishDialog.vue";
import SwapSchedule from "@/components/FlexSchedule/SwapSchedule.vue";

import CostAnalysisDialog from "@/components/FlexSchedule/CostAnalysisDialog.vue";
import { useRouter } from "vue-router";
import { isMobileV2 } from "@/components/common/script";

declare var Affinity: any;

export enum WeeklyPublishStatus {
    empty = "",
    draft = "draft",
    published = "published",
    partial = "partial",
}

export default defineComponent({
    name: "FlexScheduleM",
    components: {
        CostAnalysisDialog,
        AnalysisChart,
        PrintableView,
        DraftCreationDialog,
        DatePicker,
        TimePicker,
        EditFlexSchedule,
        DayCell,
        FlexScheduleCellControl,
        Icon,
        AutoComplete,
        ExtraEmpDetail,
        PublishDialog,
        SwapSchedule,
    },
    data() {
        return {
            dayFocus: moment(),
            weekDays: <any[]>[], // calendar week days name
            // scheduleWeek: <any[]>[], // list of data per week
            datePickerTop: 0,
            datePickerLeft: 0,
            employeeList: <any>[],
            scheduleWeekMap: new Map(),
            weekStatus: WeeklyPublishStatus.empty,

            //
            showControl: false,
            extraEmpDetail: [],
            isAdmin: false,
            hasFuturePaidDate: false,
        };
    },
    setup() {
        // const store = useStore()
        // store.dispatch(FlexScheduleAction.loadTargetSchedule,6)
        const EditModalRef = ref<InstanceType<typeof EditFlexSchedule>>();
        const ExtraEmpDetailRef = ref<InstanceType<typeof ExtraEmpDetail>>();
        const SwapScheduleRef = ref<InstanceType<typeof SwapSchedule>>();

        const isEditingDialog = ref(false);
        const externalDetail = ref(<CFRemoteData[]>[]);
        const router = useRouter();

        console.log('window.innerWidth', window.innerWidth)
        if (!isMobileV2()) {
            router.push({
                name: "Flex Schedule",
            });
        }

        function openEditDialog(isEdit: false) {
            isEditingDialog.value = isEdit;
            EditModalRef.value!.openEdit();
        }

        function openEmployeeDetail(employeeNo: number) {
            Dialog.loading();
            Remote.GetEmployeeExtraDetail(employeeNo).then(
                (res: CFRemoteData[]) => {
                    externalDetail.value = res.map((data: CFRemoteData) => {
                        if (data.InputType === "DateTime") {
                            data.remoteKeyValue = data.KeyValue;
                            data.remoteValue = data.Value;
                            data.KeyValue = moment(data.KeyValue).format(
                                "ddd DD MMM YYYY"
                            );
                            data.Value = moment(data.Value).format(
                                "ddd DD MMM YYYY"
                            );
                        } else if (data.InputType === "Double") {
                            data.remoteKeyValue = data.KeyValue;
                            data.remoteValue = data.Value;
                            data.KeyValue =
                                Math.round(data.KeyValue * 100) / 100;
                            data.Value = Math.round(data.Value * 100) / 100;
                        }
                        return data;
                    });
                    Dialog.clear();
                    ExtraEmpDetailRef.value!.toggleShow();
                },
                (e) => {
                    Dialog.clear();
                    Dialog.alert("Something went wrong");
                }
            );
        }
        function openSwapSche() {
            SwapScheduleRef.value!.open();
        }

        return {
            EditModalRef,
            ExtraEmpDetailRef,
            SwapScheduleRef,
            isEditingDialog,
            externalDetail,
            openEditDialog,
            openEmployeeDetail,
            openSwapSche,
        };
    },
    async mounted() {
        this.$store.dispatch(
            LookupActions.getPositions,
            Affinity.user.EmployeeNo
        );
        this.$store.dispatch(LookupActions.getShifts);
        this.$store.dispatch(LookupActions.getCostElement);
        this.$store.dispatch(LookupActions.getWorkTypes);

        this.loadDate();

        Remote.GetDefaultWorkTypes().then((res) => {
            // note default worktypes is a array
            this.$store.state.FlexSchedule.defaultWorkTypes = res;
        });

        if (Affinity.user.EmployeeNo >= 5000000) {
            this.isAdmin = true;
        }
    },

    methods: {
        showCopy() {
            if (this.hasFuturePaidDate) {
                return false;
            }
            return moment().diff(this.dayFocus.endOf("week"), "day") <= 0;
        },
        reloadAfterEdit() {
            this.loadDate();
        },

        getCurrentWeekStatus() {
            if (
                this.weekStatus === WeeklyPublishStatus.empty ||
                this.weekStatus === WeeklyPublishStatus.draft
            ) {
                return "Draft";
            } else if (this.weekStatus === WeeklyPublishStatus.partial) {
                return "Partially Published";
            } else if (this.weekStatus === WeeklyPublishStatus.published) {
                return "Published";
            }

            return this.weekStatus;
        },

        async loadDate() {
            this.weekStatus = WeeklyPublishStatus.empty;
            this.showControl = false;
            this.hasFuturePaidDate = false;

            let param = {
                managerEmployeeNo: Affinity.user.EmployeeNo,
                dateFrom: this.dayFocus.format("YYYY-MM-DD"), // dateFrom: '2020-01-01',
                dateTo: this.dayFocus.format("YYYY-MM-DD"), // dateTo: '2021-01-01',
            };

            Dialog.loading();
            let [dateData, leaves, employeeList] = await Promise.all([
                this.$store.dispatch(FlexScheduleAction.loadFlexiSchedules, {
                    startDate: this.dayFocus,
                    endDate: this.dayFocus,
                    employeeNo: Affinity.user.EmployeeNo
                }),
                Remote.GetManagedLeaves(param),
                this.$store
                    .dispatch(
                        EmployeeActions.getEmployees,
                        Affinity.user.EmployeeNo
                    )
                    .then((res: any) => {
                        return res.map((data: any) => {
                            return {
                                // EmployeeName: data.commonName,
                                employeeName: data.commonName,
                                // EmployeeNo: data.employeeNo,
                                employeeNo: data.employeeNo,
                                // StdHoursPerWeek: data.stdHoursPerWeek,
                                stdHoursPerWeek: data.stdHoursPerWeek,
                                // PositionCode: data.positionCode,
                                positionCode: data.positionCode,
                            };
                        });
                    }),
            ]).finally(() => {
                Dialog.clear();
            });
            Dialog.clear();

            let gridDataSet: Map<string, any> =
                SetupGridData.prepareGridDataMobile(
                    this.dayFocus,
                    employeeList,
                    dateData,
                );
            this.scheduleWeekMap = gridDataSet;

            SetupGridData.addUnAvaToGridMobile(dateData, gridDataSet);
            SetupGridData.addLeaveToGridMobile(leaves, gridDataSet);
        },

        checkStatus(scheduleWeekMap: any) {
            let statusSet = new Set();

            for (const employeeSchedule of scheduleWeekMap.values()) {
                employeeSchedule.empSchePublishState = "";
                for (const sche of employeeSchedule.sche) {
                    if (!sche.publishStatus) {
                        // console.log('sche', sche)
                        // if (!statusSet.has(WeeklyPublishStatus.empty)) statusSet.add(WeeklyPublishStatus.empty)
                    } else if (
                        sche.publishStatus === WeeklyPublishStatus.draft
                    ) {
                        // if (!statusSet.has(WeeklyPublishStatus.draft)) statusSet.add(WeeklyPublishStatus.draft)
                        employeeSchedule.empSchePublishState =
                            WeeklyPublishStatus.draft;
                    } else if (
                        sche.publishStatus === WeeklyPublishStatus.published
                    ) {
                        // if (!statusSet.has(WeeklyPublishStatus.published)) statusSet.add(WeeklyPublishStatus.published)
                        employeeSchedule.empSchePublishState =
                            WeeklyPublishStatus.published;
                    }

                    // check schedule has Future Paid Date in current week
                    if (
                        !this.hasFuturePaidDate &&
                        sche.isPaid &&
                        moment(0, "HH").diff(moment(sche.date), "days") <= -1
                    ) {
                        this.hasFuturePaidDate = true;
                    }
                }

                if (employeeSchedule.empSchePublishState === "") {
                    statusSet.add(WeeklyPublishStatus.empty);
                } else {
                    statusSet.add(employeeSchedule.empSchePublishState);
                }
            }

            // if no published show draft
            if (!statusSet.has(WeeklyPublishStatus.published)) {
                this.weekStatus = WeeklyPublishStatus.draft;
            } else {
                // if has publish
                // and has draft or empty show partial
                if (
                    statusSet.has(WeeklyPublishStatus.draft) &&
                    statusSet.has(WeeklyPublishStatus.published)
                ) {
                    this.weekStatus = WeeklyPublishStatus.partial;
                } else if (
                    statusSet.has(WeeklyPublishStatus.empty) &&
                    statusSet.has(WeeklyPublishStatus.published)
                ) {
                    this.weekStatus = WeeklyPublishStatus.partial;
                } else {
                    // if has publish but no draft or empty show published
                    this.weekStatus = WeeklyPublishStatus.published;
                }
            }
        },

        async chooseCalendarDate($event: any) {
            let element = $event.target.getClientRects()[0];
            this.datePickerTop = element.bottom;
            this.datePickerLeft = element.left;
            let datePickerResult;
            try {
                datePickerResult = await (
                    this.$refs.DatePickerRef as any
                ).toggleShow(this.dayFocus);
            } catch (e) {
                return;
            }
            await this.selectDate(datePickerResult);
        },

        async selectDate(date: Moment) {
            this.dayFocus = date;
            this.$store.state.FlexSchedule.calendarFocusDate = date;
            await this.loadDate();
        },
        getSelectedWeek(
            selectedDate: any
        ): { momentDay: Moment; day: string }[] {
            var weekStart = selectedDate.clone().startOf("week");
            var weekEnd = selectedDate.clone().endOf("week");
            let weekDays = [];
            // this.weekDays = [];

            for (let i = 0; i <= 6; i++) {
                let momentDay = moment(weekStart).add(i, "days");
                weekDays.push({
                    momentDay: momentDay,
                    day: momentDay.format("ddd, MMM Do"),
                });
            }

            return weekDays;
        },

        getLastDate() {
            this.dayFocus = this.dayFocus.clone().subtract(1, "days");
            this.$store.state.FlexSchedule.calendarFocusDate = this.dayFocus;
            this.loadDate();
        },

        getNextDate() {
            this.dayFocus = this.dayFocus.clone().add(1, "days");
            this.$store.state.FlexSchedule.calendarFocusDate = this.dayFocus;
            this.loadDate();
        },

        moreDetail(day: ScheduleDay) {
            this.$store.commit(FlexScheduleMutation.setScheduleDay, day);
        },

        getTotalHour(sche: ScheduleDay[]) {
            let totalHours = 0;
            for (let day of sche) {
                if (day.hours) {
                    totalHours += day.hours;
                }
            }
            return totalHours.toFixed(2);
        },

        confirmEdit(day: any) {
            day.editing = false;
        },

        cancelEdit(day: any) { },

        editScheDay(day: any) {
            day.editing = true;
        },

        publishCurrentWeek(employees: number[]) {
            // let groupedSche: any[] = [];

            let weekStart = this.dayFocus.clone().startOf("week");
            let weekEnd = this.dayFocus.clone().endOf("week");
            // let employees = this.$store.getters[EmployeeGetters.getEmployees].map((e: ManagedEmployee) => e.employeeNo)

            // for (const [key, employeeData] of this.scheduleWeekMap.entries()) {
            //     let scheWithData = employeeData.sche.filter((sche: any) => {
            //         return !!sche.Id
            //     })
            //
            //     groupedSche = groupedSche.concat(scheWithData)
            // }

            let data = {
                EmployeeNumbers: employees,
                DateFrom: weekStart.format("YYYY-MM-DD"),
                DateTo: weekEnd.format("YYYY-MM-DD"),
            };
            Dialog.loading();

            Remote.PublishFlexSchedule(data).then(
                () => {
                    this.reloadAfterEdit();
                    Dialog.clear();
                },
                (e: any) => {
                    console.error(e);
                    Dialog.alert("Something went wrong.");
                }
            );
        },

        changeDisplayControl() {
            this.showControl = !this.showControl;
        },
    },
});
</script>

<style scoped lang="scss">
@use "../../../style/layouts";

$aff-blue: #00aeef;
$aff-dark-blue: #0c96e4;
$border-color: #c8d1db;

.schedule-head-grid {
    display: grid;
    width: 100%;
    //grid-template-columns: 12% 12% 12% 12% 12% 12% 12% 12%;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1px;
    height: 23px;
    border-bottom: 1px solid $border-color;
}

.weekDetail {
    font-size: 16px;
    font-weight: bold;
    margin-top: 8px;

    .arrow-height {
        height: 30px;
        margin-top: 1px;
        cursor: pointer;
    }

    @media screen and (max-width: layouts.$xs-screen) {
        margin-top: 24px;
        font-size: 14px;
    }
}

.table-body {
    display: grid;
    grid-gap: 1px;
}

.table-cell {
    padding: 4px 8px;
    text-align: center;
    font-weight: bold;
    font-size: 12px;
    border-bottom: 1px solid $border-color;
}

.schedules-arr {
    display: grid;
    width: 100%;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1px;
    border-bottom: 1px solid $border-color;
}

.flex-sche-table {
    border-collapse: collapse;
}

.content-cell {
    display: grid;
    justify-items: center;
    align-items: center;
    background: white;
    text-align: center;
    min-height: 70px;
}

.table-head {
    //background: linear-gradient(to bottom, #fcfcfc 0%, #efefef 49%, #e2e2e2 51%, #d6d6d6 100%);
    //padding: 3px 5px 3px 5px;
    height: 23px;
}

.ps {
    max-height: calc(100vh - 390px);
    overflow: auto;
}

.status-section {
    position: relative;
}

.show-control-options {
    position: absolute;
    display: flex;
    flex-direction: column;
    padding: 5px;
    margin-top: 5px;
    background: white;
    z-index: 1;
}

.action-button-width {
    width: 120px;
}

.inline-block-middle {
    display: inline-block;
    vertical-align: middle;
}

.content-employee-name {
    position: relative;
    cursor: pointer;

    &:hover {
        background: $aff-blue;
        color: white;
    }
}

.published-icon {
    position: absolute;
    top: 5px;
    right: 5px;
    border-radius: 50%;
    background: #8dca35;
    color: white;
    width: 18px;
    height: 18px;
}

.left-box {
    flex: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.right-box {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.background-legend {
    display: inline-flex;

    >div {
        white-space: nowrap;
        margin-right: 12px;
    }

    @media screen and (max-width: 420px) {
        flex-wrap: wrap;
        font-size: 14px;

        >div {
            width: 50%;
            margin-right: 0;

            &:last-child {
                margin-top: 12px;
            }
        }
    }
}

.legend-label {
    display: inline-block;
    vertical-align: middle;
    margin-left: 4px;
}

.flexi-legend,
.team-legend,
.personal-legend {
    display: inline-block;
    width: 10px;
    padding: 5px;
    border: 1px solid $border-color;
    vertical-align: middle;
    margin-right: 8px;
}

.flexi-legend {
    background: white;
}

.team-legend {
    background: #cfefc6;
}

.personal-legend {
    background: #d1f0ff;
}
</style>
