// import Vue from 'vue'
import {createWebHistory, createRouter, RouteRecordRaw} from 'vue-router'
import mySchedules from "../components/mySchedules/MySchedules.vue";
import SchedulesManagement from "../components/schedulesManagement/SchedulesManagement.vue";
import employeeAllocation from "../components/employeeAllocation/EmployeeAllocation.vue";
import FlexSchedule from "@/components/FlexSchedule/FlexSchedule.vue";
import FlexScheduleM from "@/components/FlexSchedule/Mobile/FlexScheduleM.vue";
import EmployeeTeam from "@/components/EmployeeTeam/EmployeeTeam.vue";
import EmployeeTeamM from "@/components/EmployeeTeam/EmployeeTeamM.vue";
import Availability from "@/components/Availability/Availability.vue";
import Login from "@/components/Login/Login.vue";
import Analysis from "@/components/Analysis/Analysis.vue"
import {isMobile, isMobileV2} from "@/components/common/script";

// Vue.use(VueRouter)

const routes: Array<RouteRecordRaw> = [
    {
        path: '/login',
        name: 'Login',
        component: Login
    },
    {
        path: '/my-schedules',
        name: 'My Schedule',
        component: mySchedules
    },
    {
        path: '/availability',
        name: 'Availability',
        component: Availability
    },
    {
        path: '/flex-schedule',
        name: 'Flex Schedule',
        component: FlexSchedule,
        beforeEnter: (to, from, next) => {
            if (!isMobileV2()) {
                next()
            } else {
                next('/flex-schedule-mobile')
            }
        }
    },
    {
        path: '/flex-schedule-mobile',
        name: 'FlexScheduleMobile',
        component: FlexScheduleM
    },
    {
        path: '/my-team',
        name: 'EmployeeTeamSchedule',
        component: EmployeeTeam,
    },
    {
        path: '/my-team-mobile',
        name: 'EmployeeTeamScheduleMobile',
        component: EmployeeTeamM,
    },

    {
        path: '/team-schedules',
        name: 'Schedule Management',
        component: SchedulesManagement
    },

    {
        path: '/employee-allocations',
        name: 'Employee Allocation',
        component: employeeAllocation
    },
    {
        path: '/cost-reports',
        name: 'Analysis',
        component: Analysis
    },
    {
        path: '/',
        name: 'Home',
        component: mySchedules
    },
]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    // base: process.env.BASE_URL,
    routes
})

export default router
