import { Module } from "vuex";
import { RootState } from "@/store/index";
import Remote from "@/remote/scheduleRequest";
import {Moment} from "moment";

export interface AnalysisState {
    employees: any[];
    simpleViewData: any[];
    detailedCostReportData: any[];
    weeklyAnalysisData: any[];
    employeeWeeklyAnalysisData: any[];
    payDetailViewData: any[];
}

export enum AnalysisMutations {
    setEmployees = "SET_EMPLOYEES",
    setSimpleViewData = "SET_SIMPLE_VIEW_DATA",
    setDetailedCostReportData = "SET_DETAILED_COST_REPORT_DATA",
    setWeeklyAnalysisData = "SET_WEEKLY_ANALYSIS_DATA",
    setEmployeeWeeklyAnalysisData = "SET_EMPLOYEE_WEEKLY_ANALYSIS_DATA",
    setPayDetailViewData = "SET_PAY_DETAIL_VIEW_DATA",
}

export enum AnalysisActions {
    fetchEmployees = "GET_EMPLOYEES",
    fetchSimpleViewData = "GET_SIMPLE_VIEW_DATA",
    fetchDetailedCostReportData = "GET_DETAILED_COST_REPORT_DATA",
    fetchWeeklyAnalysisData = "GET_WEEKLY_ANALYSIS_DATA",
    fetchEmployeeWeeklyAnalysisData = "GET_EMPLOYEE_WEEKLY_ANALYSIS_DATA",
    fetchPayDetailViewData = "GET_PAY_DETAIL_VIEW_DATA",
}


const AnalysisModule: Module<AnalysisState, RootState> = {
    state: {
        employees: [],
        simpleViewData: [],
        detailedCostReportData: [],
        weeklyAnalysisData: [],
        employeeWeeklyAnalysisData: [],
        payDetailViewData: [],
    },
    getters: {
        getEmployees: (state) => state.employees,
        getSimpleViewData: (state) => state.simpleViewData,
        getDetailedCostReportData: (state) => state.detailedCostReportData,
        getWeeklyAnalysisData: (state) => state.weeklyAnalysisData,
        getEmployeeWeeklyAnalysisData: (state) => state.employeeWeeklyAnalysisData,
        getPayDetailViewData: (state) => state.payDetailViewData,
    },
    mutations: {
        [AnalysisMutations.setEmployees]: (state, employees) => {
            state.employees = employees;
        },
        [AnalysisMutations.setSimpleViewData]: (state, simpleViewData) => {
            state.simpleViewData = simpleViewData;
        },
        [AnalysisMutations.setDetailedCostReportData]: (state, detailedCostReportData) => {
            state.detailedCostReportData = detailedCostReportData;
        },
        [AnalysisMutations.setWeeklyAnalysisData]: (state, weeklyAnalysisData) => {
            state.weeklyAnalysisData = weeklyAnalysisData;
        },
        [AnalysisMutations.setEmployeeWeeklyAnalysisData]: (state, employeeWeeklyAnalysisData) => {
            state.employeeWeeklyAnalysisData = employeeWeeklyAnalysisData;
        },
        [AnalysisMutations.setPayDetailViewData]: (state, payDetailViewData) => {
            state.payDetailViewData = payDetailViewData;
        },
    },
    actions: {
        [AnalysisActions.fetchSimpleViewData]: async ({ commit },  payload: {fromDate: string, toDate: string,
            employeeNumbers: any[], pagingEnabled: boolean, pageNumber: number, pageSize: number}) => {
            const param = {
                pageNumber: payload.pageNumber,
                pageSize: payload.pageSize,
                pagingEnabled: payload.pagingEnabled,
                sortBy: "CommonName",
                sortingType: "asc",
                employeeNumbers: payload.employeeNumbers,
                transDateFrom: payload.fromDate,
                transDateTo: payload.toDate
            };

            try {
                const simpleViewData = await Remote.GetCostAnalysisV2(param);
                commit(AnalysisMutations.setSimpleViewData, simpleViewData);
                return simpleViewData
            } catch (error) {
                // Handle error
            }
        },
        [AnalysisActions.fetchDetailedCostReportData]: async ({ commit }, payload: {fromDate: string, toDate: string,
            employeeNumbers: any[], pageNumber: number, pageSize: number}) => {
            const param = {
                // pageNumber: pageNumber.value,
                // pageSize: pageSize.value,
                sortBy: "CommonName",
                sortingType: "asc",
                pagingEnabled: true,
                pageNumber: payload.pageNumber,
                pageSize: payload.pageSize,
                employeeNumbers: payload.employeeNumbers,
                transDateFrom: payload.fromDate,
                transDateTo: payload.toDate
            };

            try {
                const detailedCostReportData = await Remote.GetCostAnalysisDetailV2(param);
                commit(AnalysisMutations.setDetailedCostReportData, detailedCostReportData);
                return detailedCostReportData
            } catch (error) {
                // Handle error
            }
        },
        [AnalysisActions.fetchWeeklyAnalysisData]: async ({ commit }, payload: {weekStart: string, weekEnd: string,
            employeeNumbers: any[], pageNumber: number, pageSize: number, selectedUnit: string}) => {
            const param = {
                sortBy: "CommonName",
                sortingType: "asc",
                pagingEnabled: true,
                pageNumber: payload.pageNumber,
                pageSize: payload.pageSize,
                employeeNumbers: payload.employeeNumbers,
                transDateFrom: payload.weekStart,
                transDateTo: payload.weekEnd
            }

            try {
                const weeklyAnalysisData = await Remote.GetCostingDayView(param, formatSelectUnit(payload.selectedUnit));
                commit(AnalysisMutations.setWeeklyAnalysisData, weeklyAnalysisData);
                return weeklyAnalysisData
            } catch (error) {
                // Handle error
            }
        },
        [AnalysisActions.fetchEmployeeWeeklyAnalysisData]: async ({ commit }, payload: {weekStart: string, weekEnd: string,
            employeeNumbers: any[], pageNumber: number, pageSize: number, selectedUnit: string}) => {
            const param = {
                sortBy: "CommonName",
                sortingType: "asc",
                pagingEnabled: true,
                pageNumber: payload.pageNumber,
                pageSize: payload.pageSize,
                employeeNumbers: payload.employeeNumbers,
                transDateFrom: payload.weekStart,
                transDateTo: payload.weekEnd
            }
            try {
                const employeeWeeklyAnalysisData = await Remote.CostingEmployeeView(param, formatSelectUnit(payload.selectedUnit));
                commit(AnalysisMutations.setEmployeeWeeklyAnalysisData, employeeWeeklyAnalysisData);
                return employeeWeeklyAnalysisData
            } catch (error) {
                // Handle error
            }
        },
        [AnalysisActions.fetchPayDetailViewData]: async ({ commit }, payload: {weekStart: string, weekEnd: string,
            employeeNumbers: any[], pageNumber: number, pageSize: number, selectedUnit: string, payCodes: any[]} ) => {
            const param = {
                sortBy: "CommonName",
                sortingType: "asc",
                pagingEnabled: true,
                pageNumber: payload.pageNumber,
                pageSize: payload.pageSize,
                payCodes: payload.payCodes,
                employeeNumbers: payload.employeeNumbers,
                transDateFrom: payload.weekStart,
                transDateTo: payload.weekEnd
            }
            try {
                const payDetailViewData = await Remote.CostingEmployeePayDetailView(param, formatSelectUnit(payload.selectedUnit));
                commit(AnalysisMutations.setPayDetailViewData, payDetailViewData);
                return payDetailViewData
            } catch (error) {
                // Handle error
            }
        },
    },
};

const formatSelectUnit = (unit: string) => {
    if (unit === 'values') {
        return 'value'
    } else if (unit === 'units') {
        return 'unit'
    }
}

export default AnalysisModule;