import {ActionTree, GetterTree, Module, MutationTree} from "vuex";

import {RootState} from "@/store/index";
import Remote from "@/remote/scheduleRequest";
import {EmployeeGetters, EmployeeState, ManagedEmployee} from "@/store/employeeModule";

export interface EMPPosition {
    positionTitle: string;
    positionCode: string;
}

export interface CostElement {
    id: string;
    costElementType: number;
    description: string
}

export interface ShiftType {
    archive: string
    description: string
    isDefault: string
    shiftType: string
}

export interface WorkType {
    code: number;
    description: string;
    isSelected?: boolean;
}

export interface CostCenter {
    costCentreId: string;
    description: string;
}

export interface LookupState {
    shifts: ShiftType[],
    positions: EMPPosition[],
    costElements: CostElement[],
    workTypes: WorkType[],
    costCenters: CostCenter[]
}

export enum LookupGetters {
    getWorkType = "GET_WORKTYPE"
}

export enum LookupMutations {
    setShifts = "SET_SHIFTS",
    setPosition = "SET_POSITION",
    setCostElements = "SET_COST_ELEMENTS",
    setWorkTypes = "SET_WORK_TYPES",
    setCostCenters = "SET_COST_CENTER"
}

export enum LookupActions {
    getPositions = 'GET_POSITIONS',
    getShifts = 'GET_SHIFTS',
    getCostElement = 'GET_COST_ELEMENT',
    getWorkTypes = "GET_WORK_TYPES",
    getCostCenters = "GET_COST_CENTER"
}

const lookUpGetters: GetterTree<LookupState, RootState> = {
    [LookupGetters.getWorkType] (state): WorkType[] {
        return state.workTypes;
    },
}


const lookupMutations: MutationTree<LookupState> = {
    [LookupMutations.setShifts](state, shifts: any[]) {
        state.shifts = shifts
    },

    [LookupMutations.setPosition](state, positions: EMPPosition[]) {
        state.positions = positions
    },

    [LookupMutations.setCostElements](state, costElements: CostElement[]) {
        state.costElements = costElements
    },

    [LookupMutations.setWorkTypes](state, workTypes: WorkType[]) {
        state.workTypes = workTypes
    },

    [LookupMutations.setCostCenters](state, costCenters: CostCenter[]) {
        state.costCenters = costCenters
    }
}

const lookupActions: ActionTree<LookupState, RootState> = {
    [LookupActions.getPositions] ({ commit }, employeeNo: number) {
        Remote.GetPosition(employeeNo).then(res => {
            let convertedPositions = res.map((positionRemote: any) => {
                return Object.fromEntries(
                    Object.entries(positionRemote).map(([k,v]) => [k.charAt(0).toLowerCase() + k.slice(1), v])
                );
            })
            // state.positions = res
            commit(LookupMutations.setPosition, convertedPositions)
        })
    },

    [LookupActions.getShifts] ({ commit }) {
        Remote.ShiftTypes().then(res => {

            let convertedShifts = res.map((shiftRemote: any) => {
                return Object.fromEntries(
                    Object.entries(shiftRemote).map(([k,v]) => [k.charAt(0).toLowerCase() + k.slice(1), v])
                );
            })
            // state.positions = res
            commit(LookupMutations.setShifts, convertedShifts)
        })
    },

    [LookupActions.getCostElement] ({ commit }) {
        Remote.GetCostelements().then(res => {

            let convertedCostElement = res.map((element: any) => {
                return Object.fromEntries(
                    Object.entries(element).map(([k,v]) => [k.charAt(0).toLowerCase() + k.slice(1), v])
                );
            })

            // state.positions = res
            commit(LookupMutations.setCostElements, convertedCostElement)
        })
    },

    async [LookupActions.getWorkTypes] ({ commit }) {
        return Remote.GetWorkTypes().then(res => {

            let convertedWorkType = res.map((element: any) => {
                return Object.fromEntries(
                    Object.entries(element).map(([k,v]) => [k.charAt(0).toLowerCase() + k.slice(1), v])
                );
            })

            convertedWorkType.map((type: WorkType) => {
                type.isSelected = false;
            })

            commit(LookupMutations.setWorkTypes, convertedWorkType)
        })
    },

    [LookupActions.getCostCenters] ({ commit }, costCenterIds) {
        return Remote.GetEmployeesCostCentres(costCenterIds).then(res => {

            let convertedCostElement = res.data.map((element: any) => {
                return Object.fromEntries(
                    Object.entries(element).map(([k,v]) => [k.charAt(0).toLowerCase() + k.slice(1), v])
                );
            })

            commit(LookupMutations.setCostCenters, convertedCostElement)
            return convertedCostElement;
        })
    },

}

const LookupSelections: Module<LookupState, RootState> = {
    state: {
        shifts: [],
        positions: [],
        costElements: [],
        workTypes: [],
        costCenters: []
    },
    mutations: lookupMutations,
    actions: lookupActions
}

export default LookupSelections;
