<template>
    <div>
        <span>
            <div class="custom-select" v-bind:style="{ width: width ? width + 'px' : null }" tabindex="1" ref="selectRef" @mouseenter="setSelectItemsPosition()">
                <div v-if="modelValue" class="custom-select-selected">
                    <span v-if="modelValue
                            && typeof modelValue === 'object'
                            && displayKey">
                        {{ modelValue[displayKey] }}
                    </span>
                    <span v-else-if="modelValue">{{ modelValue }}</span>
                    <span v-else class="custom-select-empty-selected"></span>
                </div>
                <div v-else class="custom-select-selected">
                    <span v-if="selected && typeof selected === 'object'">
                        {{ selected[displayKey] }}
                    </span>
                    <span v-else-if="selected">{{ selected }}</span>
                    <span v-else class="custom-select-empty-selected"></span>
                </div>
                <div class="custom-select-items"
                     v-bind:style="{ left: left + 'px',
                                     top: top + 'px',
                                     width: width ? width + 'px' : null }">
                    <div class="custom-select-item" v-for="(option, index) of options"
                         :key="index" @click="selectOption($event, option)">
                        <div class="cell-config">
                            <label v-if="displayKey">
                                {{ option[displayKey] }}</label>
                            <label v-else>{{ option }}</label>
                            <label></label>
                        </div>
                    </div>
                </div>
            </div>
        </span>
    </div>
</template>

<script lang="ts">
import { defineComponent, ref } from "vue";

export default defineComponent({
    name: "CustomSelect",
    props: {
        options: {
            type: Array,
            default: [],
        },
        displayKey: {
            type: String,
            default: "",
        },
        modelValue: {
            type: [String, Number],
        },
        width: {
            type: Number,
        },
        selected: {},
    },
    data: function () {
        return {
            left: 0,
            top: 0,
        };
    },
    mounted() {
        const rect = (this.$refs as any).selectRef.getClientRects()[0];
        this.left = rect.left;
        this.top = rect.top + rect.height;
    },
    methods: {
        selectOption($event: any, option: any) {
            $event.target.blur();
            let b = this.$refs.selectRef as any;
            b.blur();

            if (this.modelValue) {
                this.$emit("update:modelValue", option);
            } else {
                this.$emit("selectOption", option);
            }
        },
        setSelectItemsPosition() {
            const rect = (this.$refs as any).selectRef.getClientRects()[0];
            this.left = rect.left;
            this.top = rect.top + rect.height;
        },
    },
});
</script>

<style lang="scss" scoped>
@use "../../style/layouts";

.custom-select-selected:after {
    top: calc(50% - 3px);
    right: 10px;
}

.custom-select-items {
    position: fixed;
    width: 150px;

    @media screen and (max-width: layouts.$xs-screen) {
        overflow: auto;
        max-height: 300px;
    }
}

.custom-select-empty-selected {
    display: inline-block;
    width: 1px;
    height: 15px;
}

.cell-config {
    label {
        cursor: pointer;
    }
}
</style>
