import {Moment} from "moment";

export interface ScheduleWeek {
    empName: string;
    scheDays: ScheduleDay[];
}

export interface EmployeeData {
    employeeName: string,
    employeeNo: number,
    stdHoursPerWeek: number,
    positionCode: string
    scheDays: ScheduleDay[];
}

export interface ScheduleDay {
    id?: number;
    scheduleDetail?: any;
    date?: string;
    startTime?: string,
    endTime?: string,
    hours?: number,
    schedule: string,
    dayInWeek: number,
    allowCreateOrEdit?: boolean,
    isPaid?: boolean,
    notes?: string,
}


export interface FlexScheduleRequestCap {
    FlexSchedules: any[];
    Unavailabilities: any[];
}

export interface FlexScheduleRequest {
    flexSchedules: FlexScheduleR[];
    unAvailabilities: any[];
}

export interface FlexScheduleR {
    id: number,
    date: string,
    employeeName: string,
    employeeNo: number,
    publishStatus: string,
    startTime?: string,
    StartTime?: string,
    endTime?: string,
    EndTime?: string,
    hours: number,
    standardHoursPerWeek: number,
    scheduleType: string,
    scheduleData: ScheduleDataR
}

export interface ScheduleDataR {
    breakLength: number,
    costElement1: null,
    costElement2: null,
    position: null
    positionTitle: string
    shiftId: null
}

export interface ScheduleUnAvailable {
    unavailabilityDate: string,
    empAva: EmpUnAvailable
}

export interface EmpUnAvailable {
    id: number,
    guid: string,
    employeeNo: number,
    startTime: string,
    endTime: string,
    dayOfWeek: string,
    dayOfMonth: number,
    frequencyType: string,
    startDate: string,
    endDate: string | null,
    reason: string,
    deleted: string
}

