// import 'babel-polyfill'
// import Vue from 'vue'
import {createApp, ref} from 'vue'
import App from './App.vue'
import router from './router'
// import store, {key} from './store'
import store from './store'
// import vuetify from './plugins/vuetify';
// import Dashboard from '../external-lib/dashboard/index.vue'
// import './components/directive';
import './style/dashboard.default.less'
import './assets/fonts/ui.font.icons.scss';
import './style/index.scss';
import moment from 'moment'
import mitt, {Emitter} from "mitt";
import FloatingVue from 'floating-vue'
import customFilters from "@/plugins/filters";
import iconifyIcons from './plugins/iconifyIcons';
import companyConfig from "@/plugins/companyConfig";
import PerfectScrollbar from 'vue3-perfect-scrollbar'
import 'floating-vue/dist/style.css'
import './tool-tip.scss'
import 'vue3-perfect-scrollbar/dist/vue3-perfect-scrollbar.css'

export const emitter: Emitter = mitt();

declare var window: any;

// Vue.use(VTooltip, {
//     defaultBoundariesElement: 'window'
// })

// VTooltip.enabled = window.innerWidth > 768


navigator.serviceWorker.getRegistrations().then(function (registrations) {
    for (let registration of registrations) {
        registration.unregister()
    }
})

window.$globalConfig = {t: ''};
window.$globalUser = {t: ''};


window.myVue = createApp(App)
window.myVue.config.globalProperties.emitter = emitter

//TODO: investigate move this to inject and provide
window.myVue.config.globalProperties.$config = ref(window.$globalConfig);
window.myVue.config.globalProperties.$user = ref(window.$globalUser);
window.myVue.use(FloatingVue)
window.myVue.use(PerfectScrollbar)
window.myVue.use(companyConfig)

// app.use(store, key).use(router).mount('#app')
window.myVue.use(store).use(router).mount('#app')



window.myVue.config.globalProperties.$filters = customFilters
window.myVue.config.globalProperties.$icons = iconifyIcons

