import { App } from 'vue';

const CompanyConfig: any = {}

export interface CompanyConfigInterface {
    setAllowPersonal: (allow: boolean) => void;
    setAllowFlexi: (allow: boolean) => void;
    setAllowSundayFirst: (allow: boolean) => boolean;
    getAllowSundayFirst: () => boolean;
}

/*
    Company Config plugin, build to manage global properties company config.
 */

export default {
    install: (app: App) => {
        function setAllowPersonal(allow: boolean): void {
            if (!app.config.globalProperties.$CompanyConfig) {
                app.config.globalProperties.$CompanyConfig = {
                    isAllowPersonal: allow
                }
            } else {
                app.config.globalProperties.$CompanyConfig.isAllowPersonal = allow
            }
        }

        function setAllowSundayFirst(allow: boolean): void {
            if (!app.config.globalProperties.$CompanyConfig) {
                app.config.globalProperties.$CompanyConfig = {
                    isAllowSundayFirst: allow
                }
            } else {
                app.config.globalProperties.$CompanyConfig.isAllowSundayFirst = allow
            }
        }

        function getAllowPersonal(): boolean {
            return app.config.globalProperties.$CompanyConfig ? app.config.globalProperties.$CompanyConfig.isAllowPersonal : null;
        }

        function getAllowSundayFirst(): boolean {
            return app.config.globalProperties.$CompanyConfig ? app.config.globalProperties.$CompanyConfig.isAllowSundayFirst : null;
        }

        CompanyConfig.setAllowPersonal = setAllowPersonal;
        CompanyConfig.getAllowPersonal = getAllowPersonal;
        CompanyConfig.setAllowSundayFirst = setAllowSundayFirst;
        CompanyConfig.getAllowSundayFirst = getAllowSundayFirst;

        app.provide('MANAGE_COMPANY_CONFIG', CompanyConfig)
    }
}