
<template>
    <Modal ref="ModalRef" 
        @cancel="cancelSwap">
        <template v-slot:header>
            <div>
                <h2 class="bold">
                    <span>Swap Schedules</span>    
                </h2>
            </div>
        </template>
        <template v-slot:body>
            <div class="flex swap-body mt-4">
                <div class="swap-sche-body edit-form ">
                    <h3>Schedule 1</h3> <div></div>
                    <label>Date</label>
                    <DatePickerInput 
                        :initSourceData="sch1Data.date"
                        :minDate="minDate"
                        @updateDate="updateScheDate1">
                    </DatePickerInput>    
                    <label>Employee Name</label>
                    <!-- <label>{{ sch1Data.employeeName }}</label> -->
                    <custom-select
                        :options="employeeList"
                        :displayKey="'commonName'"
                        :selected="sch1Data.employee"
                        @selectOption="selectEmployee1">
                    </custom-select>
                    <label>Employee No</label>
                    <label>{{  sch1Data.employeeNo }}</label>

                    <label>Position</label>
                    <span>{{ sch1Data.scheduleDetail?.positionTitle }}</span>
                    <label>Start Time</label>
                    <span>{{ sch1Data.startTime }}</span>
                    <label>Finish Time</label>
                    <span>{{ sch1Data.endTime }}</span>
                    <label>Unpaid Break</label>
                    <span>{{ sch1Data.scheduleDetail?.breakLength }}</span>
                    <label>Net Hours</label>
                    <span>{{ sch1Data.hours }}</span>
                    <template v-if="sch1Data.scheduleDetail?.shiftId">
                        <label>Shift ID</label>
                        <span>{{sch1Data.scheduleDetail?.shiftId}}</span>
                    </template>
                    
                    <label>Notes</label>
                    <span>{{sch1Data.notes}}</span>
                </div>
                <div class="swap-sche-body edit-form mobile-margin-top">
                    <h3>Schedule 2</h3> <div></div>
                    <label>Date</label> 
                    <DatePickerInput 
                        :initSourceData="sch2Data.date"
                        :minDate="minDate"
                        @updateDate="updateScheDate2">
                    </DatePickerInput>    
                    <label>Employee Name</label>
                    <custom-select
                        :options="employeeList"
                        :displayKey="'commonName'"
                        :selected="sch2Data.employee"
                        @selectOption="selectEmployee2">
                    </custom-select>
                    <label>Employee No</label>
                    <label>{{  sch2Data.employeeNo }}</label>
                    <template v-if="sch2Data && sch2Data.employee && sch2Data.date">
                        <label>Position</label>
                        <span>{{ sch2Data.scheduleDetail?.positionTitle }}</span>
                        <label>Start Time</label>
                        <span>{{ sch2Data.startTime }}</span>
                        <label>Finish Time</label>
                        <span>{{ sch2Data.endTime }}</span>
                        <label>Unpaid Break</label>
                        <span>{{ sch2Data.scheduleDetail?.breakLength }}</span>
                        <label>Net Hours</label>
                        <span>{{ sch2Data.hours }}</span>
                        <template v-if="sch2Data.scheduleDetail?.shiftId">
                            <label>Shift ID</label>
                            <span>{{sch2Data.scheduleDetail?.shiftId}}</span>
                        </template>
                        <label>Notes</label>
                        <span>{{sch2Data.notes}}</span>
                    </template>
                    
                </div>
            </div>
        </template>
        <template v-slot:footer>
            <div class="mt-4 full-width">
                <div class="d-flex justify-end">
                    <button class="white small copy2" @click="swap()">
                        Swap
                    </button>
                    <button class="orange small copy2 ml-4" @click="close()">
                        Cancel
                    </button>
                </div>
            </div>
        </template>
    </Modal>
</template>

<script lang="ts">

import { defineComponent, nextTick, ref } from "vue";
import Modal from "@/components/common/Modal.vue";
import CustomSelect from "@/components/common/CustomSelect.vue";
import moment, { Moment } from "moment";
import DatePickerInput from "@/components/common/DatePickerInput.vue";
import { ScheduleDay } from "./FlexScheduleInterface";
import _ from "lodash";
import { useStore } from 'vuex';
import { EmployeeGetters, ManagedEmployee } from "@/store/employeeModule";
import Remote from "@/remote/scheduleRequest";
import { keysToLowerCase } from "../common/script";
import { ScheduleDaySwap, swapSche } from "./Dialogs/swapRequst";
import Dialog from "@/components/dialog";



export default defineComponent({
    components: {
        Modal,
        CustomSelect,
        DatePickerInput
    },
    setup(props, context) {
        const store = useStore(); 
        const employeeList = ref([]);
        const sch1Data = ref<ScheduleDaySwap>({} as ScheduleDaySwap);
        const sch2Data = ref<ScheduleDaySwap>({} as ScheduleDaySwap);
        const minDate = ref<Moment>(moment().subtract(2, 'days'));

        // create const modalRef
        const ModalRef = ref<InstanceType<typeof Modal>>();

        sch2Data.value.date = moment().format();
        console.log('employeeList', employeeList.value)

        function selectEmployee1(employee: any) {
            console.log('selectEmployee1', employee)
            sch1Data.value.employee = employee;
            sch1Data.value.employeeName = employee.employeeName;
            sch1Data.value.employeeNo = employee.employeeNo;
            nextTick(getSch1Data);
        }

        function selectEmployee2(employee: any) {
            console.log('selectEmployee2', employee)
            sch2Data.value.employee = employee;
            sch2Data.value.employeeName = employee.employeeName;
            sch2Data.value.employeeNo = employee.employeeNo;
            nextTick(getSche2Data);
        }

        function getSch1Data() {
            if (!validateDateAndEmployee(sch1Data.value)) {
                return 
            }

            console.log('sch2Data', sch1Data.value)
            const requestDate = moment(sch1Data.value.date).format('YYYY-MM-DD');
            const param = {
                employeeNo: sch1Data.value.employeeNo,
                startDate: requestDate, // dateFrom: '2020-01-01',
                endDate: requestDate, // dateTo: '2021-01-01',
                forManagedEmployees: false,
            };

            Remote.GetFlexSchedules(param)
            .then(res => {
                console.log('res', res)
                const employeeSche = res.FlexSchedules[0]
                if (!employeeSche) {
                    clearSche1Data();
                    return
                }
                sch1Data.value.scheduleDetail = keysToLowerCase(employeeSche.ScheduleData);
                sch1Data.value.startTime =  employeeSche.StartTime ? moment(employeeSche.StartTime).format('HH:mm') : '';
                sch1Data.value.endTime = employeeSche.EndTime ? moment(employeeSche.EndTime).format('HH:mm') : '';
                sch1Data.value.hours = employeeSche.Hours;
                sch1Data.value.scheduleType = employeeSche.ScheduleType;
                
                sch1Data.value.notes = employeeSche.Notes;
                sch1Data.value.id = employeeSche.Id;
            })

            // call backend to get sch2Data
        }
        function getSche2Data() {
            if (!validateDateAndEmployee(sch2Data.value)) {
                return 
            }
            console.log('sch2Data', sch2Data.value)
            const requestDate = moment(sch2Data.value.date).format('YYYY-MM-DD');
            const param = {
                employeeNo: sch2Data.value.employeeNo,
                startDate: requestDate, // dateFrom: '2020-01-01',
                endDate: requestDate, // dateTo: '2021-01-01',
                forManagedEmployees: false,
            };

            Remote.GetFlexSchedules(param)
            .then(res => {
                console.log('res', res)
                const employeeSche = res.FlexSchedules[0]
                if (!employeeSche) {
                    clearSche2Data();
                    return
                }
                sch2Data.value.scheduleDetail = keysToLowerCase(employeeSche.ScheduleData);
                sch2Data.value.startTime =  employeeSche.StartTime ? moment(employeeSche.StartTime).format('HH:mm') : '';
                sch2Data.value.endTime = employeeSche.EndTime ? moment(employeeSche.EndTime).format('HH:mm') : '';
                sch2Data.value.hours = employeeSche.Hours;
                sch2Data.value.notes = employeeSche.Notes;
                sch2Data.value.scheduleType = employeeSche.ScheduleType;
                sch2Data.value.id = employeeSche.Id;
                sch2Data.value.publishStatus = employeeSche.PublishStatus;
            })
            // call backend to get sch2Data
        }

        function updateScheDate2(date: Moment) {
            sch2Data.value.date = date.format();
            nextTick(getSche2Data);
        }

        function updateScheDate1(date: Moment) {
            sch1Data.value.date = date.format();
            nextTick(getSch1Data);
        }

        function clearSche1Data() {
            sch1Data.value.scheduleDetail = null;
            sch1Data.value.startTime = undefined;
            sch1Data.value.endTime = undefined;
            sch1Data.value.hours = undefined;
            sch1Data.value.notes = undefined;
            sch1Data.value.scheduleType = '';
            sch1Data.value.id = undefined;
        }

        function clearSche2Data() {
            sch2Data.value.scheduleDetail = null;
            sch2Data.value.startTime = undefined;
            sch2Data.value.endTime = undefined;
            sch2Data.value.hours = undefined;
            sch2Data.value.notes = undefined;
            sch2Data.value.scheduleType = '';
            sch2Data.value.id = undefined;
        }

        function getEmployeeFromNumber(employeeNo: number) {
            return employeeList.value.find((employee: ManagedEmployee) => employee.employeeNo === employeeNo);

        }

        function validateDateAndEmployee(targetData: ScheduleDaySwap) {
            if (targetData.date === '' || targetData.employee === null) {
                return false;
            }
            return true;
        }

        function open() {
            ModalRef.value!.toggleShow(true);
            sch1Data.value = _.clone(store.state.FlexSchedule.selectedFlexDay);
            employeeList.value = store.getters[EmployeeGetters.getEmployees];
            console.log('flex day', sch1Data.value)
            sch1Data.value.employee = getEmployeeFromNumber(sch1Data.value.employeeNo)!;    
            sch2Data.value.date = moment(sch1Data.value.date).add(1, 'days').format();
        }

        async function swap() {
            console.log('sch1Data', sch1Data.value)
            console.log('sch2Data', sch2Data.value)
            if (!sch1Data.value.hours) {
                Dialog.alert('Please select an employee and date for schedule 1.')
                return 
            }

            if (!sch2Data.value.employee) {
                Dialog.alert('Please select an employee for schedule 2.')
                return 
            }

            if (!sch2Data.value.date) {
                Dialog.alert('Please select the date for schedule 2.')
                return 
            }

            if (!sch2Data.value.employee && !sch2Data.value.date) {
                Dialog.alert('Please select an employee and date for schedule 2.')
                return 
            }

            if (!sch2Data.value.hours) {
                Dialog.alert('The schedule 2 employee currently has no schedule.')
                return 
            }

            // publishStatus === 'published'
            if (sch1Data.value.publishStatus !== 'published' || sch2Data.value.publishStatus !== 'published') {
                Dialog.alert('You can only swap published schedules.')
                return
            }

            Dialog.confirmOrCancel({
                message: "Are you sure you want to swap this schedule?",
            }).then(async () => {
                try {
                    Dialog.loading();
                    await swapSche(sch1Data.value, sch2Data.value)
                    close();
                    Dialog.clear();
                    context.emit('reload')
                } catch (e) {
                    Dialog.alert(
                        "Swap Schedule failed."
                    );
                }
            }, () => {
                Dialog.clear();
            });
        }

        function clearAll() {
            clearSche1Data();
            clearSche2Data();
            sch2Data.value.employee = null as any;
            sch2Data.value.employeeNo = null as any;
        }

        function close() {
            clearAll();
            ModalRef.value!.toggleShow(false);
        }
       
        function cancelSwap() {
            clearAll();
        }
        return {
            employeeList,
            sch1Data,
            sch2Data,
            minDate,
            ModalRef,
            selectEmployee1,
            selectEmployee2,
            updateScheDate1,
            updateScheDate2,
            open,
            close,
            cancelSwap,
            swap,
            getSch1Data,
            getSche2Data,
        }
    },
})
</script>

<style scoped lang="scss">
@use "../../style/colors";
@use "../../style/layouts";
.swap-body {
    display: flex;
    min-width: 700px;

    @media screen and (max-width: 768px){
        display: block;
        min-width: auto;
        overflow-y: auto;
    }
}

.swap-sche-body {
    width: 50%;
}

.edit-form {
    display: grid;
    grid-template-columns: 150px 160px;
    grid-template-rows: repeat(9, min-content);
    row-gap: 8px;
    align-items: center;

    input {
        height: 30px;
        border-radius: 5px;
        border: 1px solid colors.$grey;
        padding: 0 10px;

        &:focus {
            outline: none;
        }
    }

    @media screen and (max-width: layouts.$xs-screen) {
        font-size: 12px;
        // grid-template-columns: 120px 120px;
    }
}

.mobile-margin-top {
    @media screen and (max-width: 768px){
        margin-top: 20px;
        
    }
}
</style>