<template>
    <!--    <div v-model="dialog"  content-class="dialog-width">-->
    <modal ref="alertModalRef" :noBox="true">
        <template v-slot:body>
            <div class="promt-box">
                <div>
                    <div class="mr-2 d-inline loader-icon" v-if="showLoader">
                        <svg
                            viewBox="0 0 1024 1024"
                            focusable="false"
                            data-icon="loading-3-quarters"
                            width="28"
                            height="28"
                            fill="currentColor"
                            aria-hidden="true"
                        >
                            <path
                                d="M512 1024c-69.1 0-136.2-13.5-199.3-40.2C251.7 958 197 921 150 874c-47-47-84-101.7-109.8-162.7C13.5 648.2 0 581.1 0 512c0-19.9 16.1-36 36-36s36 16.1 36 36c0 59.4 11.6 117 34.6 171.3 22.2 52.4 53.9 99.5 94.3 139.9 40.4 40.4 87.5 72.2 139.9 94.3C395 940.4 452.6 952 512 952c59.4 0 117-11.6 171.3-34.6 52.4-22.2 99.5-53.9 139.9-94.3 40.4-40.4 72.2-87.5 94.3-139.9C940.4 629 952 571.4 952 512c0-59.4-11.6-117-34.6-171.3a440.45 440.45 0 00-94.3-139.9 437.71 437.71 0 00-139.9-94.3C629 83.6 571.4 72 512 72c-19.9 0-36-16.1-36-36s16.1-36 36-36c69.1 0 136.2 13.5 199.3 40.2C772.3 66 827 103 874 150c47 47 83.9 101.8 109.7 162.7 26.7 63.1 40.2 130.2 40.2 199.3s-13.5 136.2-40.2 199.3C958 772.3 921 827 874 874c-47 47-101.8 83.9-162.7 109.7-63.1 26.8-130.2 40.3-199.3 40.3z"
                            ></path>
                        </svg>
                    </div>

                    <div class="content" v-html="message"></div>
                </div>

                <div class="promptButtons mt-4" v-if="showButtons">
                    <button
                        type="button"
                        class="orange white--text small"
                        v-bind:class="{ 'mr-2': showCancel }"
                        tabindex="9999998"
                        @click="onOk"
                    >
                        <!--                    <v-icon small>icon-tick</v-icon>-->
                        <span class="label text-capitalize">{{
                            confirmButtonText
                        }}</span>
                    </button>

                    <button
                        class="grey white--text small"
                        v-if="showCancel"
                        small
                        tabindex="9999999"
                        @click="onCancel"
                    >
                        <!--                    <v-icon class="mr-1" small>icon-cancel</v-icon>-->
                        <span class="label text-capitalize">{{
                            cancelButtonText
                        }}</span>
                    </button>
                </div>
            </div>
        </template>

        <!--        <div v-if="showLoader">-->
        <!--            <img src="data:image/gif;base64,">-->
        <!--            <div class="content" v-html="message" ></div>-->
        <!--        </div>-->
    </modal>
</template>

<script lang="ts">
import { emitter } from "../main";
import Modal from "@/components/common/Modal.vue";
import { defineComponent } from "vue";

export default defineComponent({
    name: "alert",
    components: {
        Modal,
    },
    data() {
        return {
            dialog: false,
            message: "some message ",
            showLoader: false,
            showButtons: false,
            showCancel: false,
            confirmButtonText: "Ok",
            cancelButtonText: "Cancel",
        };
    },
    mounted() {
        // console.log('mounted in alert')
        emitter.on("alert", (message) => {
            console.log("emitter alert");
            this.dialog = true;
            this.message = message;
            this.showLoader = false;
            this.showButtons = true;
        });

        emitter.on("dialog--loading--request", (message) => {
            console.log("emitter loading request");
            this.dialog = true;
            this.message = message;
            this.showCancel = false;
            this.showButtons = false;
            this.showLoader = true;
        });

        emitter.on("clearDialog", () => {
            console.log("emitter clear dialog");
            this.clearDialog();
            this.dialog = false;
        });
        emitter.on("dialog", (inputObj) => {
            console.log("emitter on dialog");
            this.show();
            this.dialog = !!inputObj;
            this.message = inputObj.message;
            this.showCancel = inputObj.showCancel;
            this.showButtons = inputObj.showButtons;
            this.showLoader = inputObj.showLoader;
            this.confirmButtonText = inputObj.confirmButtonText
                ? inputObj.confirmButtonText
                : "Ok";
            this.cancelButtonText = inputObj.cancelButtonText
                ? inputObj.cancelButtonText
                : "Cancel";

            if (inputObj.onOk) {
                this.onOk = () => {
                    inputObj.onOk();
                    this.dialog = false;
                    this.toggleShow();
                };
            }

            if (inputObj.onCancel) {
                this.onCancel = () => {
                    inputObj.onCancel();
                    this.dialog = false;
                    this.toggleShow();
                };
            }
        });
    },
    methods: {
        // onOk() {
        //     this.dialog = false;
        // },
        // onCancel() {
        //     this.dialog = false;
        // },
        clearDialog() {
            (this.$refs.alertModalRef as any).toggleShow(false);
        },
        toggleShow() {
            (this.$refs.alertModalRef as any).toggleShow();
        },
        show() {
            (this.$refs.alertModalRef as any).toggleShow(true);
        },
        onOk() {
            (this.$refs.alertModalRef as any).toggleShow();
        },
        onCancel() {
            (this.$refs.alertModalRef as any).toggleShow();
        },
    },
});
</script>

<style scoped>
:deep(.dialog-width) {
    width: auto;
    max-width: 800px;
}

.promt-box {
    font-size: 16px;
    background: #fff;
    color: #333;
    text-align: center;
    line-height: 1.2;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    border-radius: 10px;
    z-index: 10000000;
    box-shadow: 0 5px 20px #00378033;
    max-width: 480px;
}

.promt-box .promptButtons button {
    min-width: 75px;
}

@media screen and (min-width: 768px) {
    .promt-box {
        padding: 32px;
    }
}

@media screen and (max-width: 767px) {
    .promt-box {
        padding: 24px;
    }
}

.content {
    display: inline-block;
    vertical-align: middle;
    text-align: center;
}

.loader-icon {
    vertical-align: middle;
    color: #1268b3;
    animation: spin 750ms linear infinite;
    width: 28px;
    height: 28px;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
</style>
