import moment from "moment";

const filters = {
    reformatDate(input: any) {
        if (!input) return input

        if (typeof input === "string") return input

        // console.log('input', input)
        if (typeof input === 'object' && input.constructor?.name === "Date") {
            let date = moment(input);

            return date.format('DD/MM/YYYY')
        }

        if (input.isValid()) {
            let date = moment(input);
            if (date == null || !date.isValid()) {
                return input;
            }

            // console.log('date', date)
            return date.format('DD/MM/YYYY')
        } else {
            return input
        }
    },

    reformatDateString(input: any, format: string) {
        if (!input || typeof input !== "string") return input

        return moment(input, format).format('DD/MM/YYYY')
    },

    reformatTime(input: any) {
        if (!input) return input

        let date = moment(input);
        if (date == null || !date.isValid()) {

            let time = moment(input, 'h:m');
            if (time == null || !time.isValid()) {
                return input;
            } else {
                return time.format('HH:mm')
            }
            return input;
        }

        return date.format('HH:mm')
    },

    reformatBreakHours(breakHours: any) {
        if (!breakHours) return breakHours;

        let n = breakHours;
        let hourU = Math.floor(n)
        let minU = n - hourU;
        if (minU > 0) {
            minU = Math.round(minU * 60)
        }
        if (hourU && minU) {
            return `${hourU}:${minU}`
        } else if (hourU) {
            return `${hourU} hour`
        } else if (minU) {
            return `${minU} min`
        }
    },

    ordinalSuffix(i: number) {
        let j = i % 10,
            k = i % 100;
        if (j == 1 && k != 11) {
            return i + "st";
        }
        if (j == 2 && k != 12) {
            return i + "nd";
        }
        if (j == 3 && k != 13) {
            return i + "rd";
        }
        return i + "th";
    }
}

export default filters