<template>
    <div class="schedules-data-medium mt-4">
        <div class="flex-sche-table-wrap">
            <div class="flex-sche-table">
                <perfect-scrollbar>
                    <div class="table-head" v-bind:style="tableMinWidthStyle">
                        <div class="schedule-head-grid" v-bind:style="gridColumnsStyle">
                            <div class="table-cell table-head-corner"></div>
                            <template v-for="(columnOption, index) in selectableColumns" :key="index">
                                <div v-if="columnOption && columnOption.selected" class="table-cell">{{columnOption.optionName}}</div>
                            </template>
                            <div class="table-cell-flex" v-for="(weekDay, index) in weekDays" :key="index">
                                {{ weekDay.day }}
                            </div>
                        </div>
                    </div>
                    <div class="table-contents"  ref="TableContentsRef" v-bind:style="tableMinWidthStyle">
                        <template v-for="(empNo, index) in scheduleWeekMap.keys()" :key="index">
                            <div class="schedules-arr-body" v-if="scheduleWeekMap.get(empNo) && scheduleWeekMap.get(empNo).show"
                                 v-bind:style="gridColumnsStyle">
                                <div class="schedules-arr-name sticky-left"
                                     v-if="scheduleWeekMap.get(empNo) && scheduleWeekMap.get(empNo).show">
                                    <div class="content-cell content-employee-name"
                                        :class="{'content-employee-name-hover': !isEmployee}"
                                         @click="openEmployeeDetail(empNo)">
                                        <span>{{scheduleWeekMap.get(empNo).employeeName }}</span>
                                        <div v-if="cellSettings.showPosition.value && !isEmployee">
                                            <span>{{scheduleWeekMap.get(empNo).positionTitle }}</span>
                                        </div>

                                        <span v-if="scheduleWeekMap.get(empNo).empSchePublishState === 'published' && !isEmployee"
                                              class="published-icon"
                                              v-tooltip.top="{content: 'Schedule for the week is published. ',}">
                                        <span>P</span>
                                    </span>
                                    </div>
                                </div>
                                <template v-for="(columnOption, index) in selectableColumns" :key="index">
                                    <div class="content-cell" v-if="columnOption && columnOption.selected && columnOption.optionName === 'Std Hours'">
                                        {{scheduleWeekMap.get(empNo).stdHoursPerWeek?.toFixed(2) }}
                                    </div>
                                    <div class="content-cell" v-if="columnOption && columnOption.selected && columnOption.optionName === 'Net Hours'">
                                        {{ (scheduleWeekMap.get(empNo).netHours % 1 !== 0) ? scheduleWeekMap.get(empNo).netHours.toFixed(2) : Math.floor(scheduleWeekMap.get(empNo).netHours) }}
                                    </div>
                                    <div class="content-cell" v-if="columnOption && columnOption.selected && columnOption.optionName === 'Ta Role'">
                                        {{scheduleWeekMap.get(empNo).taRole}}
                                    </div>
                                    <div class="content-cell" v-if="columnOption && columnOption.selected && columnOption.optionName === 'Worker Type'">
                                        {{scheduleWeekMap.get(empNo).workTypeName}}
                                    </div>
                                    <div class="content-cell" v-if="columnOption && columnOption.selected && columnOption.optionName === 'Roster Group'">
                                        {{scheduleWeekMap.get(empNo).defaultRosterGroup}}
                                    </div>
                                </template>

                                <div class="content-cell-flex"
                                    v-for="(scheDay, index) in scheduleWeekMap.get(empNo).sche" :key="index"
                                    v-bind:style="scheDay.column">
                                    <day-cell
                                        :scheDay="scheDay"
                                        :employeeNo="empNo"
                                        :employeeName="scheduleWeekMap.get(empNo).employeeName"
                                        :allowEdit="!isEmployee"
                                        :allowCreate="!isEmployee"
                                        :isEmployee="isEmployee"
                                        @openEditDialog="openEditDialog">
                                    </day-cell>
                                </div>
                            </div>
                        </template>
                    </div>
                </perfect-scrollbar>
            </div>
        </div>
        <div class="" v-if="weekDays && weekDays.length > 0">
            <printable-view :scheduleWeek="scheduleWeekMap" :weekDays="weekDays" :selectableColumns="selectableColumns" ref="PrintableViewRef"></printable-view>
        </div>
    </div>
</template>

<script lang="ts">
import Dialog from "@/components/dialog";
import DayCell from "@/components/FlexSchedule/DayCell.vue";
import {CFRemoteData} from "@/components/FlexSchedule/FlexScheduleInterface";
import SetupGridData from "@/components/FlexSchedule/setupGridData";
import {Leave} from "@/remote/flexSchedulesRequest";
import Remote from "@/remote/scheduleRequest";
import {
    EmployeeActions,
    EmployeeGetters,
    EmployeeMutations,
    ManagedEmployee,
} from "@/store/employeeModule";
import {
    FlexScheduleAction, FlexScheduleCellSetting,
    FlexScheduleMutation, SortOptions,
} from "@/store/flexScheduleModule";
import {LookupActions} from "@/store/lookupModule";
import {Icon} from "@iconify/vue";
import moment, {Moment} from "moment";
import {defineComponent, ref, inject } from "vue";
import {useRouter} from "vue-router";
import {ScheduleDay} from "./FlexScheduleInterface";
import _ from "lodash";
import {isMobileV2, isScreenTablet} from "@/components/common/script";
import FlexScheduleMultiColumn from "@/components/FlexSchedule/FlexScheduleMultiColumn.vue";
import {useStore} from "vuex";
import { CompanyConfigInterface } from "@/plugins/companyConfig";
import PrintableView from "@/components/FlexSchedule/Print/PrintableView.vue";

declare var Affinity: any;

export enum WeeklyPublishStatus {
    empty = "",
    draft = "draft",
    published = "published",
    partial = "partial",
}

export default defineComponent({
    name: "FlexScheduleCalendar",
    components: {
        DayCell,
        Icon,
        FlexScheduleMultiColumn,
        PrintableView
    },
    props: {
        dayFocus: {
            type: Object as () => Moment,
            default: moment(),
        },
        weekStart: {
            type: Object as () => Moment,
            default: null,
        },
        weekEnd: {
            type: Object as () => Moment,
            default: null,
        },
        isEmployee: {
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {
            // dayFocus: moment(),
            weekDays: <any[]>[], // calendar week days name
            // scheduleWeek: <any[]>[], // list of data per week
            
            datePickerTop: 0,
            datePickerLeft: 0,
            employeeList: <any>[],
            employeeHeightArr: <any>[],
            scheduleWeekMap: new Map(),
            weekStatus: WeeklyPublishStatus.empty,

            //
            showControl: false,
            extraEmpDetail: [],
            isAdmin: false,
            hasFuturePaidDate: false,

            //
            costCenterOptions: <any>[],
            selectableColumns: <SortOptions[]>[],
            gridColumnsStyle: {},
            tableMinWidthStyle: {},

            // inject
            manageCompanyConfig: <any>null,

        };
    },

    setup(props) {       
        const isEditingDialog = ref(false);
        const externalDetail = ref(<CFRemoteData[]>[]);
        const TableContentsRef = ref(<any>[]);
        const TableNameRef = ref(<any>{});

        const PrintableViewRef = ref<InstanceType<typeof PrintableView>>();
        const store = useStore()

        const cellSettings: FlexScheduleCellSetting = store.state.FlexSchedule.flexScheduleCellSetting
        let scheEmployeeNo = ref(Affinity.user.EmployeeNo);

        store.commit(EmployeeMutations.resetEmployees);
        if (props.isEmployee) {
            scheEmployeeNo.value = store.state.EmployeeModule.managerNo;
        }
        
        function printFlexSchedule() {
            PrintableViewRef.value!.printFlexSchedule()
        }
        return {
            TableContentsRef,
            TableNameRef,
            PrintableViewRef,
            isEditingDialog,
            externalDetail,
            cellSettings,
            printFlexSchedule,
            scheEmployeeNo,
        };
    },
    computed: {
        selectedColumn(): SortOptions[] {
            if (this.isEmployee) {
                return this.$store.state.FlexSchedule.employeeSelectableColumn;
            } else {
                return this.$store.state.FlexSchedule.selectableColumn;
            }
        },
    }, 
    watch: {
        weekStart(newVal: Moment, oldVal: Moment) {
            console.log('old val', oldVal)
            console.log('newval', newVal)
            if (newVal.format() !== oldVal.format() && oldVal !== null) {
                this.refreshScheduleData(newVal, this.weekEnd);
            }
        },  
        selectedColumn: {
            deep: true,
            handler(newVal: SortOptions[], oldVal: SortOptions[]) {
                this.multiColumnSelected()
            }
        }
    },

    async mounted() {
        console.log('mounted in calendar')
        this.manageCompanyConfig = inject('MANAGE_COMPANY_CONFIG') as CompanyConfigInterface;

        this.$store.dispatch(
            LookupActions.getPositions,
            this.scheEmployeeNo
        );
        this.$store.dispatch(LookupActions.getShifts);
        this.$store.dispatch(LookupActions.getCostElement);
        await this.$store.dispatch(LookupActions.getWorkTypes);

        this.loadDate(this.weekStart, this.weekEnd);

        Remote.GetDefaultWorkTypes().then((res) => {
            // note default worktypes is a array
            this.$store.state.FlexSchedule.defaultWorkTypes = res;
        });

        if (this.scheEmployeeNo >= 5000000) {
            this.isAdmin = true;
        }

        if (this.isEmployee) {
            this.selectableColumns = this.$store.state.FlexSchedule.employeeSelectableColumn;
        } else {
            this.selectableColumns = this.$store.state.FlexSchedule.selectableColumn;
        }
        this.multiColumnSelected();
    },

    methods: {
        
        multiColumnSelected() {
            let columnStyle = '120px ';
            for (const columnOption of this.selectableColumns) {
                if (columnOption.selected) {
                    columnStyle = columnStyle + ' ' + columnOption.width + 'px'
                }
            }
            columnStyle = columnStyle + ' repeat(7, 1fr)';
            this.gridColumnsStyle = {'grid-template-columns': columnStyle}
            this.calculateTableMinWidth()
            this.sortWeekScheduleData();
        },

        calculateTableMinWidth() {
            let minWidth = 120 ;

            for (const columnOption of this.selectableColumns) {
                if (columnOption.selected) {
                    minWidth = minWidth + columnOption.width
                }
            }
            minWidth = minWidth + (110 * 7);
            const minWidthStyle = minWidth + 'px';
            this.tableMinWidthStyle = {'min-width': minWidthStyle}
        },

        sortWeekScheduleData() {
            // Notes: multicolumn sort removed. Only one sort active at a time.
            const sortOption = this.$store.state.FlexSchedule.selectableColumn;
            const values = Array.from(this.scheduleWeekMap.values());
            let result
            const selectedOptions = sortOption.filter((s: any) => {
                return s.selected && !!s.sort;
            })
            if (selectedOptions && selectedOptions.length > 0) {
                const sOptionName = this.getOptionNameKey(selectedOptions[0].optionName);

                const arrWithValue = values.filter((v: any) => {
                    return v[sOptionName] !== null && v[sOptionName] !== undefined
                })
                // filter empty values
                const emptyValues = values.filter((v: any) => {
                    return v[sOptionName] === null || v[sOptionName] === undefined
                })

                // build for loop for multi sort
                let iteratee = []
                let orders: (boolean | "asc" | "desc")[] = []
                for (let option of sortOption) {
                    if (option.selected && option.sort !== '') {
                        iteratee.push(this.getOptionNameKey(option.optionName))
                        if (option.sort === 'Ascend') {
                            orders.push('asc')
                        } else if (option.sort === 'Descend') {
                            orders.push('desc')
                        }

                    }
                }

                if (iteratee.length === 0) {
                    // result = values.sort((a, b) => (a.employeeName > b.employeeName) ? 1 : -1)
                    result = _.orderBy(values,  o => o.employeeName.toLowerCase(), ['asc']);
                } else {
                    result = _.orderBy(arrWithValue, iteratee, orders);
                    if (emptyValues && emptyValues.length > 0) {
                        result = [...result, ...emptyValues]
                    }
                }

            } else {
                result = _.orderBy(values,  o => o.employeeName.toLowerCase(), ['asc']);
            }

            const scheduleWeekmap = new Map();
            result.map((v: any ) => {
                scheduleWeekmap.set(v.employeeNo, v)
            })
            this.scheduleWeekMap = this.$store.state.FlexSchedule.scheduleWeek = scheduleWeekmap;
            this.$nextTick(() => {
                this.resetEmployeeHeight();
            })
        },

        getOptionNameKey(optionName: string) {
            switch(optionName) {
                case 'Std Hours':
                    return 'stdHoursPerWeek'
                case 'Net Hours':
                    return 'netHours'
                case 'Worker Type':
                    return 'workTypeName'
                case 'Ta Role':
                    return 'taRole'
                case 'Roster Group':
                    return 'defaultRosterGroup'
                default: return ''
            }
        },

        showCopy() {
            if (this.hasFuturePaidDate) {
                return false;
            }
            return moment().diff(this.dayFocus.clone().endOf("week"), "day") <= 0;
        },
        async reloadAfterEdit() {
            await this.refreshScheduleData(this.weekStart, this.weekEnd);
            this.multiColumnSelected();
        },

        async loadDate(weekStart: Moment, weekEnd: Moment) {
            console.log('load date in calendar')
            this.weekStatus = WeeklyPublishStatus.empty;
            this.showControl = false;
            this.hasFuturePaidDate = false;

            let param = {
                managerEmployeeNo: this.scheEmployeeNo,
                dateFrom: weekStart.format("YYYY-MM-DD"), // dateFrom: '2020-01-01',
                dateTo: weekEnd.format("YYYY-MM-DD"), // dateTo: '2021-01-01',
            };

            Dialog.loading();
            let [dateData, leaves, employeeList] = await Promise.all([
                this.$store.dispatch(FlexScheduleAction.loadFlexiSchedules, {
                    startDate: weekStart,
                    endDate: weekEnd,
                    employeeNo: this.scheEmployeeNo
                }),
                Remote.GetManagedLeaves(param),
                this.$store.dispatch(
                    EmployeeActions.getEmployees,
                    this.scheEmployeeNo
                ),
            ]).finally(() => {
                Dialog.clear();
            });
            Dialog.clear();
            this.setupGridData(dateData, leaves, employeeList, weekStart);
            this.$store.state.FlexSchedule.leaveList = leaves;

            this.setupDate(weekStart);
            console.log('week start', weekStart)
            this.checkStatus(this.scheduleWeekMap);
            this.setupCostCenterList(employeeList);
            this.sortWeekScheduleData();

            console.log('this.scheduleWeekMap', this.scheduleWeekMap)

            this.$nextTick(() => {
                this.resetEmployeeHeight();
            })
        },

        setupCostCenterList(employeeList: ManagedEmployee[]) {
            const costCenterIdsDuplicate = employeeList.map((e) => e.cost1);
            const costCenterIds = [...new Set(costCenterIdsDuplicate)];

            this.$store.dispatch(LookupActions.getCostCenters, costCenterIds)
                
        },

        resetEmployeeHeight() {
            let index = 0;
            for (const child of this.TableContentsRef.children) {
                this.employeeHeightArr[index] = Math.round(child.getBoundingClientRect().height * 100) / 100;
                index++ ;
            }
        },

        async refreshScheduleData(weekStart: Moment, weekEnd: Moment) {
            console.log('refresh get called')
            this.weekStatus = WeeklyPublishStatus.empty;
            this.showControl = false;
            this.hasFuturePaidDate = false;

            Dialog.loading();

            let getLeaveParam = {
                managerEmployeeNo: this.scheEmployeeNo,
                dateFrom: weekStart.format("YYYY-MM-DD"), // dateFrom: '2020-01-01',
                dateTo: weekEnd.format("YYYY-MM-DD"), // dateTo: '2021-01-01',
            };

            const [dateData, managedLeaves] = await Promise.all([
             this.$store.dispatch(
                FlexScheduleAction.loadFlexiSchedules,
                {
                    startDate: weekStart,
                    endDate: weekEnd,
                    employeeNo: this.scheEmployeeNo
                }
             ),

              Remote.GetManagedLeaves(getLeaveParam)
            ])
            this.$store.state.FlexSchedule.leaveList = managedLeaves;
            Dialog.clear();
            this.setupGridData(
                dateData,
                managedLeaves,
                this.$store.getters[EmployeeGetters.getEmployees],
                weekStart
            );
            this.checkStatus(this.scheduleWeekMap);
            this.setupDate(weekStart);

            for (const [key, employeeData] of this.scheduleWeekMap.entries()) {
                employeeData.show = false;
            }
            for (const employee of this.$store.state.EmployeeModule
                .filteredEmployees) {
                const employeeWeek = this.scheduleWeekMap.get(
                    employee.employeeNo
                );
                employeeWeek.show = true;
            }

            this.$nextTick(() => {
                console.log(" refreshScheduleData For loop is all done rendering everything!");
                this.resetEmployeeHeight();
            })
        },

        setupGridData(
            dateData: any,
            leaves: Leave[],
            employeeList: ManagedEmployee[],
            weekStart: Moment
        ) {
            let gridDataSet: Map<string, any> = SetupGridData.prepareGridData(
                weekStart,
                employeeList,
                dateData,
            );

            if (this.manageCompanyConfig.getAllowSundayFirst()) {
                SetupGridData.scheDaysToColumn(weekStart, gridDataSet, true);
            } else {
                SetupGridData.scheDaysToColumn(weekStart, gridDataSet, false);
            }
            
            SetupGridData.addLeaveToGrid(weekStart, leaves, gridDataSet);
            SetupGridData.addUnAvaToGrid(dateData, gridDataSet);
            this.scheduleWeekMap = this.$store.state.FlexSchedule.scheduleWeek = gridDataSet;
            console.log('week map', this.scheduleWeekMap)
        },

        setupDate(weekStart: Moment) {
            this.$store.state.FlexSchedule.calendarFocusDate = this.dayFocus;
            this.weekDays = this.getSelectedWeek(weekStart); // todo: check this move to top
        },

        checkStatus(scheduleWeekMap: any) {
            let statusSet = new Set();

            for (const employeeSchedule of scheduleWeekMap.values()) {
                employeeSchedule.empSchePublishState = "";
                for (const sche of employeeSchedule.sche) {
                    if (!sche.publishStatus) {
                        // console.log('sche', sche)
                        // if (!statusSet.has(WeeklyPublishStatus.empty)) statusSet.add(WeeklyPublishStatus.empty)
                    } else if (sche.publishStatus === WeeklyPublishStatus.draft) {
                        // if (!statusSet.has(WeeklyPublishStatus.draft)) statusSet.add(WeeklyPublishStatus.draft)
                        employeeSchedule.empSchePublishState = WeeklyPublishStatus.draft;
                    } else if (sche.publishStatus === WeeklyPublishStatus.published) {
                        // if (!statusSet.has(WeeklyPublishStatus.published)) statusSet.add(WeeklyPublishStatus.published)
                        employeeSchedule.empSchePublishState = WeeklyPublishStatus.published;
                    }

                    // check schedule has Future Paid Date in current week
                    if (
                        !this.hasFuturePaidDate &&
                        sche.isPaid &&
                        moment(0, "HH").diff(moment(sche.date), "days") <= -1
                    ) {
                        this.hasFuturePaidDate = true;
                    }
                }

                if (employeeSchedule.empSchePublishState === "") {
                    statusSet.add(WeeklyPublishStatus.empty);
                } else {
                    statusSet.add(employeeSchedule.empSchePublishState);
                }

                // check status if employee has a Team or Personal Schedule
                let includeTeamOrPersonalSche = employeeSchedule.sche.find(
                    (sche: any) => {
                        return (
                            sche.scheduleType === "Personal" ||
                            sche.scheduleType === "Team"
                        );
                    }
                );

                if (includeTeamOrPersonalSche) {
                    employeeSchedule.includeTeamOrPersonal = true;
                }
            }

            // if no published show draft
            if (!statusSet.has(WeeklyPublishStatus.published)) {
                this.weekStatus = WeeklyPublishStatus.draft;
                this.$store.commit(FlexScheduleMutation.setWeekStatus, WeeklyPublishStatus.draft);
            } else {
                // if has publish
                // and has draft or empty show partial
                if (
                    statusSet.has(WeeklyPublishStatus.draft) &&
                    statusSet.has(WeeklyPublishStatus.published)
                ) {
                    this.weekStatus = WeeklyPublishStatus.partial;
                    this.$store.commit(FlexScheduleMutation.setWeekStatus, WeeklyPublishStatus.partial);
                } else if (
                    statusSet.has(WeeklyPublishStatus.empty) &&
                    statusSet.has(WeeklyPublishStatus.published)
                ) {
                    this.weekStatus = WeeklyPublishStatus.partial;
                    this.$store.commit(FlexScheduleMutation.setWeekStatus, WeeklyPublishStatus.partial);
                } else {
                    // if has publish but no draft or empty show published
                    this.weekStatus = WeeklyPublishStatus.published;
                    this.$store.commit(FlexScheduleMutation.setWeekStatus, WeeklyPublishStatus.published);
                }
            }
        },

        getSelectedWeek(selectedDate: any) : { momentDay: Moment; day: string }[] {
            let weekDays = [];

            for (let i = 0; i <= 6; i++) {
                let momentDay = moment(this.weekStart).add(i, "days");
                weekDays.push({
                    momentDay: momentDay,
                    day: momentDay.format("ddd, MMM Do"),
                });
            }

            return weekDays;
        },

        openEmployeeDetail(empNo: number) {
            if (this.isEmployee) return;
            this.$emit('openEmployeeDetail', empNo);
        },

        openEditDialog(scheDay: ScheduleDay) {
            this.$emit('openEditDialog', scheDay);
        },
    },
});
</script>

<style scoped lang="scss">
@use "../../style/layouts";

$aff-blue: #00aeef;
$aff-dark-blue: #0c96e4;
$border-color: #c8d1db;

.table-head-scroll {
    width: auto;
    overflow: hidden;
    margin-left: 121px;

    @media (max-width: 1200px) {
        margin-left: 100px;
    }
}

.schedule-head-grid {
    display: grid;
    width: 100%;
    grid-gap: 1px;
    height: 23px;

    @media (min-width: 1201px) {
        grid-template-columns: 90px 90px 70px 100px repeat(7, 1fr);
    }

    @media (max-width: 1200px) {
        //grid-template-columns: 100px 0 90px repeat(7, 1fr);
        grid-template-columns: 90px 90px 90px repeat(7, minmax(100px, 1fr));
    }
}

.table-head {
    position: sticky;
    top: 0;
    z-index: 2;
    min-width: 900px;
}

.table-cell {
    padding: 4px 4px;
    text-align: center;
    font-weight: bold;
    font-size: 12px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    background-color: #fff;
}

.table-cell-flex {
    @extend .table-cell;
    min-width: 80px;
}

.table-names {
    display: grid;
    grid-gap: 1px;
    width: 120px;
    background: $border-color;
    max-height: calc(100vh - 390px);
    overflow: hidden;
    @media (max-width: 1200px) {
        width: 100px;
    }
}

.table-names-head-cell {
    padding: 4px 8px;
    height: 22px;
    background-color: #fff;
}

.table-contents {
    display: grid;
    grid-gap: 1px;
    min-width: 900px;
    background: #c8d1db;

    .schedules-arr-body:first-child {
        margin-top: 1px;
    }
}

.schedules-arr-body {
    display: inline-grid;
    width: 100%;
    grid-gap: 1px;
    background: white;

    &:last-child {
        border-bottom: 0;
    }

    @media (min-width: 1201px) {
        grid-template-columns: 90px 90px 70px 100px repeat(7, 1fr);
    }
    @media (max-width: 1200px) {
        grid-template-columns: 90px 90px 90px 90px repeat(7, minmax(100px, 1fr));
    }
}

.flex-sche-table {
    border-bottom: 1px solid $border-color;
    border-collapse: collapse;
    //min-width: 1024px;
}

.content-cell {
    display: grid;
    justify-items: center;
    align-items: center;
    // align-content: center;
    text-align: center;
    // min-height: 70px;
    background-color: #fff;
}

.content-cell-flex {
    @extend .content-cell;
    min-width: 80px;
}


.ps {
    max-height: calc(100vh - 390px);
    overflow: auto;
}

.table-contents {
    .ps {
        max-height: initial;
        overflow-x: auto;
        overflow-y: hidden;
        min-width: 1021px;
    }
}

.table-head-corner {
    position: sticky;
    left: 0;
    top: 0;
    z-index: 10;
}

.sticky-left {
    position: sticky;
    left: 0;
    z-index: 1;
}

:deep(.ps__rail-x) {
    z-index: 2;
}

.status-section {
    position: relative;
}

.show-control-options {
    position: absolute;
    display: flex;
    flex-direction: column;
    padding: 5px;
    margin-top: 5px;
    background: white;
    z-index: 1;
}

.action-button-width {
    width: 120px;
}

.inline-block-middle {
    display: inline-block;
    vertical-align: middle;
}

.action-buttons {
    display: flex;
    flex-direction: column;
    padding: 5px 10px;

    button {
        color: #000;
        border-bottom: 1px solid #c8d1db;
        border-radius: 0 !important;

        &:last-child {
            border-bottom-color: transparent;
        }
    }
}

.content-employee-name {
    position: relative;
    height: 100%;
    min-height: 50px;
    background: white;
    word-break: break-word;
    align-content: center;

    @media screen and (max-width: 1200px) {
        font-size: 12px;
    }
}

.content-employee-name-hover {
    cursor: pointer;

    &:hover {
        background: $aff-blue;
        color: white;
    }
}

.published-icon {
    position: absolute;
    top: 5px;
    right: -15px;
    border-radius: 50%;
    background: #8dca35;
