<template>
    <Modal ref="ModalRef">
        <template v-slot:header>
            <div>
                <h2 class="bold">
                    <span v-if="isEdit">Edit Unavailability</span>
                    <span v-else>Create Unavailability</span>
                </h2>
            </div>
        </template>
        <template v-slot:body>
            <div>
                <div class="edit-form mt-4 copy2">
                    <div class="form-control mt-4">
                        <label class="form-label">Start Time </label>
                        <input
                            type="text"
                            v-model="startTime"
                            @change="updateStartTime"
                        />
                    </div>
                    <div class="form-control">
                        <label class="form-label">End Time </label>
                        <input
                            type="text"
                            v-model="endTime"
                            @change="updateEndTime"
                        />
                    </div>
                    <div class="form-control">
                        <label class="form-label">Day of Week</label>
                        <div class="select-day-of-week">
                            <custom-select
                                :options="dayOfWeekOption"
                                v-model.modelValue="selectedDayOfWeek"
                            ></custom-select>
                        </div>
                    </div>
                    <div class="form-control">
                        <label class="form-label">Repeat</label>
                        <div class="select-repeat">
                            <custom-select
                                :options="repeatOption"
                                v-model.modelValue="selectedRepeat"
                                @update:modelValue="updateRepeat"
                            ></custom-select>
                        </div>
                    </div>
                    <div class="form-control">
                        <label class="form-label">Start On</label>
                        <input
                            class="input-box"
                            type="text"
                            v-model="startDateText"
                            @change="updateDateTimeText('startDate')"
                            @click="updateDateTime($event, 'startDate')"
                        />
                    </div>
                    <div class="form-control endDateWidth">
                        <div v-if="!endDateDisable">
                            <label class="form-label">End On</label>
                            <input
                                class="input-box"
                                type="text"
                                v-model="endDateText"
                                @change="updateDateTimeText('endDate')"
                                @click="updateDateTime($event, 'endDate')"
                            />
                            <span class="ml-2">
                                <input
                                    type="checkbox"
                                    v-model="noEndDate"
                                    @change="clickedNoEndDate"
                                />
                                <label class="checkbox-label"
                                    >No End Date</label
                                >
                            </span>
                        </div>
                    </div>
                    <div class="form-control">
                        <label class="form-label mb-1">Reason</label>
                        <textarea v-model="reason"></textarea>
                    </div>
                </div>
                <date-picker
                    ref="DatePickerRef"
                    :top="datePickerTop"
                    :left="datePickerLeft"
                ></date-picker>
            </div>
        </template>
        <template v-slot:footer>
            <div class="mt-4 full-width d-flex justify-end">
                <button
                    v-if="isEdit"
                    class="blue normal copy2"
                    @click="updateAvailability()"
                >
                    <span class="white">Save</span>
                </button>
                <button
                    v-if="isEdit"
                    class="grey normal copy2 ml-2"
                    @click="confirmDelete()"
                >
                    <span class="white">Delete</span>
                </button>
                <button
                    v-if="!isEdit"
                    class="blue normal copy2"
                    @click="createAvailability()"
                >
                    <span class="white">Create</span>
                </button>

                <button class="ml-2 normal orange copy2" @click="cancel()">
                    <span class="white">Cancel</span>
                </button>
            </div>
        </template>
    </Modal>
</template>

<script lang="ts">
import { defineComponent, ref } from "vue";
import Modal from "@/components/common/Modal.vue";
import CustomSelect from "@/components/common/CustomSelect.vue";
import AvailabilityLookup from "@/components/Availability/AvailabilityLookUp";
import moment, { Moment } from "moment";
import DatePicker from "@/components/common/DatePicker.vue";
import Common from "@/components/common/script";
import Remote from "@/remote/scheduleRequest";
import Dialog from "@/components/dialog";

declare var Affinity: any;

export default defineComponent({
    name: "EditAvailability",
    components: {
        Modal,
        CustomSelect,
        DatePicker,
    },
    props: {
        isEdit: {
            type: Boolean,
        },
        weekStatus: {
            type: String,
        },
    },
    data() {
        return {
            ava: <any>null,
            employeeNo: 0,
            startTime: "",
            endTime: "",
            dayOfWeekOption: AvailabilityLookup.fullWeekDays,
            selectedDayOfWeek: "Monday",

            repeatOption: AvailabilityLookup.frequencyCycle,
            selectedRepeat: "Weekly",

            datePickerTop: 0,
            datePickerLeft: 0,

            startDate: <Moment | null>moment(),
            startDateText: "",
            endDate: <Moment | null>moment(),
            endDateText: "",
            reason: "",
            //
            noEndDate: true,
            endDateDisable: false,
        };
    },
    setup() {
        return {};
    },
    watch: {
        selectedDayOfWeek(value1, value2) {
            //console.log(value1, value2)
        },
    },
    methods: {
        reset() {
            this.ava = <any>null;
            this.employeeNo = 0;
            this.startTime = "";
            this.endTime = "";
            this.startDate = moment();
            this.startDateText = "";
            this.endDate = moment();
            this.endDateText = "";
            this.reason = "";
            this.selectedRepeat = "Weekly";
            this.noEndDate = true;
            this.endDateDisable = false;
        },

        updateStartTime() {
            this.startTime = Common.parseTimeColumn(this.startTime);
        },
        updateEndTime() {
            this.endTime = Common.parseTimeColumn(this.endTime);
        },
        updateRepeat() {
            if (this.selectedRepeat === "Once") {
                this.endDateDisable = true;
            } else {
                this.endDateDisable = false;
            }
        },
        clickedNoEndDate() {
            if (this.noEndDate) {
                this.endDateText = "";
                this.endDate = null;
            }
        },

        createAvailability() {
            Dialog.loading();

            let endDate = this.endDateText
                ? this.endDate!.format("DD/MMM/YYYY")
                : null;
            endDate = this.noEndDate || this.endDateDisable ? null : endDate;

            let data = [
                {
                    employeeNo: Affinity.user.EmployeeNo,
                    startTime: this.startTime,
                    endTime: this.endTime,
                    dayOfWeek: AvailabilityLookup.findShortDayByFull(
                        this.selectedDayOfWeek
                    ),
                    dayOfMonth: null,
                    frequencyType: this.selectedRepeat,
                    startDate: this.startDate!.format("DD/MMM/YYYY"),
                    endDate: endDate,
                    reason: this.reason,
                    hasNoEndDate: this.noEndDate,
                },
            ];

            Remote.CreateAvailability(data).then((res) => {
                Dialog.clear();
                this.$emit("reload");
                (this.$refs.ModalRef as any).toggleShow();
            });
        },
        updateDateTimeText(type: any) {
            if (type === "endDate") {
                if (this.endDateText === "") {
                    this.endDate = null;
                } else {
                    this.noEndDate = false;
                    const endDateM = moment(this.endDateText, "DD/MM/YYYY");
                    if (endDateM.isValid()) {
                        this.endDate = endDateM;
                    }
                }
            } else if (type === "startDate") {
                if (this.startDateText === "") {
                    this.startDate = null;
                } else {
                    this.noEndDate = false;
                    const startDateM = moment(this.startDateText, "DD/MM/YYYY");
                    if (startDateM.isValid()) {
                        this.startDate = startDateM;
                    }
                }
            }
        },
        confirmDelete() {
            Dialog.confirmOrCancel({
                message:
                    "Are you sure you want to delete this period of unavailability?",
            }).then(() => {
                Remote.DeleteAvailabilities(this.ava!.id).then(
                    (res) => {
                        this.$emit("reload");
                        (this.$refs.ModalRef as any).toggleShow();
                    },
                    (err) => {
                        Dialog.alert(
                            "Something went wrong. Delete Availability failed"
                        );
                    }
                );
            });
        },
        updateAvailability() {
            let endDate = this.endDateText
                ? this.endDate!.format("DD/MMM/YYYY")
                : null;
            endDate = this.noEndDate || this.endDateDisable ? null : endDate;

            Dialog.loading();
            let data = [
                {
                    Id: this.ava!.id,
                    StartTime: this.startTime,
                    EndTime: this.endTime,
                    DayOfWeek: AvailabilityLookup.findShortDayByFull(
                        this.selectedDayOfWeek
                    ),
                    DayOfMonth: null,
                    FrequencyType: this.selectedRepeat,
                    StartDate: this.startDate!.format("DD/MMM/YYYY"),
                    EndDate: endDate,
                    Reason: this.reason,
                    HasNoEndDate: this.noEndDate,
                },
            ];
            Remote.PatchAvailabilities(data).then((res) => {
                Dialog.clear();
                this.$emit("reload");
                (this.$refs.ModalRef as any).toggleShow();
            });
        },

        async updateDateTime($event: any, selectedDateTarget: string) {
            let element = $event.target.getClientRects()[0];
            this.datePickerTop = element.bottom;
            this.datePickerLeft = element.left;

            try {
                switch (selectedDateTarget) {
                    case "startDate":
                        this.startDate = await (
                            this.$refs.DatePickerRef as any
                        ).toggleShow(this.startDate);
                        this.startDateText =
                            this.startDate!.format("DD/MM/YYYY");
                        return;
                    case "endDate":
                        if (!this.endDate || !this.endDate.isValid()) {
                            this.endDate = moment();
                        }
                        this.endDate = await (
                            this.$refs.DatePickerRef as any
                        ).toggleShow(this.endDate);
                        this.endDateText = this.endDate!.format("DD/MM/YYYY");
                        this.noEndDate = false;
                        return;
                }
            } catch (e) {
                return;
            }
        },

        openEditDialog(avaInput: any) {
            if (avaInput) {
                this.ava = avaInput;
                this.employeeNo = Affinity.user.EmployeeNo;
                this.startTime = avaInput.startTime;
                this.endTime = avaInput.endTime;
                this.selectedDayOfWeek = avaInput.dayOfWeek;
                this.selectedRepeat = avaInput.frequencyType;
                this.startDateText = avaInput.startDate;
                this.startDate = moment(this.startDateText, "DD/MM/YYYY");
                this.endDateText = avaInput.endDate ? avaInput.endDate : "";
                this.endDate = moment(this.endDateText, "DD/MM/YYYY");
                (this.reason = avaInput.reason),
                    (this.noEndDate = avaInput.hasNoEndDate);
            } else {
                this.reset();
            }
            (this.$refs.ModalRef as any).toggleShow();
        },

        cancel() {
            this.toggleShow();
        },
        toggleShow() {
            (this.$refs.ModalRef as any).toggleShow();
        },
    },
});
</script>

<style scoped lang="scss">
$border-color: rgba(0, 0, 0, 0.42);
.edit-form {
    //  display: flex;
    input[type="text"] {
        width: 120px !important;
    }

    input[type="checkbox"] {
        display: inline-block;
        vertical-align: middle;
    }

    .checkbox-label {
        vertical-align: middle;
        line-height: 14px;
        white-space: nowrap;
    }

    textarea {
        padding: 4px 10px;
        border: 1px solid $border-color;
        border-radius: 4px;
        vertical-align: top;
        font-size: 12px;
        max-width: 100%;
    }
}

.modal-body {
    overflow-y: auto;
}

.form-control {
    @media screen and (min-width: 561px) {
        display: flex;
        margin-bottom: 12px;
    }
    @media screen and (max-width: 560px) {
        margin-bottom: 20px;
    }
}

.form-label {
    font-weight: bold;

    @media screen and (min-width: 561px) {
        display: inline-block;
        width: 132px;
        padding-right: 8px;
        flex-grow: 0;
        flex-shrink: 0;
    }
    @media screen and (max-width: 560px) {
        padding-bottom: 8px;
        display: block;
    }
}

.select-day-of-week {
    display: inline-block;
    min-width: 120px;
    vertical-align: middle;
}

.custom-select-selected:after {
    top: calc(50% - 2px);
}

.select-repeat {
    display: inline-block;
    min-width: 120px;
    vertical-align: middle;
}

.disable {
    opacity: 0.5;
    pointer-events: none;
}

@media screen and (min-width: 561px) {
    .endDateWidth {
        width: 389px;
    }
}
:deep(.custom-select) {
    .custom-select-items {
        width: 120px;
    }
}
</style>
