<template>
    <div>
        <input ref="InputRef" 
            :value="$filters.reformatDate(pickerDate)"
            class="d-inline-flex ma-0 pa-0 mr-2 caption"
            style="max-width:120px"
            @change="updateDateText($event)"
            @click="updateDateTime($event)"
            @click:clear="pickerDate = null">
        <date-picker ref="DatePickerRef" :top="datePickerTop" :left="datePickerLeft" :minDate="minDate"></date-picker>

    </div>
    

</template>

<script lang="ts">
import { defineComponent, ref } from 'vue';
import DatePicker from "@/components/common/DatePicker.vue";
import moment, { Moment } from 'moment';

export default defineComponent({
    name: 'DatePickerInput',
    components: {
        DatePicker,
    },
    props: {
        initSourceData: {
            type: String,
            default: null,
        },
        minDate: {
            type: Object, // moment
            default: null,
        },
    },
    setup(props, {emit }) {
        const pickerDate = ref<Moment>(moment());
        const dateModal = ref<boolean>(false);
        // const sourceData = ref<Moment>(moment());
        const datePickerTop = ref(0);
        const datePickerLeft = ref(0);
        // const DatePickerRef = ref<InstanceType < typeof DatePicker >>();
        const DatePickerRef = ref<InstanceType<typeof DatePicker>>();

        const InputRef = ref<InstanceType < typeof HTMLInputElement>>();

        if (props.initSourceData) {
            pickerDate.value = moment(props.initSourceData)
        }
        const updateDateText = (event: Event) => {
            const target = event.target as HTMLInputElement;
            pickerDate.value = moment(target.value, 'DD/MM/YYYY');
            dateModal.value = false;
            console.log('updated date text')
            emit('updateDate', pickerDate.value);
        };

        const updateDateTime = ($event: Event) => {
            console.log('updateDateTime', $event)
            const target = $event.target as HTMLInputElement;
            let element = target.getClientRects()[0];
            datePickerTop.value = element.bottom;
            datePickerLeft.value = element.left;
          
            dateModal.value = true;
            // openDatePicker() function needs to be defined
            DatePickerRef.value!.toggleShow(pickerDate.value).then((val: any) => {
                console.log('picker val', val)
                pickerDate.value = val;
                emit('updateDate', pickerDate.value);
            }).catch(err => {
                console.log('picker err', err)
            });
        };

        return {
            pickerDate,
            updateDateText,
            updateDateTime,
            datePickerTop,
            datePickerLeft,
            DatePickerRef,
            InputRef,
        };
    }
});
</script>

<style scoped>
.d-inline-flex {
    display: inline-flex;
}

.ma-0 {
    margin: 0;
}

.pa-0 {
    padding: 0;
}

.mr-2 {
    margin-right: 2rem;
}

.caption {
    font-size: 0.75rem;
    font-weight: 400;
    letter-spacing: 0.03333em;
}
</style>