import moment, {Moment} from "moment";
import {isMobileV2, isScreenTablet} from "@/components/common/script";

function copyScheToCell(sche: any) {
    return {
        id: sche.id,
        startTime: sche.startTime,
        endTime: sche.endTime,
        hours: sche.hours,
        date: sche.date,
        isPaid: sche.isPaid,
        scheduleType: sche.scheduleType,
        dayInWeek: sche.dayInWeek,
        publishStatus: sche.publishStatus,
        // scheduleDetail: sche.scheduleDetail,
        breakLength: sche.scheduleDetail.breakLength,
        position: sche.scheduleDetail.position,
        positionTitle: sche.scheduleDetail.positionTitle,
        shiftId: sche.scheduleDetail.shiftId,
        notes: sche.notes,
    }
}

function prepareGridData(weekStart: Moment, employeeList: any[], dateData: any) {
    // for (let employee of employeeList) {
    //     this.scheduleWeekMap.set(employee.EmployeeNo, employee)
    // }

    let flexSchedulesDateData: any = dateData.flexSchedules;

    const gridDataSet = new Map();
    for (let employee of employeeList) {
        // const employeeDataInRow = {
        //     data1: '1', // employee name and number
        //     data2: '2', // std hours
        //     data3: '3', // net hours
        //     data4: '4', //
        //     data5: '5',
        // }
        const employeeDataInRow = {... employee};
        employeeDataInRow.show = true;
        gridDataSet.set(employee.employeeNo, employeeDataInRow)
    }

    for (let sche of flexSchedulesDateData) {
        const employeeData = gridDataSet.get(sche.EmployeeNo);
        if (employeeData && !employeeData.sche) {
            // employeeData.sche = [sche]
            employeeData.sche = [copyScheToCell(sche)]
        } else if (employeeData) {
            const cellData = copyScheToCell(sche)
            employeeData.sche.push(cellData)
        }
    }

    return gridDataSet;
}

function prepareGridDataMobile(weekStart: Moment, employeeList: any[], dateData: any) {
    let flexSchedulesDateData: any = dateData.flexSchedules;

    const gridDataSet = new Map();
    for (let employee of employeeList) {
        const employeeDataInRow = {... employee};
        employeeDataInRow.show = true;
        gridDataSet.set(employee.employeeNo, employeeDataInRow)
    }

    for (let sche of flexSchedulesDateData) {
        const employeeData = gridDataSet.get(sche.EmployeeNo);
        if (employeeData) {
            employeeData.sche = copyScheToCell(sche)
        }
    }

    for (let employeeData of gridDataSet.values()) {
        if (!employeeData.sche) {
            employeeData.sche = {}
            employeeData.sche.dayInWeek = 0;
            employeeData.sche.employeeName = employeeData.employeeName;
            employeeData.sche.employeeNo = employeeData.employeeNo;
        }

        employeeData.sche.allowCreateOrEdit = true;
        employeeData.sche.editing = false;
    }

    return gridDataSet;

}


function addLeaveToGrid(weekStart: Moment, leaves: any, gridDataSet: any) {
    for (let leave of leaves) {
        if (!leave.leaveUnits) continue;
        for (let leaveUnit of leave.leaveUnits) {
            let dateDiff = moment(leaveUnit.leaveDate).diff(weekStart, 'days')
            if (dateDiff < 0) {
                continue;
            }
            leaveUnit.calcUnit = leave.calcUnit;
            leaveUnit.leaveCode = leave.leaveCode;
            leaveUnit.leaveName = leave.leaveName;
            if (gridDataSet.get(leave.employeeNo)?.sche[dateDiff]) {
                gridDataSet.get(leave.employeeNo).sche[dateDiff].leaveUnit = leaveUnit
            }
        }
    }
}

function addLeaveToGridMobile(leaves: any, gridDataSet: any) {
    for (let leave of leaves) {
        if (!leave.leaveUnits) continue;
        for (let leaveUnit of leave.leaveUnits) {
            leaveUnit.calcUnit = leave.calcUnit;
            leaveUnit.leaveCode = leave.leaveCode;
            leaveUnit.leaveName = leave.leaveName;
            if (gridDataSet.get(leave.employeeNo)?.sche) {
                gridDataSet.get(leave.employeeNo).sche.leaveUnit = leaveUnit
            }
        }
    }
}


function addUnAvaToGrid(dateData: any, gridDataSet: any) {
    for (let unAva of dateData.unAvailabilities) {
        let weeklyDay = moment(unAva.unavailabilityDate).day()
        if (gridDataSet.get(unAva.employeeNo) && gridDataSet.get(unAva.employeeNo).sche && gridDataSet.get(unAva.employeeNo).sche[weeklyDay]) {
            gridDataSet.get(unAva.employeeNo).sche[weeklyDay].unAvailability = unAva
        }
    }
}
function addUnAvaToGridMobile(dateData: any, gridDataSet: any) {
    for (let unAva of dateData.unAvailabilities) {
        // let weeklyDay = moment(unAva.unavailabilityDate).day()
        if (gridDataSet.get(unAva.employeeNo) && gridDataSet.get(unAva.employeeNo).sche) {
            gridDataSet.get(unAva.employeeNo).sche.unAvailability = unAva
        }
    }
}

// calculate number of week day into schedule data.
function scheDaysToColumn(weekStart: Moment, gridDataSet: any, sundayFirst: boolean) {
    console.log('gridDataset', gridDataSet)
    const today = moment();
    for (let scheduleArr of gridDataSet.values()) {
        let employeeName = scheduleArr.employeeName || scheduleArr.EmployeeName
        let employeeNo = scheduleArr.employeeNo || scheduleArr.EmployeeNo
        let netHours = 0
        for (let i = 0; i <= 6; i++) {
            let findResult
            if (scheduleArr.sche) {
                findResult = scheduleArr.sche.find((day: any) => {
                    let dayInWeekHolder
                    if (sundayFirst) {
                        dayInWeekHolder = i;
                    } else {
                        dayInWeekHolder = i + 1;
                        if (dayInWeekHolder > 6) {
                            dayInWeekHolder = 0;
                        }
                    }
                    return day.dayInWeek === dayInWeekHolder;
                })
            } else {
                scheduleArr.sche = []
            }

            if (scheduleArr.sche[i]?.hours) {
                netHours = netHours + scheduleArr.sche[i].hours
            }

            if (!findResult) {
                // if first day in array and that employee do not have flex schedule ([0].dayInWeek == undefined)
                if (i === 0 && (!scheduleArr.sche[0]
                    || (scheduleArr.sche[0].dayInWeek === null
                        || scheduleArr.sche[0].dayInWeek === undefined ))) {
                    scheduleArr.sche[0] = {}
                    if (sundayFirst) {
                        scheduleArr.sche[0].dayInWeek = 0;
                    } else {
                        scheduleArr.sche[0].dayInWeek = 1;
                    }
                    
                    scheduleArr.sche[0].employeeName = employeeName;
                    scheduleArr.sche[0].employeeNo = employeeNo;
                } else {
                    let dayInWeekHolder
                    if (sundayFirst) {
                        dayInWeekHolder = i;
                    } else {
                        dayInWeekHolder = i + 1;
                        if (dayInWeekHolder > 6) {
                            dayInWeekHolder = 0;
                        }
                    }
                    scheduleArr.sche.push({
                        dayInWeek: dayInWeekHolder,
                        employeeName: employeeName,
                        employeeNo: employeeNo,
                    })
                }
            }
        }

        scheduleArr.sche.sort((a: any, b: any) => a.dayInWeek - b.dayInWeek)
        //
        if (!sundayFirst) {
            const sunday = scheduleArr.sche.shift()
            scheduleArr.sche.push(sunday)
        }
        
        const columnFactor = isScreenTablet() ? 4 : 5;
        for (let [index, day] of scheduleArr.sche.entries()) {
            // day.column = `grid-column-start: ${day.dayInWeek + 3}`;
            // day.column = `grid-column-start: ${day.dayInWeek + columnFactor}`;
            // day.allowCreateOrEdit = weekStart.clone().add(index, 'day').diff(today, 'day') >=0;
            day.allowCreateOrEdit = true;
            day.editing = false;
        }
        scheduleArr.netHours = netHours
    }

}

export default {
    prepareGridData,
    prepareGridDataMobile,
    scheDaysToColumn,
    addLeaveToGrid,
    addUnAvaToGrid,
    addUnAvaToGridMobile,
    addLeaveToGridMobile
}