import moment from "moment";

// parse time string and insert column automatic
export function parseTimeColumn(input: string) {
    if (!input) {
        return '';
    }

    let parseFormat = 'Hmm';
    if (input.includes(":")) {
        parseFormat = 'H:mm'
    }

    let time = moment(input, parseFormat)
    if (time.isValid()) {
        return time.format("HH:mm")
    } else {
        return 'Invalid Time'
    }
}

export function isScreenTablet() {
    return window.innerWidth < 1200;
}

export function isMobileV2() {
    return window.innerWidth < 768;
}

export function isMobile() {
    return window.innerWidth < 1000;
}

export function nameOfDays(startFromSunday: boolean = false) {
    if (startFromSunday) {
        return ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"]
    } else {
        return ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"]
    }

}

export function keysToLowerCase(obj: Record<string, any>): Record<string, any> {
    return Object.fromEntries(
        Object.entries(obj).map(([key, value]) => [
            key.charAt(0).toLowerCase() + key.slice(1),
            value,
        ])
    );
}

export default {
    parseTimeColumn,
};