<template>
    <TopFilters :isEmployee="true" @changeWeek="changeWeek">
    </TopFilters>
    <div v-if="managerNo && weekStart">
        <FlexScheduleCalendar 
            ref="FlexScheduleCalendarRef"
            :dayFocus=dayFocus
            :weekStart="weekStart"
            :weekEnd="weekEnd"
            :isEmployee="true">
        </FlexScheduleCalendar>
    </div>

</template>

<script lang="ts">
import FlexScheduleCalendar from "@/components/FlexSchedule/FlexScheduleCalendar.vue";
import TopFilters from "@/components/FlexSchedule/TopFilters.vue";
import moment, {Moment} from "moment";
import {defineComponent, ref, inject } from "vue";
import {useRouter} from "vue-router";
import {isMobileV2} from "@/components/common/script";
import {LookupActions} from "@/store/lookupModule";
import {FlexScheduleCellSetting, FlexScheduleMutation} from "@/store/flexScheduleModule";
import _ from "lodash";
import {useStore} from "vuex";
import { CompanyConfigInterface } from "@/plugins/companyConfig";
import Remote from "@/remote/scheduleRequest";

declare var Affinity: any;

export enum WeeklyPublishStatus {
    empty = "",
    draft = "draft",
    published = "published",
    partial = "partial",
}

export default defineComponent({
    name: "FlexSchedule",
    components: {
        FlexScheduleCalendar,
        TopFilters
    },
    data() {
        return {
            weekStart: <any>null,
            weekEnd: <any>null,

            managerNo: null,
            dayFocus: moment(),
            weekDays: <any[]>[], // calendar week days name
            // scheduleWeek: <any[]>[], // list of data per week
            datePickerTop: 0,
            datePickerLeft: 0,
            employeeList: <any>[],
            employeeHeightArr: <any>[],
            scheduleWeekMap: new Map(),
            weekStatus: WeeklyPublishStatus.empty,

            //
            showControl: false,
            extraEmpDetail: [],
            isAdmin: false,
            hasFuturePaidDate: false,

            //
            costCenterOptions: <any>[],
            gridColumnsStyle: {},
            tableMinWidthStyle: {},

            // inject
            manageCompanyConfig: <any>null,

        };
    },

    setup() {
        const router = useRouter();
        const TableNameRef = ref(<any>{});
        const store = useStore()
        const FlexScheduleCalendarRef = ref<InstanceType<typeof FlexScheduleCalendar>>();

        const cellSettings: FlexScheduleCellSetting = store.state.FlexSchedule.flexScheduleCellSetting

        if (isMobileV2()) {
            router.push({
                name: "EmployeeTeamScheduleMobile",
            });
        }

        store.commit(FlexScheduleMutation.resetState)
        
        return {
            FlexScheduleCalendarRef,
            TableNameRef,
            cellSettings,
        };
    },
    async mounted() {
        this.manageCompanyConfig = inject('MANAGE_COMPANY_CONFIG') as CompanyConfigInterface;

        const mymanager: any = await Remote.getMyManager()
        this.$store.state.EmployeeModule.managerNo = mymanager[0].EmployeeNo;
        this.managerNo = mymanager[0].EmployeeNo;
        this.$store.dispatch(
            LookupActions.getPositions,
            this.managerNo
        );
        this.$store.dispatch(LookupActions.getShifts);
        this.$store.dispatch(LookupActions.getCostElement);
        await this.$store.dispatch(LookupActions.getWorkTypes);

        if (this.manageCompanyConfig.getAllowSundayFirst()) {
            this.weekStart = this.dayFocus.clone().startOf("week");
            this.weekEnd = this.dayFocus.clone().endOf("week");    
        } else {
            this.weekStart = this.dayFocus.clone().startOf("isoWeek");
            this.weekEnd = this.dayFocus.clone().endOf("isoWeek");    
        }
        
        Remote.GetDefaultWorkTypes().then((res) => {
            // note default worktypes is a array
            this.$store.state.FlexSchedule.defaultWorkTypes = res;
        });
    },

    methods: {
        reloadAfterEdit() {
            console.log('reload requested')
            this.FlexScheduleCalendarRef!.reloadAfterEdit();
        },

        changeWeek(newDateFocus: Moment) {
            console.log('changed week', newDateFocus)
            this.dayFocus = newDateFocus
            if ((this.manageCompanyConfig as any).getAllowSundayFirst()) {
                this.weekStart =  this.dayFocus.clone().startOf("week");
                this.weekEnd =  this.dayFocus.clone().endOf("week");
            } else {
                this.weekStart =  this.dayFocus.clone().startOf("isoWeek");
                this.weekEnd =  this.dayFocus.clone().endOf("isoWeek");
            }
            // this.setupDate(this.weekStart)
        },
    },
});
</script>

