<style lang="scss" src="./schedulesManagement.scss"></style>
<template>
    <div class="vue-sched-management">
        <div class="dbw-box sched-card-default mr-xs-0" v-show="!createNew">
            <div>
                <h3 class="mt-2">Schedules</h3>
            </div>
            <search-schedule
                ref="searchScheduleRef"
                @createNewSchedule="createNewSchedule"
                @selectSchedFromSearch="selectSchedulesDetail"
            ></search-schedule>
        </div>
        <div
            class="dbw-box sched-card-default sched-detail-card sche-detail-mt"
            v-if="schedDetail"
        >
            <div v-if="createNew">
                <h3>Create New Schedule</h3>
            </div>
            <div v-else class="d-flex justify-space-between">
                <h3>Schedule Details</h3>
                <span
                    class="icon-cross vp-icon"
                    @click="closeScheduleDetail(true)"
                ></span>
            </div>
            <schedule-detail
                v-if="schedDetail"
                ref="detail"
                :shiftTypeList="shiftTypeList"
                :detail="schedDetail"
                :read-only="0"
                @cancel="cancelScheduleDetail"
                @reload="reloadSchedulesDetail"
                @schedule-saved="scheduleSaved"
                @schedule-deleted="scheduleDeleted"
            ></schedule-detail>
            <div
                class="sched-man-allocations mt-4"
                v-if="
                    schedDetail &&
                    schedDetail.EmpAllocations &&
                    schedDetail.EmpAllocations.length > 0
                "
            >
                <div class="caption font-weight-bold text-start">
                    Employee Allocations
                </div>
                <div class="aff-table allowcated-employee-table mt-4">
                    <table>
                        <thead>
                            <tr>
                                <th class="text-center">Employee Name</th>
                                <th class="text-center">Date From</th>
                                <th class="text-center">Date To</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr
                                v-for="empSched in schedDetail.EmpAllocations"
                                v-bind:class="{
                                    'hide-none-active-detail':
                                        !empSched.isActive &&
                                        showActiveSchedule,
                                }"
                            >
                                <td>{{ empSched.EmployeeName }}</td>
                                <td class="text-center">
                                    {{
                                        $filters.reformatDateString(
                                            empSched.DateFrom,
                                            null
                                        )
                                    }}
                                </td>
                                <td class="text-center">
                                    {{
                                        $filters.reformatDateString(
                                            empSched.DateTo,
                                            null
                                        )
                                    }}
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <div class="d-flex justify-end mt-2">
                    <button
                        class="grey white--text text-capitalize small"
                        @click="toggleShowActiveSchedule"
                    >
                        <span v-show="!showActiveSchedule">Show Active</span>
                        <span v-show="showActiveSchedule">Show All</span>
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>
<script lang="ts">
import moment from "moment";
import Remote from "@/remote/scheduleRequest";
import ScheduleDetail from "@/components/scheduleDetail/ScheduleDetail.vue";
import SearchSchedule from "@/components/schedulesManagement/SearchSchedule.vue";
import Dialog from "@/components/dialog";
import { defineComponent } from "vue";
import { EmployeeActions } from "@/store/employeeModule";
declare var Affinity: any;

export default defineComponent({
    name: "schedulesManagement",
    components: {
        SearchSchedule,
        "schedule-detail": ScheduleDetail,
    },
    data() {
        return {
            selectedSched: <any>null,
            shiftTypeList: [],
            schedDetail: <any>null,
            createNew: false,
            empListHeight: "",
            showActiveSchedule: true,
            employees: [],
        };
    },
    async mounted() {
        Dialog.loading();
        await Promise.all([this.getShiftTypeList(), this.getEmployees()]);
        Dialog.clear();
    },
    methods: {
        async getShiftTypeList() {
            try {
                let shiftList = await Remote.ShiftTypes();
                this.shiftTypeList = shiftList.map((s: any) => s.ShiftType);
            } catch (e) {
                console.error(e);
            }
        },

        async getEmployees() {
            try {
                this.employees = await this.$store.dispatch(
                    EmployeeActions.getEmployees,
                    Affinity.user.EmployeeNo
                );
            } catch (e) {
                console.error(e);
            }
        },

        toggleShowActiveSchedule() {
            this.showActiveSchedule = !this.showActiveSchedule;
        },

        selectSchedulesDetail(sched: any) {
            if (this.$refs.detail && (this.$refs as any).detail.hasChanges) {
                Dialog.checkChange({
                    message:
                        "There are currently Schedule Detail changes <br /> Do you want to discard your changes?",
                    onOk: () => {
                        this.loadScheduleDetail(sched);
                    },
                });
            } else {
                this.loadScheduleDetail(sched);
            }
        },

        loadScheduleDetail(sched: any) {
            Dialog.loading();
            Remote.ScheduleDetail(sched.Id).then(
                (res) => {
                    this.schedDetail = res.Data;
                    this.checkEmpAllocationActive();
                    Dialog.clear();
                    this.selectedSched = sched;
                },
                (err) => {
                    Dialog.clear();
                    if (err.message === "Network Error") {
                        Dialog.alert(
                            "Lost Internet connection. Please try again after reconnect."
                        );
                    } else {
                        Dialog.alert("Something went wrong");
                    }
                }
            );
        },

        checkEmpAllocationActive() {
            this.schedDetail.EmpAllocations.forEach((detail: any) => {
                if (!detail.DateTo) {
                    detail.isActive = true;
                    return;
                }
                let dateFrom = moment(detail.DateFrom);
                let dateTo = moment(detail.DateTo);
                detail.isActive =
                    dateTo.diff(moment(), "day") >= 0 &&
                    dateFrom.diff(moment(), "day") <= 0;
            });
        },

        createNewSchedule: function () {
            this.createNew = true;
            let startDate = moment();
            startDate.hour(0);
            startDate.minute(0);
            startDate.second(0);
            this.schedDetail = {
                Id: -1,
                Name: "",
                Description: "",
                NoOfWeeks: 1,
                StartDate: startDate,
                Days: [],
            };

            if (this.$refs.detail) {
                (this.$refs.detail as any).hasChanges = true;
            }
        },

        cancelScheduleDetail() {
            if (this.createNew) {
                this.createNew = false;
            }

            this.schedDetail = null;
        },

        reloadSchedulesDetail: function () {
            if (!this.selectedSched) this.schedDetail = null;
            else this.selectSchedulesDetail(this.selectedSched);

            this.createNew = false;
        },

        closeScheduleDetail(changeCheck: boolean) {
            if (!changeCheck || !(this.$refs as any).detail.hasChanges) {
                this.schedDetail = null;
                this.createNew = false;
                this.selectedSched = null;
                (this.$refs as any).searchScheduleRef.deselectScheDetail();
                return;
            }

            Dialog.checkChange({
                message:
                    "There are currently Schedule Detail changes <br /> Do you want to discard your changes?",
                onOk: () => {
                    this.closeScheduleDetail(false);
                },
            });
        },

        scheduleSaved: function (detail: any) {
            if (!this.selectedSched) this.schedDetail = null;
            else {
                this.schedDetail = detail;
                this.selectedSched = {
                    Id: detail.Id,
                };
            }

            (this.$refs as any).searchScheduleRef.getSchedulesList();
            this.createNew = false;
        },

        scheduleDeleted: function () {
            this.selectedSched = null;
            this.schedDetail = null;
            (this.$refs as any).searchScheduleRef.getSchedulesList();
        },
    },
});
</script>

<style lang="scss" scoped>
$button-blue: #44b5ec;

.hide-none-active-detail {
    display: none;
}

.sched-card-default {
    padding: 12px 20px;
    @media screen and (min-width: 992px) {
        min-height: 420px;
    }
    @media screen and (max-width: 991px) {
        margin-top: 8px;
    }
}

.sched-detail-card {
    width: 665px;

    @media screen and (max-width: 768px) {
        width: 100%;
    }
}

.sched-search-box {
    width: 150px;
}

.sched-man-allocations {
    display: inline-block;
    vertical-align: top;
    max-width: 600px;
    max-height: 400px;
    min-height: 200px;
    width: 100%;
    padding-right: 0px;

    table {
        min-width: 300px;
    }
}

.aff-table {
    .selected td {
        background-color: $button-blue;
        color: white;
    }

    .hidden {
        display: none;
    }

    @media screen and (max-width: 768px) {
        overflow-x: auto;
    }
}

.vue-sched-management {

    .sche-detail-mt {
        width: auto;

        @media screen and (max-width: 1440px) {
            margin-top: 10px;

        }
    }
}


.allowcated-employee-table {
    max-height: 300px;
    overflow: auto;
}

.vue-sched-management {
    display: flex;
    @media only screen and (min-width: 992px) {
        flex-wrap: wrap;
    }
    @media only screen and (max-width: 991px) {
        flex-flow: column;
    }
    .sched-card-default {
        // max-width: 720px;

        @media only screen and (min-width: 1201px) {
            // width: calc(50% - 24px);
            min-width: 460px;
            margin-right: 24px;

            &:nth-child(2n) {
                margin-right: 0;
            }
        }

        @media only screen and (max-width: 1200px) {
            margin-top: 24px;
            width: 100%;
        }
    }
}

.text-center {
    text-align: center;
}
</style>
