// Flex Schedule data module. Store current editing schedule data

// import { Module } from "vuex"
import {RootState} from "./index"
import _ from "lodash";
import moment, {Moment} from "moment";
import {ActionTree, GetterTree, Module, MutationTree} from "vuex";
import Remote from "@/remote/scheduleRequest";
import {WorkType} from "@/store/lookupModule";
import {FlexScheduleRequestCap, WeeklyPublishStatus} from "@/components/FlexSchedule/FlexScheduleInterface";
import FlexScheduleM from "@/components/FlexSchedule/Mobile/FlexScheduleM.vue";
import {Leave} from "@/remote/flexSchedulesRequest";
import FlexScheduleMultiColumn from "@/components/FlexSchedule/FlexScheduleMultiColumn.vue";

declare var Affinity: any

export interface DictionaryOfCheckboxes {
    name: string;
    value: boolean;
}

export interface FlexScheduleState {
    selectedDate: Moment
    flexScheduleDay: FlexScheduleDay,
    selectedFlexDay: ScheduleDayInView | null,
    scheduleWeek: ScheduleWeek[],
    editFlexScheduleDayConfig: FlexScheduleDayEditConfig
    flexScheduleCellSetting: FlexScheduleCellSetting
    calendarFocusDate: Moment,
    defaultWorkTypes: WorkType[] | null,
    analysisHoursChartData: any,
    flexScheduleRemoteData: any,
    leaveList: Leave[],
    selectableColumn: SortOptions[],
    employeeSelectableColumn: SortOptions[],
    weekStatus: WeeklyPublishStatus,
    hasFuturePaidDate: boolean,
}

export interface SortOptions {
    optionName: string,
    width: number
    selected: boolean,
    selectable: boolean,
    sort: '' | 'Ascend' | 'Descend',
}

export interface ScheduleWeek {
    empName: string;
    scheDays: ScheduleDayInView[];
}

export interface ScheduleDayInView {
    scheduleDetail?: any;
    employeeName?: string;
    employeeNo?: number;
    date?: string;
    time?: string, // todo check if this can be removed
    startTime: string,
    endTime: string,
    hours: number,
    Hours?: number,
    schedule: string,
    dayInWeek: number,
    column?: string,
    editing?: boolean,
    unAvailability?: UnAvailability,
    scheduleType?: string,
    notes: string,
}

export interface UnAvailability {
    employeeNo: number,
    endTime: string,
    id: number,
    reason: string,
    startTime: string,
    unavailabilityDate: string
}

export interface FlexScheduleDayEditConfig {
    startTime: boolean,
    endTime: boolean,
    hours: boolean,
    breakLength: boolean,
    shiftId: boolean,
    position: boolean,
    costElement1: boolean,
    costElement2: boolean,
}

export interface FlexScheduleDay {
    date: string,
    startTime: string,
    endTime: string,
    hours: number,
    breakLength: number,
    shiftId: string,
    position: string,
    costElement1: string,
    costElement2: string,
    scheduleType: string,
    notes: string,
}

export interface FlexScheduleCellSetting {
    showHours: DictionaryOfCheckboxes,
    showBreakLength: DictionaryOfCheckboxes,
    showShiftId: DictionaryOfCheckboxes,
    showPosition: DictionaryOfCheckboxes
    showNotes: DictionaryOfCheckboxes
    // showCostElement1: DictionaryOfCheckboxes,
    // showCostElement2: DictionaryOfCheckboxes,
}

const EMPTY_FORM = {
    date: '',
    startTime: '',
    endTime: '',
    hours: 0,
    breakLength: 0,
    shiftId: '',
    position: '',
    costElement1: '',
    costElement2: '',
    scheduleType: '',
    notes: '',
}

const DEFAULT_SELECTABLE_COLUMN: SortOptions[] =  [
    {
        optionName: 'Std Hours',
        selectable: false,
        selected: true,
        sort: '',
        width: 90,
    },
    {
        optionName: 'Net Hours',
        selectable: true,
        selected: true,
        sort: '',
        width: 90,
    },
    {
        optionName: 'Worker Type',
        selectable: true,
        selected: false,
        sort: '',
        width: 90,
    },
    {
        optionName: 'Ta Role',
        selectable: true,
        selected: false,
        sort: '',
        width: 70,
    },
    {
        optionName: 'Roster Group',
        selectable: true,
        selected: false,
        sort: '',
        width: 100,
    }
]

export enum FlexScheduleGetter {
    getWeekStatus = "GET_WEEK_STATUS",
    hasFuturePaidDate = "HAS_FUTURE_PAID_DATE",
}

export enum FlexScheduleAction {
    loadTargetSchedule = "LOAD_TARGET_SCHEDULE",
    loadFlexiSchedules = "LOAD_FLEXI_SCHEDULES"
}


export enum FlexScheduleMutation {
    clearForm = "CLEAR_FORM",
    setScheduleDay = "SET_SCHEDULE_DAY",
    saveScheduleDayToWeek = "SAVE_SCHEDULE_DAY_TO_WEEK",
    saveRemoteData = "SAVE_REMOTE_DATA",
    updateEditConfig = "UPDATE_EDIT_CONFIG",
    createNewScheduleDay = "CREATE_NEW_SCHEDULE_DAY",
    setAnalysisData = "SET_ANALYSIS_DATA",
    setConfigAllTrue = "SET_CONFIG_ALL_TRUE",
    setWeekStatus = "SET_WEEK_STATUS",
    setHasFuturePaidDate = "SET_HAS_FUTURE_PAID_DATE",
    resetState = "RESET_STATE",
    resetMultiColumnState = "RESET_MULTI_COLUMN_STATE",
    employeeMultiColumnState = "EMPLOYEE_MULTI_COLUMN_STATE"
}

const defaultState: FlexScheduleState = {
    selectedDate: moment(),
    calendarFocusDate: moment(),
    selectedFlexDay: null,
    defaultWorkTypes: null,
    analysisHoursChartData: null,
    flexScheduleRemoteData: null,
    weekStatus: WeeklyPublishStatus.empty,
    flexScheduleDay: { // modal
        date: '',
        startTime: '',
        endTime: '',
        hours: 0,
        breakLength: 0,
        shiftId: '',
        position: '',
        costElement1: '',
        costElement2: '',
        scheduleType: '',
        notes: ''
    },
    editFlexScheduleDayConfig: {
        startTime: false,
        endTime: false,
        hours: false,
        breakLength: false,
        shiftId: false,
        position: false,
        costElement1: false,
        costElement2: false
    },
    scheduleWeek: [],
    flexScheduleCellSetting: {
        showHours: {
            value: false,
            name: 'Net Hours'
        },
        showBreakLength: {
            value: false,
            name: 'Break Length'
        },
        showShiftId: {
            value: false,
            name: 'Shift ID'
        },
        showPosition: {
            value: false,
            name: 'Position'
        },
        showNotes: {
            value: false,
            name: "Notes"
        },
    },
    leaveList: [],
    selectableColumn: DEFAULT_SELECTABLE_COLUMN,
    employeeSelectableColumn: DEFAULT_SELECTABLE_COLUMN.map((option) => {
        option.selected = false
        return option;
    }),
    hasFuturePaidDate: false,
}

const getters: GetterTree<FlexScheduleState, RootState> = {
    [FlexScheduleGetter.getWeekStatus] (state) {
        return state.weekStatus;
    },
    [FlexScheduleGetter.hasFuturePaidDate]: state => state.hasFuturePaidDate,

}

const mutations: MutationTree<FlexScheduleState> = {
    [FlexScheduleMutation.clearForm](state) {
        state.flexScheduleDay = {...EMPTY_FORM}
    },

    [FlexScheduleMutation.createNewScheduleDay](state, day: ScheduleDayInView) {
        state.flexScheduleDay = EMPTY_FORM // todo fix cache data between create and clear form problem.
        state.flexScheduleDay.scheduleType = '';
        state.selectedFlexDay = day;
    },

    [FlexScheduleMutation.setScheduleDay](state, day: ScheduleDayInView) {
        // state.flexScheduleDay = _.merge({}, state.flexScheduleDay, day)
        state.selectedFlexDay = day;

        state.flexScheduleDay.startTime = day.startTime;
        state.flexScheduleDay.endTime = day.endTime;
        state.flexScheduleDay.hours = day.hours!;
        state.flexScheduleDay.scheduleType = day.scheduleType!;
        state.flexScheduleDay.notes = day.notes!;

        let scheduleDetail = Object.fromEntries(
            Object.entries(day.scheduleDetail).map(
                ([k,v]) => [k.charAt(0).toLowerCase() + k.slice(1), v])
        );

        state.flexScheduleDay = _.merge({}, state.flexScheduleDay, scheduleDetail)
    },
    [FlexScheduleMutation.saveScheduleDayToWeek](state) {
        if (!state.selectedFlexDay) {
            console.error('missing selected flex day');
            return
        }

        state.selectedFlexDay.startTime = state.flexScheduleDay.startTime;
        state.selectedFlexDay.endTime = state.flexScheduleDay.endTime;
        state.selectedFlexDay.scheduleDetail = _.merge({}, state.selectedFlexDay.scheduleDetail, state.flexScheduleDay)

        // state.FlexScheduleDay
    },
    [FlexScheduleMutation.updateEditConfig](state, newConfig: FlexScheduleDayEditConfig) {
        state.editFlexScheduleDayConfig = _.clone(newConfig)
    },

    [FlexScheduleMutation.setAnalysisData](state, data) {
        state.analysisHoursChartData = data;
    },

    [FlexScheduleMutation.saveRemoteData](state, data) {
        state.flexScheduleRemoteData = data
    },

    [FlexScheduleMutation.setConfigAllTrue](state, data) {
        state.flexScheduleCellSetting.showHours.value = true;
        state.flexScheduleCellSetting.showBreakLength.value = true;
        state.flexScheduleCellSetting.showShiftId.value = true;
        state.flexScheduleCellSetting.showPosition.value = true;
        state.flexScheduleCellSetting.showNotes.value = true;
    },

    [FlexScheduleMutation.setWeekStatus](state, status: WeeklyPublishStatus) {
        state.weekStatus = status;
    },
    [FlexScheduleMutation.setHasFuturePaidDate](state, value) {
        state.hasFuturePaidDate = value;
    },
    [FlexScheduleMutation.resetMultiColumnState](state) {
        resetMultiColumnState(state.selectableColumn)
    },
    [FlexScheduleMutation.employeeMultiColumnState](state) {
        state.selectableColumn = state.selectableColumn.map((option) => {
            option.selected = false
            return option;
        })
    },
    [FlexScheduleMutation.resetState](state) {
        state = _.cloneDeep(defaultState);
    },
}

const actions: ActionTree<FlexScheduleState, RootState> = {
    [FlexScheduleAction.loadTargetSchedule]({ commit }, scheduleId: number) {
        Remote.GetFlexScheduleDetail(scheduleId)
            .then(res => {
                console.log('GetFlexScheduleDetail', res)
            })
    },

    [FlexScheduleAction.loadFlexiSchedules]({ commit }, payload: {startDate: Moment, endDate: Moment, employeeNo: number} ) {
        const param = {
            // employeeNo: Affinity.user.EmployeeNo,
            employeeNo: payload.employeeNo,
            startDate: payload.startDate.format('YYYY-MM-DD'),
            endDate: payload.endDate.format('YYYY-MM-DD'),
            forManagedEmployees: true,
        }

        return Remote.GetFlexSchedules(param)
            .then((res: FlexScheduleRequestCap) => {
                let flexScheduleRemoteData = res.FlexSchedules;
                flexScheduleRemoteData.forEach((obj: any) => {
                    obj.id = obj.Id;
                    obj.date = obj.Date;
                    obj.employeeName = obj.EmployeeName;
                    obj.employeeNo = obj.EmployeeNo;
                    obj.startTime = obj.StartTime ? moment(obj.StartTime).format('HH:mm') : ''
                    obj.endTime = obj.EndTime ? moment(obj.EndTime).format('HH:mm') : ''
                    obj.hours = obj.Hours
                    obj.scheduleDetail = obj.ScheduleData
                    obj.scheduleDetail.breakLength = obj.scheduleDetail.BreakLength;
                    obj.scheduleDetail.position = obj.scheduleDetail.Position;
                    obj.scheduleDetail.positionTitle = obj.scheduleDetail.PositionTitle;
                    obj.scheduleDetail.shiftId = obj.scheduleDetail.ShiftId;
                    obj.scheduleDetail.costElement1 = obj.scheduleDetail.CostElement1;
                    obj.scheduleDetail.costElement2 = obj.scheduleDetail.CostElement2;
                    obj.scheduleType = obj.ScheduleType;
                    obj.publishStatus = obj.PublishStatus;
                    obj.isPaid = obj.IsPaid;
                    obj.dayInWeek = moment(obj.Date).day();
                    obj.notes = obj.Notes;
                })
                commit(FlexScheduleMutation.saveRemoteData, flexScheduleRemoteData)

                let unAvailabilitiesData = res.Unavailabilities.map((obj: any) => {
                    return {
                        id: obj.EmpAvailability.Id,
                        unavailabilityDate: obj.UnavailabilityDate,
                        employeeNo: obj.EmpAvailability.EmployeeNo,
                        startTime: obj.EmpAvailability.StartTime,
                        endTime: obj.EmpAvailability.EndTime,
                        reason: obj.EmpAvailability.Reason
                    }
                })

                return {
                    flexSchedules: flexScheduleRemoteData,
                    unAvailabilities: unAvailabilitiesData
                };
            })
    }
}

const FlexSchedule: Module<FlexScheduleState, RootState> = {
    // namespaced: true as true,

    state: _.cloneDeep(defaultState),
    getters: getters,
    mutations: mutations,
    actions: actions
}

function resetMultiColumnState(multiColumnState: SortOptions[]) {
    multiColumnState = _.clone(DEFAULT_SELECTABLE_COLUMN);
}

export default FlexSchedule;