<template>
    <div v-if="pageList.length > 0">
        <div class="pagination">
            <div class="total-page-small">
                <div class="previous-button page-button"
                     v-if="totalPage > 1 && currentPage > 1"
                     @click="selectNextPage(-1)"> &lt;</div>
                <div v-for="pageListNumber of pageList" class="page-button"
                     v-bind:class="{'selected': currentPage === pageListNumber }"
                     @click="selectPage(pageListNumber)">
                    {{ pageListNumber }}
                </div>
                <div class="next-button page-button"
                     v-if="totalPage > 1 && currentPage < totalPage"
                     @click="selectNextPage(1)"> ></div>
                <div v-if="totalPage > 5" class="ml-2">Total Pages: {{ totalPage }}</div>
            </div>
        </div>
        <div v-if="recordCount > 0" class="ml-4">No. of Records: {{ recordCount }}</div>
    </div>
</template>

<script lang="ts">
import {defineComponent, ref} from "vue";
import Dialog from "@/components/dialog";

export default defineComponent ({
    name: "Paginator",
    props: {
        totalPage: {
            type: Number,
            default: 1
        },
        currentPage: {
            type: Number,
            default: 1
        }
    },

    setup(props, {emit}) {
        const pageSize = ref(20)
        const pageList = ref(<number[]> []);
        const recordCount = ref(0);

        function calculatePageList(pageNumber: number, pageSize: number) {
            if (pageSize <= 5) {
                pageList.value = Array.from(Array(pageSize).keys()).map(k => k + 1)
            } else {
                if (pageNumber <= 3) {
                    pageList.value = Array.from(Array(5).keys()).map(k => k + 1)
                } else {
                    if ( pageSize > pageNumber + 2) {
                        pageList.value = Array.from(Array(5).keys()).map(k => k + pageNumber - 2)
                    } else {
                        pageList.value = Array.from(Array(5).keys()).map(k => k + pageSize - 4)
                    }
                }
            }
        }
        async function selectPage(newPageNumber: number) {
            emit('selectPage', newPageNumber)
        }

        async function selectNextPage(pageDiff: number = 1) {
            let newPageNumber = props.currentPage + pageDiff
            emit('selectPage', newPageNumber)
        }

        return {
            pageSize, pageList, recordCount,
            selectPage, selectNextPage, calculatePageList,
        }
    }
})
</script>

<style scoped lang="scss">
$border-color: #00378033;

.total-page-small {
    display: flex;
    align-items: center;
    justify-content: center;
.selected {
    background: #0C96E4;
    color: white;
}
}
.page-button {
    box-sizing: border-box;
    padding: 0;
    margin-right: 5px;
    text-align: center;
    border-radius: 5px;
    cursor: pointer;
    height: 24px;
    width: 24px;
    line-height: 24px;
    font-weight: bold;
    display: inline-block;

    &:last-child {
        margin-right: 0;
    }
}

</style>