<template>
    <div class="mt-4" v-if="!data[0]">
        <h3>No data found.</h3>
    </div>
    <div class="mt-4 costAnalaysisBody" v-else>
        <div class="display-option mt-4">
            <label>Columns: </label>
            <multi-select class="ml-4" :selectOptionInput="displayOptions" :targetString="'description'"
                          :fixed-left="false"></multi-select>
        </div>
        <div class="cost-grid mt-4">
            <div class="day-arr cost-grid-heading"
                 v-bind:style="getGridTemplateStyle()">
                <div class="table-cell"></div>
                <div class="table-cell"></div>
                <div class="table-cell" v-for="val in displayList">
                    {{val.heading}}
                </div>
            </div>
            <perfect-scrollbar>

                <div class="day-arr" v-for="(day, index) in data"
                    v-bind:style="getGridTemplateStyle()">
                    <div class="table-cell" v-if="day.day">
                        {{day.day}}
                    </div>
                    <div class="table-cell">
                        {{day.weekDay}}
                    </div>
                    <div class="table-cell" v-for="[key, value] of Object.entries(displayList)"
                        v-bind:class="getDataStyle(calculatedData[index], key, value)">
                        <span>{{getDisplayValue(day[key], value.isCalculated)}}</span>
                       
                    </div>
                </div>
                <div class="day-arr"
                    v-bind:style="getGridTemplateStyle()">
                    <div class="table-cell bold">
                        Total
                    </div>
                    <div class="table-cell bold"></div>
                    <div class="table-cell bold" v-for="[key, val] of Object.entries(displayList)"
                        v-bind:class="getDataStyle(totalInNumber, key, val)">
                        <span>{{getDisplayValue(total[key], val.isCalculated)}}</span>
                    </div>
                </div>
            </perfect-scrollbar>

        </div>

    </div>
    <div class="main-canvas-wrapper">
        <div class="myChart-wrapper" v-bind:style="{height: 600 + 'px'}">
            <canvas class="main-canvas" ref="myChartRef" id="myChart" colours="[ { fillColor: '#ffff00' }, { fillColor: '#0066ff' } ]" ></canvas>
        </div>
    </div>
</template>

<script lang="ts">
import 'chartjs-adapter-moment';

import Modal from "@/components/common/Modal.vue";

import {defineComponent, nextTick, onMounted, onUnmounted, ref, watch} from "vue";
import { useStore } from 'vuex';
import moment, {Moment} from "moment";
import Remote from "@/remote/scheduleRequest";
import {EmployeeGetters} from "@/store/employeeModule";
import Dialog from "@/components/dialog";
import MultiSelect from "@/components/common/MultiSelect.vue";
import {entries} from "lodash";
import currency from 'currency.js';
import Chart from "chart.js/auto/auto.mjs";
import {
    CostDay,
    CostDayConverted,
    DISPLAY_OPTIONS,
    SOURCE_DISPLAY_LIST,
    UnitDropDown
} from "@/components/Analysis/preset";
import {AnalysisActions} from "@/store/analysisModule";
import Paginator from "@/components/Analysis/Paginator.vue";
let MyChart = <any>null;

declare var Affinity: any

interface Props {
    dayFocus: Moment | null;
    fromDate: Moment | null;
    toDate: Moment | null;
    modalIndex: number;
}
export default defineComponent ({
    name: "CostAnalysisWeekly",
    components: {
        Modal,
        MultiSelect,
        Dialog,
    },
    props: {
        dayFocus: {
            type: Object
        },
        fromDate: {
            type: Object,
        },
        toDate: {
            type: Object,
        },
        selectedUnit: {
            type: String,
        },
        modalIndex: {
            type: Number
        },
        employeeList: {
            type: Array
        },
    },

    setup(props: any) {
        //
        const store = useStore();
        const selectedDate = ref<Moment | null>(null);
        const data = ref<any[]>([]);
        const calculatedData = ref<any[]>([]);
        const total = ref<any>({});
        const totalInNumber = ref<any>({});
        const displayN = ref<number>(6);
        const displayList = ref<any>({});
        const sourceDisplayList = ref<any>(SOURCE_DISPLAY_LIST);
        const weekStart = ref<string>('');
        const weekEnd = ref<string>('');
        const displayOptions = ref<any>(DISPLAY_OPTIONS);
        const noDataError = ref(false);
        const tableUnit = ref(props.selectedUnit);

        const getDataStyle = (day: any, key: any, value: any) => {
            if (!value.isCalculated) {
                return;
            }

            if (key === 'scheduledVSBudgeted' || key === 'actualsVSBudgeted') {
                if (day[key] > 0) {
                    return 'positive-cell'
                } else {
                    return 'negative-cell'
                }
            }
        };

        const getGridTemplateStyle = () => {
            calculateDisplayRow();
            if (displayN.value > 1) {
                return `grid-template-columns: 100px 100px repeat( ${displayN.value} , 100px)`;
            } else {
                return `grid-template-columns: 100px 100px repeat( ${displayN.value} , auto)`;
            }

        };

        const loadGridData = async () => {
            Object.keys(displayList.value).forEach((v: any) => {
                displayList.value[v].isDisplay = false
            });

            let fromDate;
            let toDate;

            fromDate = props.fromDate
                ? props.fromDate.clone().format("YYYY-MM-DD")
                : moment().startOf("week").format("YYYY-MM-DD");

            toDate = props.toDate
                ? props.toDate.clone().format("YYYY-MM-DD")
                : moment().endOf("week").format("YYYY-MM-DD");

            Dialog.loading();

            for (const [key, value] of Object.entries(sourceDisplayList.value)) {
                const found: any = displayOptions.value.find((v: any) => v.key === key);
                if (found && found.isSelected === true) {
                    displayList.value[key] = value;
                    displayList.value[key].isDisplay = found.isSelected;
                } else {
                    // console.error('key not found', key)
                }
            }

            try {
                const res: any = await store.dispatch(AnalysisActions.fetchWeeklyAnalysisData, {
                    weekStart: fromDate,
                    weekEnd: toDate,
                    employeeNumbers: props.employeeList!.filter((employeeNumber: any) => employeeNumber !== null && employeeNumber !== undefined),
                    selectedUnit: props.selectedUnit.toLowerCase(),
                    pageNumber: 1,
                    pageSize: 99
                });

                if (!res.response || !res.response[0]) {
                    data.value = [];
                    total.value = [];
                    // Assuming you would inject a ref named ModalRef into the setup function.
                    Dialog.clear();
                    return;
                }

                let costDays = res.response;
                calculatedData.value = [];

                data.value = costDays.map((costDay: CostDay) => {
                    costDay.actual = (costDay as any).worked || 0;
                    costDay.budgeted = (costDay as any).budget || 0;
                    costDay.sales = (costDay as any).sales || 0;
                    let newDay: CostDay = {...costDay};
                    let convertedDays: CostDayConverted = {} as any;
                    newDay.day = moment(costDay.transDate).format('YYYY-MM-DD');
                    newDay.weekDay = moment(costDay.transDate).format('dddd');

                    // newDay.sales = 10000costDay.sales * 100) / 100;

                    if (sourceDisplayList.value.scheduledVSBudgeted) {
                        newDay.scheduledVSBudgeted = calculateRatioPercentage(costDay.scheduled,costDay.budgeted)
                    }

                    if (sourceDisplayList.value.actualsVSBudgeted) {
                        newDay.actualsVSBudgeted = calculateRatioPercentage(costDay.actual,costDay.budgeted)
                    }

                    if (sourceDisplayList.value.actualsVSScheduled) {
                        newDay.actualsVSScheduled = calculateDiffPercentage(costDay.actual,costDay.scheduled)
                    }
                    if (sourceDisplayList.value.actualsVSSales) {
                        newDay.actualsVSSales = calculateRatioPercentage(costDay.actual,costDay.sales)
                    }

                    if (sourceDisplayList.value.scheduledVSSales) {
                        newDay.scheduledVSSales = calculateRatioPercentage(costDay.scheduled,costDay.sales)
                    }

                    for (let [key, val] of Object.entries(newDay)) {
                        if (Number.isNaN(val)) {
                            (newDay as any)[key] = 0;
                        }
                        if (key === 'day' || key === 'dayNumber' || key === 'transDate' || key === 'date' || key === 'weekDay') {
                            convertedDays[key as keyof typeof costDay] = val;
                        } else {
                            convertedDays[key as keyof typeof costDay] = currency(newDay[key as keyof typeof costDay]!).format({symbol: '' });
                        }

                    }
                    calculatedData.value.push(newDay)

                    return convertedDays;
                });


                calculateTotal();
                
                nextTick(() => {
                    setGraphData();
                    Dialog.clear();
                })
                tableUnit.value = props.selectedUnit;
            } catch(err: any) {
                data.value = [];
                total.value = [];
            }
        };

        const calculateTotal = () => {
            const keyList = [];

            for (const [key] of Object.entries(sourceDisplayList.value)) {
                keyList.push(key);
            }

            if (keyList.length === 1) {
                totalInNumber.value[keyList[0]] = Math.round(calculatedData.value[0][keyList[0]] * 100) / 100;
                return;
            }

            for (let k of keyList) {
                totalInNumber.value[k] = 0;
                for (let data of calculatedData.value) {
                    if (!data[k]) {
                        data[k] = 0;
                    }
                    totalInNumber.value[k] += data[k];
                }
            }

            if (sourceDisplayList.value.scheduledVSBudgeted) {
                totalInNumber.value.scheduledVSBudgeted = calculateRatioPercentage(totalInNumber.value.scheduled, totalInNumber.value.budgeted)
            }
            if (sourceDisplayList.value.actualsVSBudgeted) {
                totalInNumber.value.actualsVSBudgeted = calculateRatioPercentage(totalInNumber.value.actual, totalInNumber.value.budgeted)
            }
            if (sourceDisplayList.value.actualsVSScheduled) {
                totalInNumber.value.actualsVSScheduled = calculateDiffPercentage(totalInNumber.value.actual, totalInNumber.value.scheduled)
            }
            if (sourceDisplayList.value.actualsVSSales) {
                totalInNumber.value.actualsVSSales = calculateRatioPercentage(totalInNumber.value.actual, totalInNumber.value.sales)
            }
            if (sourceDisplayList.value.scheduledVSSales) {
                totalInNumber.value.scheduledVSSales = calculateRatioPercentage(totalInNumber.value.scheduled, totalInNumber.value.sales)
            }


            for (let [key, val] of Object.entries(totalInNumber.value)) {
                if (Number.isNaN(val)) {
                    totalInNumber.value[key] = 0;
                }
                total.value[key as any] = currency(totalInNumber.value[key]).format({symbol: ''});
            }
        }
        const calculateDiffPercentage = (numerator: number, denominator: number) => {
            if (!denominator) {
                return 0;
            }
            return (numerator - denominator) / denominator * 100;
        }

        const calculateRatioPercentage = (numerator: number, denominator: number) => {
            if (!denominator) {
                return 0;
            }
            return numerator / denominator * 100;
        }

        const calculateDisplayRow = () => {
            displayN.value = displayOptions.value.filter((option: any) => option.isSelected ).length;
        }

        const setGraphData = () => {
            // prevent load chart when there is no data
            if (!calculatedData.value || calculatedData.value.length < 1) {
                return;
            }

            let chartLabels = []
            let chartDatasets = [{
                label: 'Schedule',
                data: <any[]>[],
                backgroundColor: [
                    '#e65d79',
                ],
                hidden: false
            }, {
                label: 'Actual',
                data: <any[]>[],
                backgroundColor: [
                    '#6375ff',
                ],
                hidden: false
            }, {
                label: 'Budget',
                data: <any[]>[],
                backgroundColor: [
                    '#ffe04e',
                ],
                hidden: false
            }, {
                label: 'Sales',
                data: <any[]>[],
                backgroundColor: [
                    '#96ff8c',
                ],
                hidden: false
            }]

            for (let option of displayOptions.value) {
                if (option.key === 'scheduled' && !option.isSelected) {
                    chartDatasets[0].hidden = true;
                }
                if (option.key === 'actual' && !option.isSelected) {
                    chartDatasets[1].hidden = true;
                }
                if (option.key === 'budgeted' && !option.isSelected) {
                    chartDatasets[2].hidden = true;
                }
                if (option.key === 'sales' && !option.isSelected) {
                    chartDatasets[3].hidden = true;
                }
            }

            for (const dayData of calculatedData.value) {
                chartLabels.push(dayData.day)
                chartDatasets[0].data.push(dayData.scheduled)
                chartDatasets[1].data.push(dayData.actual)
                chartDatasets[2].data.push(dayData.budgeted)
                chartDatasets[3].data.push(dayData.sales)
            }

            let ctx = (document as any).getElementById('myChart').getContext('2d');

            let config = {
                type: 'bar',
                data: {
                    labels: chartLabels,
                    datasets: chartDatasets,
                },

                options: {
                    responsive: true,
                    plugins: {
                        legend: {
                            labels: {
                                filter: function (item: any) {
                                    return !item.hidden
                                }
                            }
                        }
                    }
                }
            }
            // TODO:handle empty chart data
            if (MyChart) {
                // handle switch tab that only destroy chart
                if (MyChart.ctx === null) {
                    MyChart = new Chart(ctx!, config as any) as any;
                } else {

                    MyChart.data.labels = chartLabels
                    MyChart.data.datasets = chartDatasets

                    MyChart.update();
                }

            } else {
                MyChart = new Chart(ctx!, config as any) as any;
            }
        }

        function getDisplayValue(value: any, isCalculated: boolean) {
            if (isCalculated) {
                return value + '%'
            } else {
                if (tableUnit.value === UnitDropDown.unit) {
                    return value || 0
                }
                return ('$' + value) || 0
            }
        }

        async function exportCSV() {
            if (noDataError.value) {
                Dialog.alert("No data to export");
                return;
            }

            Dialog.loading();
            // const allData: any = await loadGridData();
            const allData: any = calculatedData.value
            Dialog.clear();
            let csvContent = "data:text/csv;charset=utf-8,";

            let headings = []
            for (let v in displayList.value) {
                headings.push(displayList.value[v].heading)
            }
            csvContent += "," +  "," + headings.join(",") + "\r\n";

            const textArray = allData.map((day: any, index: number) => {
                const cells = [
                    day.day,
                    day.weekDay,
                    ...Object.entries(displayList.value).map(([key, value]: [string, any]) => {
                        let text = '';
                        if (day[key] === undefined || day[key] === null) {
                            return '';
                        }

                        text += day[key] ? day[key] : '';
                        if (value.isCalculated) {
                            text += '%';
                        }
                        return text;
                    })
                ];
                return cells.join(',');
            });
            csvContent += textArray.join("\r\n") ;
            csvContent += "\r\n";

            let totalCells = [
                'Total',
                '',
                ...Object.entries(displayList.value).map(([key, value]: [string, any]) => {
                    let text = '';
                    if (total.value[key] === undefined || total.value[key] === null) {
                        return '';
                    }

                    text += total.value[key] ? total.value[key] : '';
                    text = text.replace(/,/g, '');
                    if (value.isCalculated) {
                        text += '%';
                    }
                    return text;
                })
            ];
            
            totalCells.join(',')
            csvContent += totalCells
            csvContent += "\r\n";

            var encodedUri = encodeURI(csvContent);
            var link = document.createElement("a");
            link.setAttribute("href", encodedUri);
            link.setAttribute("download", "Analytic.csv");
            document.body.appendChild(link); // Required for FF

            link.click();
        }


        watch(displayOptions, (val, oldVal) => {
            displayList.value = {};
            for (const [key, value] of Object.entries(sourceDisplayList.value)) {
                const found = val.find((v: any) => v.key.toLowerCase() === key.toLowerCase());
                if (found && found.isSelected) {
                    displayList.value[key] = value;
                    displayList.value[key].isDisplay = found.isSelected;
                } else {
                    // console.error('key not found', key)
                }
            }
            setGraphData();
            localStorage.setItem('costAnalysisSetting_v1.3', JSON.stringify(val));
        }, { deep: true });

        onMounted(() => {
            console.log('weekly mounted')
            const localSetting = JSON.parse(localStorage.getItem('costAnalysisSetting_v1.3')!);

            if (localSetting) {
                displayOptions.value = localSetting;
            }

            loadGridData()
        });
        onUnmounted(() => {
            MyChart.destroy();
            MyChart = null;
        });

        return {
            selectedDate,
            data,
            calculatedData,
            total,
            totalInNumber,
            displayN,
            displayList,
            sourceDisplayList,
            weekStart,
            weekEnd,
            displayOptions,
            getDisplayValue,
            getDataStyle,
            getGridTemplateStyle,
            loadGridData,
            calculateTotal,
            exportCSV,
        }
    },

    computed: {
        comparedValue(): any {
            let returnList = []
            for (let [key, value] of Object.entries(this.displayList)) {
                if ((value as any).isDisplay) {
                    returnList.push(value)
                }
            }
            return returnList
        }
    },

})
</script>

<style scoped lang="scss">
@use '../../style/colors';
$border-color: #00378033;

.costAnalaysisBody {
    overflow: auto;
    min-height: 260px;
    margin-right: 20px;
}

.display-option {
    display: flex;
    align-items: center;
}

.grid-background {
    background: $border-color;
}

.cost-grid {
    display: grid;
    //grid-template-rows: repeat(auto-fill, 60px);
    //grid-template-rows: min-content;
    grid-gap: 1px;
    width: fit-content;
    border: 1px solid $border-color;
    background: $border-color;
}

.day-arr {
    display: grid;
    width: 100%;
    grid-template-columns: 100px 100px repeat(6, 80px);
    //grid-template-rows: min-content;
    //grid-template-rows: repeat(auto-fit, 60px);
    grid-gap: 1px;
}

.table-cell {
    padding: 10px 5px;
    text-align: center;
    background:white;
}

.positive-cell {
    color: red;
}

.negative-cell {
    color: #558115;
}

.main-canvas-wrapper {
    margin-top: 40px;
    width: 80%;
    max-width: 1000px;
}

:deep(.custom-select) {
    width: 220px;
    .custom-select-selected {
        max-width: 220px;
        width: 100%;
    }

    .custom-select-items {
        width: 220px;
    }

}

.ps {
    display: grid;
    grid-gap: 1px;
    height: 520px;

}

</style>