<template>
    <div class="mt-4 full-width grid-section">
        <div class="d-flex justify-center mt-4" v-if="noDataError">
            <h3>No data found.</h3>
        </div>
        <div class="dbw-box" v-if="!noDataError && dataHeadings.length > 0">
            <perfect-scrollbar>
                <div
                    class="grid-extra-width"
                    v-bind:style="gridWidthStyle">
                    <div class="grid-heading" v-bind:style="headingStyle">
                        <template v-for="heading of dataHeadings">
                            <div class="heading-cell">{{ heading }}</div>
                        </template>
                    </div>

                    <div class="grid-body">
                        <perfect-scrollbar>
                            <template v-for="data in dataAnalysis">
                                <div
                                    class="grid-body-row"
                                    v-bind:style="headingStyle"
                                >
                                    <div
                                        class="grid-body-cell"
                                        v-for="[key, value] of Object.entries(data)"
                                    >
                                        {{ value }}
                                    </div>
                                </div>
                            </template>
                        </perfect-scrollbar>
                    </div>
                </div>
            </perfect-scrollbar>
           
        </div>
        <div class="pagination-wrap" v-show="!noDataError && dataHeadings.length > 0">
            <paginator
                ref="PaginatorRef"
                :totalPage="totalPage"
                :currentPage="pageNumber"
                @selectPage="selectPage"
            ></paginator>
            <div v-if="recordCount > 0" class="no-records">
                No. of Records: {{ recordCount }}
            </div>
        </div>
    </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref } from "vue";
import moment from "moment";
import Remote from "@/remote/scheduleRequest";
import Dialog from "@/components/dialog";
import { useStore } from "vuex";
import Paginator from "@/components/Analysis/Paginator.vue";
import {AnalysisActions} from "@/store/analysisModule";
import {transform} from "lodash";

declare var Affinity: any;

export default defineComponent({
    name: "DetailCostReport",
    components: {
        Paginator,
    },
    props: {
        fromDate: {
            type: Object,
        },
        toDate: {
            type: Object,
        },
        employeeList: {
            type: Array,
        },
    },

    setup(props: any) {
        const store = useStore();
        const data = ref(<any[]>[]);
        const costRepeatNo = ref(0);
        const pageNumber = ref(1);
        const pageSize = ref(20);
        const totalPage = ref(5);
        const recordCount = ref(0);
        const PaginatorRef = ref<InstanceType<typeof Paginator>>();

        const dataHeadings = ref(<any[]>[]);
        const headingStyle = ref();
        const gridWidth = ref(2000);
        const gridWidthStyle = ref({ width: "2000px" });

        const dataAnalysis = ref(<any[]>[]);
        const options = ref(["Start/End Time", "Detailed Cost Report"]);
        const noDataError = ref(false);

        onMounted(async () => {
            console.log('cost detail report mounted')
            dataAnalysis.value = [];
            pageNumber.value = 1;
            totalPage.value = 5;
            noDataError.value = false;

            resetData();
            Dialog.loading();
            await loadGridData();
            Dialog.clear();
        });

        async function loadGridDataFromParent() {
            resetData();
            Dialog.loading();
            await loadGridData();
            Dialog.clear();
        }

        async function loadGridData() {
            Dialog.loading();
            noDataError.value = false;
            let fromDate = props.fromDate;
            let toDate = props.toDate;
            let employeeList = props.employeeList;
            if (!fromDate || !toDate) {
                fromDate = moment().startOf("month");
                toDate = moment().endOf("month");
            }

            // const query = {
            //     // pageNumber: pageNumber.value,
            //     // pageSize: pageSize.value,
            //     sortBy: "CommonName",
            //     sortingType: "asc",
            //     pagingEnabled: true,
            //     pageNumber: pageNumber.value,
            //     pageSize: pageSize.value,
            //     employeeNumbers: employeeList,
            //     transDateFrom: fromDate ? fromDate.format("YYYY-MM-DD") : null,
            //     transDateTo: toDate ? toDate.format("YYYY-MM-DD") : null,
            // };

            // await Remote.GetCostAnalysisDetailV2(query).then((res) => {
            await store.dispatch(AnalysisActions.fetchDetailedCostReportData, {
                fromDate: props.fromDate ? props.fromDate.format("YYYY-MM-DD") : null,
                toDate: props.toDate ? props.toDate.format("YYYY-MM-DD") : null,
                employeeNumbers: employeeList!.filter((employeeNumber: any) => employeeNumber !== null && employeeNumber !== undefined),
                pageNumber: pageNumber.value,
                pageSize: pageSize.value})
                .then(res => {
                if (
                    (res.response && res.response.length === 0) ||
                    !res.response
                ) {

                    noDataError.value = true;
                    return;
                }

                const responseArray = res.response.slice();
                responseArray.forEach((data: any) => {
                    data.TransDate = moment(data.TransDate).format(
                        "YYYY-MM-DD"
                    );
                    data.TotalCost = Math.round(data.TotalCost * 100) / 100;

                    for (const [key, value] of Object.entries(data)) {
                        if (typeof value === "number") {
                            data[key] = Math.round(value * 100) / 100;
                        }
                    }
                });

                const response = responseArray.map((data: any) => {
                    const dataArray = Object.values(data);
                    dataArray[0] = data.CommonName;
                    dataArray[1] = data.EmployeeNo;
                    return dataArray;
                });

                let costKeyCount = -1;
                const firstRow = res.response[0];
                for (const keys of Object.keys(firstRow)) {
                    if ((keys as string).indexOf("Cost") > -1) {
                        costKeyCount++;
                    }
                }
                costRepeatNo.value = costKeyCount;
                dataHeadings.value = generateHeading(costRepeatNo.value);

                getHeadingStyle();
                getGridWidth();

                pageNumber.value = res.currentPageNumber;
                recordCount.value = res.recordCount;
                totalPage.value = res.totalPages;

                PaginatorRef.value!.calculatePageList(
                    res.currentPageNumber,
                    res.totalPages
                );
                dataAnalysis.value = response;
            });
            Dialog.clear();

        }

        function generateHeading(repeatNo: number) {
            const staticHeading = [
                "Employee Name",
                "Employee Number",
                "Functional Area",
                "Date",
                "Pay Rate",
                "Total Hours",
                "Total Cost",
            ];

            const repeatHeadingOne = ["Description", "Hours", "Cost"];

            let headings = staticHeading.slice();
            let i = 0;

            while (i < repeatNo) {
                const numberedRepeatHeading = repeatHeadingOne.map(
                    (heading) => {
                        return heading + (i + 1);
                    }
                );
                headings.push(...numberedRepeatHeading);
                i++;
            }

            return headings;
        }

        function getHeadingStyle() {
            let styleString = "";
            Array.from(Array(costRepeatNo.value)).map((_, i) => {
                styleString += " 150px 100px 100px";
            });

            headingStyle.value = {
                "grid-template-columns": `150px 150px 150px 100px 100px 100px 100px${styleString}`,
            };
        }

        function getGridWidth() {
            gridWidth.value =
                850 + 350 * costRepeatNo.value + costRepeatNo.value * 3 + 6;
            gridWidthStyle.value = {
                width: `${gridWidth.value}px`,
            };
            return gridWidth.value;
        }

        async function selectPage(newPageNumber: number) {
            pageNumber.value = newPageNumber;
            Dialog.loading();
            await loadGridData();
            Dialog.clear();
        }

        async function exportCSV() {
            if (noDataError.value) {
                Dialog.alert("No data to export");
                return;
            }

            Dialog.loading();
            const allData: any = await loadAllData();
            Dialog.clear();
            let csvContent = "data:text/csv;charset=utf-8,";

            csvContent += dataHeadings.value.join(",") + "\r\n";

            allData.map((rowArray: any) => {
                csvContent += Object.values(rowArray).join(",") + "\r\n";
            });

            var encodedUri = encodeURI(csvContent);
            var link = document.createElement("a");
            link.setAttribute("href", encodedUri);
            link.setAttribute("download", "Analytic.csv");
            link.setAttribute("Content-Type", "application/octet-stream");
            document.body.appendChild(link); // Required for FF

            link.click();
        }

        async function loadAllData() {
            const query = {
                pageNumber: 1,
                pageSize: 10000,
                pagingEnabled: false,
                sortBy: "CommonName",
                sortingType: "asc",
                employeeNumbers: props.employeeList.filter((employee: number | null) => employee !== null && employee !== undefined),
                transDateFrom: props.fromDate
                    ? props.fromDate.format("YYYY-MM-DD")
                    : null,
                transDateTo: props.toDate
                    ? props.toDate.format("YYYY-MM-DD")
                    : null,
            };
            return await Remote.GetCostAnalysisDetailV2(query).then(
                (res: any) => {
                    const response = res.response.map((data: any) => {
                        data.TransDate = moment(data.TransDate).format(
                            "YYYY-MM-DD"
                        );
                        data.TotalCost = Math.round(data.TotalCost * 100) / 100;

                        for (const [key, value] of Object.entries(data)) {
                            if (typeof value === "number") {
                                data[key] = Math.round(value * 100) / 100;
                            }
                        }

                        const dataArray = Object.values(data);
                        dataArray[0] = data.CommonName;
                        dataArray[1] = data.EmployeeNo;
                        return dataArray;
                    });

                    let costKeyCount = -1;
                    const firstRow = res.response[0];
                    for (const keys of Object.keys(firstRow)) {
                        if ((keys as string).indexOf("Cost") > -1) {
                            costKeyCount++;
                        }
                    }
                    costRepeatNo.value = costKeyCount;
                    dataHeadings.value = generateHeading(costRepeatNo.value);
                    const resultSummary = Object.values(res.resultSummary).map(
                        (value) => {
                            if (typeof value === "number") {
                                return Math.round(value * 100) / 100;
                            }
                            return value;
                        }
                    );
                    let totalArray = Array(7).fill(null).concat(resultSummary);
                    response.push(totalArray);

                    return response;
                }
            );
        }

        function resetData() {
            noDataError.value = false;
            pageNumber.value = 1;
            totalPage.value = 5;
        }

        return {
            data,
            dataHeadings,
            dataAnalysis,
            options,
            headingStyle,
            gridWidthStyle,
            pageNumber,
            pageSize,
            totalPage,
            noDataError,
            recordCount,
            PaginatorRef,
            selectPage,
            exportCSV,
            // fromDate, toDate,
            loadGridData,
            // loadGridDataFromParent,
        };
    },
});
</script>

<style scoped lang="scss">
@use "../../style/colors";
$border-color: #00378033;

rt-controls {
    label {
        line-height: 32px;
    }

    .input-box {
        font-size: 12px;
    }

    @media screen and (min-width: 768px) {
        display: flex;
        justify-content: flex-start;
        align-items: flex-end;

        > div {
            margin-right: 12px;
            label {
                margin-right: 8px;
            }
        }

        .input-box {
            width: 90px !important;
        }
    }
    @media screen and (max-width: 767px) {
        > div {
            margin-bottom: 12px;
            label {
                display: block;
                margin-bottom: 8px;
            }
        }
    }
}

.grid-extra-width {
    width: 3000px;
}

.grid-heading {
    display: grid;
    width: 100%;
    grid-template-columns: 200px repeat(2, 1fr) 200px 150px repeat(4, 0.8fr) repeat(
            2,
            1fr
        );
    grid-gap: 1px;
}

.heading-cell {
    padding: 4px 8px;
    text-align: center;
    border-bottom: 1px solid #c8d1db;
}

.grid-body {
    display: grid;
    grid-gap: 1px;
    width: 100%;
    height: calc(100vh - 300px);
    overflow: auto;
    border-top: none;
    border-bottom: 1px solid #c8d1db;
}

.grid-body-row {
    display: grid;
    width: 100%;
    grid-template-columns: 200px repeat(2, 1fr) 100px 150px repeat(4, 0.8fr) repeat(
            2,
            1fr
        );
    grid-gap: 1px;
    text-align: center;
}

.grid-body-cell {
    display: grid;
    align-items: center;
    min-height: 30px;
    border-bottom: 1px solid #c8d1db;
}

.input-box {
    width: 90px !important;
    padding: 4px 12px;
    border: 1px solid #767676;
    font-size: 12px;
    border-radius: 4px;
}

.action-button {
    box-shadow: none;
}

:deep(.custom-select) {
    width: 160px;

    .custom-select-selected {
        min-height: auto;
        padding: 4px 10px;
    }

    .custom-select-items {
        width: 160px;
    }
}
</style>
