<template>
    <div class="mt-4" v-if="!data[0]">
        <h3>No data found.</h3>
    </div>
    <div class="mt-4 costAnalaysisBody" v-else>
        <div class="cost-grid">
            <div class="day-arr cost-grid-heading">
                <div class="table-cell"></div>
                <div class="table-cell" v-for="(val, index) in displayList" :key="index">
                    {{val}}
                </div>
            </div>
            <perfect-scrollbar>
                <div class="day-arr" v-for="(day, index) in formattedData" :key="index">
                    <span class="day-cell" v-for="(val, index) in day" :key="index">
                        <div class="table-cell" v-if="val !== null && val !== undefined">
                            {{val}}
                        </div>
                    </span>
                </div>
                <div class="day-arr">
                    <span class="day-cell" v-for="(val, index) in formattedTotal" :key="index">
                        <div class="table-cell bold" v-if="val !== null && val !== undefined">
                            {{val}}
                        </div>
                    </span>
                </div>
            </perfect-scrollbar>
        </div>

    </div>
    <div class="main-canvas-wrapper" v-show="currentEmployeeList!.length <= 20">
        <div class="myChart-wrapper" v-bind:style="{height: 600 + 'px'}">
            <canvas class="main-canvas" ref="myChartRef" id="employeeWeeklyChart" ></canvas>
        </div>
    </div>
    <div class="mt-4 ml-4" v-show="currentEmployeeList!.length > 20">Graphs can only show up to 20 employees.</div>
</template>

<script lang="ts">
import 'chartjs-adapter-moment';

import Modal from "@/components/common/Modal.vue";

import {defineComponent, nextTick, onMounted, onUnmounted, ref, watch} from "vue";
import moment, {Moment} from "moment";
import Remote from "@/remote/scheduleRequest";
import {EmployeeGetters} from "@/store/employeeModule";
import Dialog from "@/components/dialog";
import {entries} from "lodash";
import currency from 'currency.js';
import Chart from "chart.js/auto/auto.mjs";
import {getDateMockEmployeeUnit, getDateMockEmployeeValue} from "@/components/Analysis/AnalysisMockData";
import {nameOfDays} from "@/components/common/script";
import CustomSelect from "@/components/common/CustomSelect.vue";
import {AnalysisActions} from "@/store/analysisModule";
import { useStore } from 'vuex';
import { UnitDropDown } from './preset';
let MyChart = <any>null;

declare var Affinity: any


export default defineComponent ({
    name: "EmployeeWeekly",
    components: {
        CustomSelect,
        Modal,
        Dialog,
        // MultiSelect,
    },
    props: {
        dayFocus: {
            type: Object
        },
        fromDate: {
            type: Object,
        },
        toDate: {
            type: Object,
        },
        selectedUnit: {
            type: String,
        },
        modalIndex: {
            type: Number
        },
        employeeList: {
            type: Array
        },
    },

    setup(props) {
        const store = useStore();
        const selectedDate = ref<Moment | null>(null);
        const data = ref<any[]>([]);
        const formattedData = ref<any[]>([]);
        const dataHours = ref<any[]>([]);
        const dataUnits = ref<any[]>([]);
        const unitOptions = ref<string[]>(['hours', 'unit']);

        const calculatedData = ref<any[]>([]);
        const total = ref<any>({});
        const formattedTotal = ref<any>({});
        const displayN = ref<number>(6); // number of column to display,

        const displayList = ref<any>({});
        const sourceDisplayList = ref<any>({
            scheduled: {
                heading: 'Schedule',
                isDisplay: true,
                isCalculated: false,
            },
            actual: {
                heading: 'Actual',
                isDisplay: false,
                isCalculated: false,
            },
            budgeted: {
                heading: 'Budget',
                isDisplay: false,
                isCalculated: false,
            },
            sales: {
                heading: 'Sales',
                isDisplay: false,
                isCalculated: false,
            },
        });

        const weekStart = ref<string>('');
        const weekEnd = ref<string>('');
        const noDataError = ref(false);
        const unitFormat = ref();
        const tableUnit = ref(props.selectedUnit);
        const currentEmployeeList = ref(props.employeeList);


        onMounted(() => {
            loadDataBySelection();
        });

        onUnmounted(() => {
            MyChart.destroy();
            MyChart = null;
        });

        const loadDataBySelection = () => {
            currentEmployeeList.value = props.employeeList;
            tableUnit.value = props.selectedUnit;
            if (tableUnit.value === UnitDropDown.value) {
                unitFormat.value = {symbol: '$', precision: 2}
            } else {
                unitFormat.value = {symbol: '', precision: 2}
            }


            let fromDate = (props.fromDate as any).clone().format("YYYY-MM-DD");

            let endDate =  (props.toDate as any).clone().format("YYYY-MM-DD");
            Dialog.loading()
            store.dispatch(AnalysisActions.fetchEmployeeWeeklyAnalysisData,  {
                weekStart: fromDate,
                weekEnd: endDate,
                employeeNumbers: props.employeeList!.filter((employeeNumber: any) => employeeNumber !== null && employeeNumber !== undefined),
                selectedUnit: props.selectedUnit?.toLowerCase(),
                pageNumber: 1,
                pageSize: 1000
            }).then((res: any) => {
                console.log('res', res.response)
                displayList.value = ['Schedule', 'Actual', 'Variance'];
                data.value = res.response.map((data: any) => {
                    return [data.employee, data.scheduled, data.actual, data.variance]
                        // currency(data.scheduled).format({symbol: ''}),
                        // currency(data.actual).format({symbol: ''}),
                        // currency(data.variance).format({symbol: ''})]
                })

                
                formattedData.value = res.response.map((data: any) => {
                return [data.employee,
                    currency(data.scheduled).format(unitFormat.value),
                    currency(data.actual).format(unitFormat.value),
                    currency(data.variance).format(unitFormat.value)]
                })
                const totals = data.value.reduce((acc, curr) => {
                    acc[0] += curr[1];
                    acc[1] += curr[2];
                    acc[2] += curr[3];
                    return acc;
                }, [0, 0, 0]);
                
                total.value = totals
                formattedTotal.value = ['Total', ...totals.map((total: any) => currency(total).format(unitFormat.value))]
              
                nextTick(() => {
                    setGraphData();
                    Dialog.clear();
                })
            })

        }

        const setGraphData = () => {
            console.log('data', data.value)
            let chartLabels = []
            let chartDatasets = [{
                label: 'Scheduled',
                data: <any[]>[],
                backgroundColor: [
                    'rgba(255, 99, 132)',
                ],
            }, {
                label: 'Actual',
                data: <any[]>[],
                backgroundColor: '#93e093'
            }]

            chartLabels = data.value.map((data: any) => {
                return data[0] // employee name
            })

            for (const dayData of data.value) {
                chartDatasets[0].data.push(dayData[1])
                chartDatasets[1].data.push(dayData[2])
            }

            let ctx = (document as any).getElementById('employeeWeeklyChart').getContext('2d');

            let config = {
                type: 'bar',
                data: {
                    labels: chartLabels,
                    datasets: chartDatasets,
                },

                options: {
                    responsive: true,
                }
            }
            if (MyChart) {
                console.log('my chart', MyChart)
                // handle switch tab that only destroy chart
                if (MyChart.ctx === null) {
                    MyChart = new Chart(ctx!, config as any ) as any;
                } else {
                    MyChart.data.labels = chartLabels;
                    MyChart.data.datasets = chartDatasets;
                    MyChart.update();
                }

            } else {
                MyChart = new Chart(ctx!, config as any ) as any;
            }
        }

        async function exportCSV() {
            if (noDataError.value) {
                Dialog.alert("No data to export");
                return;
            }

            Dialog.loading();
            // const allData: any = await loadDataBySelection();
            const allData: any = data.value;
            Dialog.clear();
            let csvContent = "data:text/csv;charset=utf-8,";

            csvContent +=  "," + displayList.value.join(",") + "\r\n";

            allData.map((rowArray: any) => {
                csvContent += Object.values(rowArray).join(",") + "\r\n";
            });

            csvContent += ['Total', ...total.value].slice().join(",") + "\r\n";

            var encodedUri = encodeURI(csvContent);
            var link = document.createElement("a");
            link.setAttribute("href", encodedUri);
            link.setAttribute("download", "Analytic.csv");
            document.body.appendChild(link); // Required for FF

            link.click();
        }


        return {
            selectedDate,
            data,
            formattedData,
            dataHours,
            dataUnits,
            unitOptions,
            calculatedData,
            total,
            formattedTotal,
            displayN,
            displayList,
            sourceDisplayList,
            weekStart,
            weekEnd,
            currentEmployeeList,
            loadDataBySelection,
            setGraphData,
            exportCSV,
        }
    },
})
</script>

<style scoped lang="scss">
@use '../../style/colors';

@import '../../style/costing-table';

$border-color: #00378033;

.costAnalaysisBody {
    min-height: 260px;
}

.grid-background {
    background: $border-color;
}

.cost-grid {
    display: grid;
    grid-gap: 1px;
    width: fit-content;
    border: 1px solid $border-color;
    background: $border-color;
    max-height: 600px;
    overflow-y: auto;
}

.day-arr {
    display: grid;
    width: 100%;
    grid-template-columns: 100px repeat(3, 100px);
    grid-gap: 1px;
}

.table-cell {
    padding: 10px 5px;
    text-align: center;
    background:white;
}

.positive-cell {
    color: red;
}

.negative-cell {
    color: #558115;
}

.main-canvas-wrapper {
    width: 50%;
    margin-left: 40px;
}

:deep(.custom-select) {
    width: 220px;
    .custom-select-selected {
        max-width: 220px;
        width: 100%;
    }

    .custom-select-items {
        width: 220px;
    }

}

.ps {
    display: grid;
    grid-gap: 1px;
    height: 520px;

}

</style>