import {ActionTree, GetterTree, Module, MutationTree} from "vuex";

import {RootState} from "@/store/index";
import Remote from "@/remote/scheduleRequest";
import {LookupGetters} from "@/store/lookupModule";

export interface ManagedEmployeeRemote {
    CommonName: string,
    EmployeeNo: number
    PositionCode: string
    StdHoursPerWeek: number
    Cost1: string,
}

export interface ManagedEmployee {
    commonName: string,
    employeeName: string,
    employeeNo: number
    positionCode: string
    stdHoursPerWeek: number
    cost1: string,
}


export interface EmployeeState {
    employees: ManagedEmployee[]
    employeesWithPeronsalSche: number[],
    filteredEmployees: ManagedEmployee[],
    managerNo: number | null,
}

export enum EmployeeGetters {
    getEmployees = "GET_EMPLOYEES",
    getFilteredEmployees = "GET_FILTERED_EMPLOYEES"
}

export enum EmployeeMutations {
    setEmployees = "SET_EMPLOYEES",
    resetEmployees = "RESET_EMPLOYEES"
}

export enum EmployeeActions {
    getEmployees = 'GET_EMPLOYEES',    
}

const employeeGetters: GetterTree<EmployeeState, RootState> = {
    [EmployeeGetters.getEmployees] (state): ManagedEmployee[] {
        return state.employees;
    },

    [EmployeeGetters.getFilteredEmployees] (state): ManagedEmployee[] {
        return state.filteredEmployees;
    }
}

const employeeMutations: MutationTree<EmployeeState> = {
    [EmployeeMutations.setEmployees](state, employees: any[]) {
        state.employees = employees;
        state.filteredEmployees = employees.slice();
    },

    [EmployeeMutations.resetEmployees](state) {
        state.employees = []
        state.employeesWithPeronsalSche = []
        state.filteredEmployees = []
    }
}

const employeeActions: ActionTree<EmployeeState, RootState> = {
    [EmployeeActions.getEmployees] ({ commit, state, rootState}, employeeNo: number) {
    // [EmployeeActions.getEmployees] (c, employeeNo: number) {
        if (state.employees.length > 0) {
            return state.employees
        }
        const workTypes = (rootState as any).LookUp.workTypes
        return Remote.GetManagedEmployees(employeeNo).then(res => {
            let convertedEmployees: ManagedEmployee[] = res.map((employeeRemote: any) => {
                employeeRemote.employeeName = employeeRemote.CommonName;
                employeeRemote.workTypeName = findWorkType(employeeRemote.WorkerType, workTypes)?.description

                return Object.fromEntries(
                    Object.entries(employeeRemote).map(([k,v]) => [k.charAt(0).toLowerCase() + k.slice(1), v])
                );
            })
            commit(EmployeeMutations.setEmployees, convertedEmployees)

            return convertedEmployees
        })
    },
}

const EmployeeModule: Module<EmployeeState, RootState> = {
    state: {
        employees: [],
        employeesWithPeronsalSche: [],
        filteredEmployees: [],
        managerNo: null,
    },
    getters: employeeGetters,
    mutations: employeeMutations,
    actions: employeeActions
}

function findWorkType(workTypeId: number, workTypes: any[]) {
    return workTypes.find((w => {

        return w.code === Number(workTypeId)
    }))
}

export default EmployeeModule;
