<template>
    <Modal ref="ModalRef">
        <template v-slot:header>
            <div>
                <h2>
                    <b>Create Draft Schedule</b>
                </h2>
            </div>
        </template>
        <template v-slot:body>
            <div>
                <form>
                    <div class="line-break my-4"></div>
                    <div>
                        <div class="d-flex align-center">
                            <span v-tooltip.top="{content: 'Copies information from a previous flexi schedule and pastes it into a current or future flexi schedule.',
                             classes: 'tool-tip-width'}">Copy from Flexi Schedules</span>
                        </div>
                        <div class="mt-2 draft-option pa-2">
                            <div class="grey-wrapper" v-if="sourceMode !== 'copy'"></div>
                            <div>
                                <span class="input-label"
                                      v-tooltip.top="{content: 'The date to copy from.'}">From Date</span>
                                <input class="input-box" type="text" v-model="copyFromDateText"
                                       @click="updateDateTime($event, 'copyFromDate')">
                            </div>
                            <div class="mt-2">
                                <span class="input-label"
                                      v-tooltip.top="{content: 'The number of days to copy.'}">Number of Days</span>
                                <input class="input-box" type="number" max="14" v-model.number="copyToDateDuration"
                                    @change="validNoDays">
                                <div class="mt-1" v-if="showNoDaysError">
                                    <span class="red">Number of days cannot be greater than 14</span>
                                </div>
                            </div>
                            <div class="mt-2">
                                <span class="input-label"
                                      v-tooltip.top="{content: 'The date in the flexi schedule when copied information will start. For example, if you copy a 5-day block from 1 April and start it on 11 July, it will run from 11- 15 July.'}">Start On</span>
                                <input class="input-box" type="text" v-model="targetDateText"
                                       @click="updateDateTime($event, 'targetDate')">
                            </div>
                        </div>

                    </div>
                </form>

                <div class="line-break my-4"></div>
                <div class="d-flex">
                    <div class="container-half">
                        <div>
                            Paste Options
                        </div>
                        <div class="px-2 mt-4">
                            <div class="d-flex align-center">
                                <input type="radio" name="copyOption" value="replace" class="mt-0 ml-0" v-model="copyOption">
                                <span v-tooltip.top='{content: "When copied information is pasted in, it will replace all information on the days specified.",
                         classes: "tool-tip-width"}'>Replace All</span>
                            </div>
                            <div class="d-flex align-center mt-2">
                                <input type="radio" name="copyOption" value="merge" class="mt-0 ml-0" v-model="copyOption">
                                <span v-tooltip.top='{content: "When copied information is pasted in, it will replace information on the days specified only if there is a conflict."}'>Merge</span>
                            </div>
                        </div>

                    </div>
                    <div class="container-half">
                        <label>Exclude Worker Type</label>
                        <multi-select class="mt-4 " :selectOptionInput="workTypeOptions" :targetString="'description'"
                                      :fixed-left="true"></multi-select>
                    </div>

                </div>
                <div class="line-break my-4"></div>
                <div class="d-flex">
                    <div>
                        <input type="checkbox" v-model="overrideTeam">
                        <span v-tooltip.top="{content: 'Selecting this option will copy Flexi Schedules for all non-published employees' +
                         ' as well as those employees who are already on a Team or a Personal Schedule.'}">Override Team and Personal Schedules</span>
                    </div>
                </div>
                <date-picker ref="DatePickerRef" :top="datePickerTop" :left="datePickerLeft"
                             :min-date="datePickerMin" :max-date="datePickerMax"></date-picker>

            </div>

        </template>
        <template v-slot:footer>
            <div class="d-flex justify-end full-width mt-4">
                <button class="blue xsmall white--text mr-2" @click="actionByMode">Create</button>
                <button class="orange xsmall white--text" @click="cancel">Cancel</button>
            </div>
        </template>

    </Modal>
</template>

<script lang="ts">
import {defineComponent} from "vue";
import Modal from "@/components/common/Modal.vue";
import moment, {Moment} from "moment";
import DatePicker from "@/components/common/DatePicker.vue";
import Remote from '@/remote/scheduleRequest'
import Dialog from "@/components/dialog";
import MultiSelect from "@/components/common/MultiSelect.vue";
import {WorkType} from "@/store/lookupModule";

export enum CopyOptions {
    copy = 'replace',
    merge = 'merge'
}

export default defineComponent({
    name: "DraftCreationDialog",
    components: {
        Modal,
        DatePicker,
        MultiSelect
    },
    data() {
        return {
            datePickerTop: 0,
            datePickerLeft: 0,
            sourceMode: 'copy',
            copyOption: 'replace',
            overrideTeam: false,

            //
            scheduleStartDateText: '',
            copyFromDateText: '',
            targetDateText: '',

            //
            scheduleStartDate: moment(),
            copyFromDate: moment(),
            copyToDateDuration: 0,
            targetDate: moment(),
            datePickerMax: <Moment | null>null,
            datePickerMin: <Moment | null>null,

            //
            dateTarget: '',
            copyPersonalDuration: 0,

            // multi select
            workTypeOptions: [],
            selected: {},
            options: [],
            left: 0,

            //
            showNoDaysError: false,

        }
    },
    mounted() {
        // this.toggleShow();
        // this.left = (this.$refs as any).selectRef.getClientRects()[0].left;
    },
    methods: {
        selectOption() {

        },
        resetData() {
            this.datePickerTop = 0;
            this.datePickerLeft = 0;
            this.sourceMode = 'copy';
            this.copyOption = 'replace';
            this.scheduleStartDateText = '';
            this.copyFromDateText = '';
            this.targetDateText = '';
            this.scheduleStartDate = moment();
            this.copyFromDate = moment();
            this.copyToDateDuration = 0;
            this.targetDate = moment();
            this.dateTarget = '';
            this.copyPersonalDuration = 0;
            this.datePickerMax = null;
            this.datePickerMin = null;
            //
            this.showNoDaysError = false;

            this.workTypeOptions = this.$store.state.LookUp.workTypes;
        },
        toggleShow() {
            this.resetData();

            (this.$refs.ModalRef as any).toggleShow();
        },

        actionByMode() {
            if (this.sourceMode === 'copy') {
                this.createDraft();
            } else {
                this.importFromPersonal();
            }
        },

        importFromPersonal() {
            Dialog.loading()

            let releaseListEmployeeNumbers = []
            for (const sche of this.$store.state.FlexSchedule.scheduleWeek.values()) {
               if (!!this.$store.state.EmployeeModule.filteredEmployees.find((employee: any) => employee.employeeNo === sche.employeeNo)) {
                   releaseListEmployeeNumbers.push(sche.employeeNo)
               }
            }

            let data = {
                CopyFromPersonalSchedule: true,
                EmployeeNumbers:  releaseListEmployeeNumbers,
                PersonalSchedCopyDurationInDays: this.copyPersonalDuration,
                TargetDate: this.scheduleStartDate.format('DD/MMM/YYYY'),
                CopyOption: this.copyOption,
                ExcludedworkTypes: this.getExcludedWorkTypes()
            }

            Remote.CopyFlexSchedule(data)
            .then(res => {
                (this.$refs.ModalRef as any).toggleShow();
                this.$emit('reload');
            })
            .finally(() => {
                // Dialog.clear();
            })
        },

        validForm() {
            // extend for different form parts.
            return this.validNoDays();
        },

        validNoDays() {
            if (this.copyToDateDuration > 14 ) {
                this.showNoDaysError = true;
                return false;
            } else {
                this.showNoDaysError = false;
                return true;
            }
        },

        createDraft() {
            if (!this.validForm()) {
                return;
            }
            
            Dialog.loading()
            let releaseListEmployeeNumbers = []

            if (this.overrideTeam) {
                for (const sche of this.$store.state.FlexSchedule.scheduleWeek.values()) {
                    if (sche.includeTeamOrPersonal) {
                        releaseListEmployeeNumbers.push(sche.employeeNo)
                    } else {
                        if (sche.empSchePublishState !== 'published') {
                            releaseListEmployeeNumbers.push(sche.employeeNo)
                        }
                    }
                }
            } else {
                for (const sche of this.$store.state.FlexSchedule.scheduleWeek.values()) {
                    if (sche.empSchePublishState !== 'published') {
                       releaseListEmployeeNumbers.push(sche.employeeNo)
                    }
                }
            }

            releaseListEmployeeNumbers = releaseListEmployeeNumbers.filter((releaseEmployeeNo: any) => {
                return !!this.$store.state.EmployeeModule.filteredEmployees.find((employee: any) => employee.employeeNo === releaseEmployeeNo)
            })

            let data = {
                CopyFromPersonalSchedule: false,
                EmployeeNumbers: releaseListEmployeeNumbers,

                CopyFromDate: this.copyFromDate.format('DD/MMM/YYYY'),
                CopyToDate: this.copyFromDate.add(this.copyToDateDuration - 1, 'days').format('DD/MMM/YYYY'),
                TargetDate: this.targetDate.format('DD/MMM/YYYY'),
                CopyOption: this.copyOption,
                ExcludedworkTypes: this.getExcludedWorkTypes(),
                OverrideTeam: this.overrideTeam
            }
            Remote.CopyFlexSchedule(data)
            .then(res => {
                Dialog.clear();

                (this.$refs.ModalRef as any).toggleShow();
                Dialog.alert(`Your schedules are being copied. This process can take a few minutes. You will need to refresh your screen to see the updated schedules.`);
                // this.$emit('reload');
            })
            .finally(() => {
                // Dialog.clear();
            })
        },

        getExcludedWorkTypes(): number[] {
            let selectedWorkTypes = this.workTypeOptions.filter((workType: WorkType) => {
                return workType.isSelected
            })

            if ( selectedWorkTypes.length < 1 ) {
                selectedWorkTypes = this.$store.state.FlexSchedule.defaultWorkTypes
            }

            return selectedWorkTypes.map((workType: WorkType) => {
                return workType.code
            })
        },

        async updateDateTime($event: any, selectedDateTarget: string) {
            let element = $event.target.getClientRects()[0]
            this.datePickerTop = element.bottom;
            this.datePickerLeft = element.left;

            try {
                switch (selectedDateTarget) {
                    case 'ScheduleStartDate' :
                        this.scheduleStartDate = await (this.$refs.DatePickerRef as any).toggleShow(this.scheduleStartDate);
                        this.scheduleStartDateText = this.scheduleStartDate.format('DD/MM/YYYY')
                        return;
                    case 'copyFromDate' :
                        this.datePickerMax = null;
                        this.datePickerMin = null;
                        this.copyFromDate = await (this.$refs.DatePickerRef as any).toggleShow(this.copyFromDate);
                        this.copyFromDateText = this.copyFromDate.format('DD/MM/YYYY')
                        return;
                    case 'targetDate' :
                        this.datePickerMax = null;
                        this.datePickerMin = moment().add(-2, "day");
                        this.targetDate = await (this.$refs.DatePickerRef as any).toggleShow(this.targetDate);
                        this.targetDateText = this.targetDate.format('DD/MM/YYYY')
                        return;

                }
            } catch (e) {
                return
            }
        },
        cancel() {
            this.toggleShow();
        }

    }
})
</script>

<style scoped lang="scss">
//@use '../../style/colors';
    .line-break {
        border-top: 1px solid rgba(#7e7e7e, 0.5);
    }

    .input-label {
        display: inline-block;
        width: 140px;
    }

    .input-box {
        width: 90px !important; 
        padding: 4px 12px;
        border: 1px solid #767676;
        font-size: 12px;
        border-radius: 4px;
    }

    .grey-wrapper {
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        background: rgba(0,0,0,.12);
        border-radius: 5px;
        z-index: 1
    }

    .draft-option {
        position: relative;
    }

    input[type=radio] {
        cursor: pointer;
    }

    .container-half {
        width: 50%;
        flex-shrink: 0;
        flex-grow: 0;
    }


</style>