<template>
    <div class="default-form sched-detail-form mt-4">
        <form @change="formUpdated" v-on:submit.prevent="formSubmited($event)">
            <div class="caption d-flex" v-if="isNewSchedule && $CompanyConfig.isAllowPersonal">
                <div class="d-flex align-center">
                    <input type="radio" id="personal" value="personal" class="cursor-pointer" v-model="createType" />
                    <label for="personal" class="ml-2 cursor-pointer">Create Personal Schedule</label>
                </div>
                <div class="d-flex align-center ml-6">
                    <input type="radio" id="team" value="team" class="cursor-pointer" v-model="createType" />
                    <label for="team" class="ml-2 cursor-pointer">Create Team Schedule</label>
                </div>
            </div>
            <div v-if="detail.Archive" class="red--text archive-text">
                <span>Archived</span>
            </div>
            <div class="form-control mt-4">
                <div class="form-label">Name</div>
                <input class="caption underline-input full-width" name="name" @keyup="hasChanges = true" counter="49"
                    v-if="isNewSchedule && createType === 'team'" v-model="detail.Name" />
                <div v-else-if="
                        isNewSchedule &&
                        createType === 'personal' &&
                        detail.EmployeeNo
                    ">
                    {{ "Personal_" + detail.EmployeeNo }}
                </div>
                <div v-else class="label-detail" v-tooltip.top="{
                        content: detail.Name,
                        delay: { show: 3000, hide: 100 },
                    }">
                    {{ detail.Name }}
                </div>
            </div>
            <div class="form-control" v-if="
                    createType === 'personal' && $CompanyConfig.isAllowPersonal
                ">
                <div class="form-label">
                    <span>Employee No</span>
                </div>

                <span v-if="isNewSchedule && employees && employees.length > 0" class="employeeNo-autoComplete">
                    <auto-complete :inputList="employees" :optionName="'joinedName'"
                        @selected="selectedEmployee"></auto-complete>
                </span>

                <div v-else class="label-detail" v-tooltip.top="{
                        content: detail.EmployeeNo,
                        delay: { show: 3000, hide: 100 },
                    }">
                    {{ detail.EmployeeNo }}
                </div>
            </div>
            <div class="form-control">
                <div class="form-label">Description</div>
                <textarea class="caption description-area" rows="2" @keyup="hasChanges = true"
                    v-model="detail.Description">
                </textarea>
            </div>
            <div class="form-control effective-date">
                <div class="form-label">Effective Date</div>
                <div class="inline-block">
                    <input class="date-input" :value="$filters.reformatDate(detail.StartDate)"
                        @click="openDatePicker" />
                    <span class="input-icon icon-calendar xs-icon"></span>
                </div>
            </div>

            <div class="form-control number-of-weeks">
                <div class="form-label">Number of Weeks</div>
                <label for="now"></label>
                <input class="number-input" max="12" min="1" step="1" id="now" name="now" type="number"
                    v-model="detail.NoOfWeeks" @change="updateNoOfWeeks" @keydown="numLimitKeyDown"
                    style="width: 60px" />
                <div class="mobile-arrow">
                    <button type="button" class="icon small" v-if="detail.NoOfWeeks < 12" @click="increaseNumber">
                        <span class="icon-arrowhead-up arrow-icon"></span>
                    </button>
                    <button type="button" class="ml-2 icon small" v-if="detail.NoOfWeeks > 1" @click="reduceNumber">
                        <span class="mt-1 icon-arrowhead-down arrow-icon"></span>
                    </button>
                </div>
            </div>

            <div class="section-table ui-gridbox-scrollable-titles mt-4">
                <div class="aff-table sched-detail-table">
                    <div class="sched-detail-days" v-bind:class="{ 'shift-min-width': showMore }">
                        <div class="aff-table-head" v-bind:class="{ 'show-shift-column': showMore, 'show-work-week-column': showWorkWeek }">
                            <div class="head-cell">Week</div>
                            <div class="head-cell">Day</div>
                            <div class="head-cell">Hours</div>
                            <div class="head-cell">Start</div>
                            <div class="head-cell">Finish</div>
                            <div class="head-cell break-column-width">
                                Unpaid Break
                            </div>
                            <div class="head-cell" v-show="showMore">Shift</div>
                            <div class="head-cell" v-show="showWorkWeek">Work Week</div>
                        </div>
                        <div class="aff-table-body">
                            <div v-for="d in detail.Days" :key="d.DayNumber" class="aff-table-body-row"
                                v-bind:class="{ 'show-shift-column': showMore, 'show-work-week-column': showWorkWeek }">
                                <div class="aff-table-body-cell">
                                    <span v-show="isFirstDayOfWeek(d.DayOfWeek)">{{ d.Week }}</span>
                                </div>
                                <div class="table-date-counter aff-table-body-cell">
                                    <span class="mr-2">{{
                                        d.DayNumber + 1
                                        }}</span>
                                    <span>{{ getDayOfWeek(d.DayOfWeek) }}</span>
                                </div>
                                <div class="aff-table-body-cell">
                                    <input type="number" step="any" min="0" max="24" v-model.number="d.Hours"
                                        class="caption sched-detail-table-input" name="Sche Hour" autocomplete="off"
                                        data-lpignore="true" :error="d.error" @keydown="
                                            numLimitKeyDown($event, true, false)
                                        " @change="checkHour(d)" :data-test="d.Hours" />
                                </div>
                                <div class="aff-table-body-cell">
                                    <input :value="
                                            $filters.reformatTime(d.StartTime)
                                        " autocomplete="off" data-lpignore="true"
                                        class="d-inline-flex ma-0 pa-0 caption sched-detail-table-input" @change="
                                            timeTextChanged(
                                                $event,
                                                d,
                                                'StartTime'
                                            )
                                        " @keydown="
                                            numLimitKeyDown($event, false, true)
                                        " @click:clear="fromDate = null" />
                                </div>
                                <div class="aff-table-body-cell">
                                    <input :value="
                                            $filters.reformatTime(d.FinishTime)
                                        " autocomplete="off" data-lpignore="true"
                                        class="d-inline-flex ma-0 pa-0 caption sched-detail-table-input" @change="
                                            timeTextChanged(
                                                $event,
                                                d,
                                                'FinishTime'
                                            )
                                        " @keydown="
                                            numLimitKeyDown($event, false, true)
                                        " @click:clear="fromDate = null" />
                                </div>
                                <div class="unpaid-break hide-arrow aff-table-body-cell" v-if="showWorkWeek">
                                    <!--                                    <input v-model="d.BreakHours"-->
                                    <input class="sched-detail-table-input" type="number" max="100"
                                        v-model.lazy="d.breakH" @change="calcBreakHour(d)" />
                                    <span>:</span>
                                    <input class="sched-detail-table-input" v-model.lazy="d.breakM"
                                        @change="calcBreakHour(d)" />
                                </div>
                                <div class="shift-select-td aff-table-body-cell" v-if="showMore">
                                    <custom-select class="caption" :options="shifts" :selected="d.ShiftId"
                                        @selectOption="selectedShift($event, d)">
                                    </custom-select>
                                    <!--                                <select class="caption" dense outlined hide-details-->
                                    <!--                                        :menu-props="{bottom: true, offsetY: true}"-->
                                    <!--                                        v-model="d.ShiftId"-->
                                    <!--                                        @change="hasChanges = true"-->
                                    <!--                                        :items="shifts" item-text="name" item-value="name"></select>-->
                                </div>
                                <div class="aff-table-body-cell" v-bind:style="{width: '100px'}">
                                    <custom-select :options="['start', 'end']" :selected="d.employeeWeek" @selectOption="selectedEmployeeWeek($event, d)">

                                    </custom-select>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div class="schedules-buttons">
                <button type="button" class="white--text grey caption text-capitalize mr-2 mt-2 small"
                    @click="toggleWorkWeek">
                    <span v-if="!showMore">Show Work Week</span>
                    <span v-if="showMore">Hide Work Week</span>
                </button>
                <button type="button" class="white--text grey caption text-capitalize mr-2 mt-2 small"
                    @click="toggleShift">
                    <span v-if="!showMore">Show Shift</span>
                    <span v-if="showMore">Hide Shift</span>
                </button>
                <button type="button" class="white--text red text-capitalize mr-2 mt-2 small" :disabled="hasChanges"
                    @click="deleteSchedule()" v-if="
                        !isNewSchedule &&
                        detail.EmpAllocations &&
                        detail.EmpAllocations.length === 0
                    ">
                    <span class="icon-trash mr-1"></span><span>Delete</span>
                </button>
                <button type="button" class="white--text red text-capitalize mr-2 mt-2 small" :disabled="hasChanges"
                    @click="archiveSchedule()" v-if="
                        !detail.Archive &&
                        !isNewSchedule &&
                        createType === 'team'
                    ">
                    <span class="icon-briefcase mr-1"></span>
                    <span>Archive</span>
                </button>
                <button type="button" class="white--text grey text-capitalize mr-2 mt-2 small"
                    :disabled="!hasChanges && !isNewSchedule" @click="cancelChanges()">
                    <span class="icon-cancel mr-1"></span><span>Cancel</span>
                </button>
                <button type="button" class="white--text text-capitalize mr-1 mt-2 green small" :disabled="!hasChanges"
                    @click="updateDetail()">
                    <span class="icon-save mr-1"></span><span>Save</span>
                </button>
            </div>
        </form>
        <date-picker ref="DatePickerRef" :top="dateModalTop" :leftInput="dateModalLeft"
            :date="detail.StartDate"></date-picker>
        <!--        <date-picker ref="DatePickerRef" @selectDate="detail.StartDate"></date-picker>-->
    </div>
</template>
<script lang="ts">
import Vue, { defineComponent } from "vue";
import moment from "moment";
import Remote from "@/remote/scheduleRequest";
import { emitter } from "@/main";
import Dialog from "@/components/dialog";
import DatePicker from "@/components/common/DatePicker.vue";
import { EmployeeGetters } from "@/store/employeeModule";
import AutoComplete from "@/components/common/AutoComplete.vue";
import CustomSelect from "@/components/common/CustomSelect.vue";

export default defineComponent({
    name: "schedule-detail",
    components: {
        AutoComplete,
        DatePicker,
        CustomSelect,
    },
    props: ["detail", "shiftTypeList", "readOnly"],
    data: function () {
        return {
            //
            createType: "team",
            employees: [],

            // todo: bring back name length rule
            // nameLengthRule: [v => v.length <= 49 || 'Max 50 characters'],
            week: 1,
            tablePadding: "0 0;",
            hasChanges: false,
            isDateToCleared: false,
            shifts: <any[]>[],
            showMore: true,
            showWorkWeek: true,
            initialEmpSched: null,

            startDateMenu: false,

            //
            dateModalTop: 0,
            dateModalLeft: 0,

            //  time picker
            timePickerTargetDay: <any>null,
            selectedTimeType: "Start", // Start Finish
        };
    },
    computed: {
        isNewSchedule() {
            let detail: any = this.detail;
            return detail.Id <= 0;
        },
    },
    // created: function () {
    // },

    mounted: function () {
        // this.loadShiftTypes();
        this.shifts = this.shiftTypeList;

        this.hasChanges = this.detail.Id == -1; //new schedules always has changes

        this.$emit("dataChanged", this.hasChanges); // todo wtf is components? check what does this line do !!!

        let employees = this.$store.getters[EmployeeGetters.getEmployees];

        employees = employees.filter((employee: any) => {
            return !this.$store.state.EmployeeModule.employeesWithPeronsalSche.includes(
                employee.employeeNo
            );
        });

        this.employees = employees.map((employee: any) => {
            employee.joinedName =
                employee.commonName + " " + employee.employeeNo;
            return employee;
        });
        //

        //Add missing 0 days
        this.remapDays();

        this.createType =
            this.detail.ScheduleType === 0 ||
            this.detail.ScheduleType === undefined
                ? "team"
                : "personal";
    },
    watch: {
        ["detail"](newVal, oldVal) {
            this.remapDays();
            this.createType =
                this.detail.ScheduleType === 0 ||
                this.detail.ScheduleType === undefined
                    ? "team"
                    : "personal";
            // todo: simplify this to create event
            if (newVal != oldVal && newVal.Id == -1) {
                this.detail.Days = [];
                this.createNewDays(1, 1);
            }
        },
    },
    methods: {
        selectedEmployeeWeek(selectedWeek: any, d: any) {
            console.log('selectedWeek', selectedWeek);
            d.employeeWeek = selectedWeek;
            this.hasChanges = true;
        },
        selectedShift(selectedShift: any, d: any) {
            d.ShiftId = selectedShift;
            this.hasChanges = true;
        },
        formSubmited($event: any) {
            $event.preventDefault();
            $event.stopPropagation();
            return false;
            //
        },
        increaseNumber() {
            this.detail.NoOfWeeks++;
            this.updateNoOfWeeks();
        },
        reduceNumber() {
            this.detail.NoOfWeeks--;
            this.updateNoOfWeeks();
        },
        toggleShift() {
            this.showMore = !this.showMore;
        },
        toggleWorkWeek() {
            this.showWorkWeek = !this.showWorkWeek;
        },
        // todo: fix calendar
        // getDay(date: any) {
        //     const daysOfWeek = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
        //
        //     let i = new Date(date).getDay(date)
        //     return daysOfWeek[i]
        // },
        remapDays() {
            var week = 0;
            var date = moment(this.detail.StartDate);
            var total = this.detail.NoOfWeeks * 7;

            var fullDays = new Array(total);

            for (var dayNum = 0; dayNum < total; dayNum++) {
                if (dayNum % 7 == 0) {
                    week++;
                }

                fullDays[dayNum] = {
                    DayNumber: dayNum,
                    DayOfWeek: date.day(),
                    Week: week,
                    Date: moment(date),
                    Hours: 0,
                    StartTime: null,
                    FinishTime: null,
                    ShiftId: null,
                    BreakHours: null,
                };

                date.add(1, "day");
            }

            this.showMore = false;
            this.showWorkWeek = true;
            this.detail.Days.map((day: any) => {
                if (day.StartTime)
                    fullDays[day.DayNumber].StartTime = moment(day.StartTime);
                if (day.FinishTime)
                    fullDays[day.DayNumber].FinishTime = moment(day.FinishTime);
                fullDays[day.DayNumber].Hours = day.Hours;
                fullDays[day.DayNumber].ShiftId = day.ShiftId;
                fullDays[day.DayNumber].BreakHours = day.BreakHours;

                let n = day.BreakHours;
                let hourU = Math.floor(n);
                let minU = n - hourU;

                if (hourU !== 0) {
                    fullDays[day.DayNumber].breakH = hourU;
                }

                if (minU > 0) {
                    fullDays[day.DayNumber].breakM = Math.round(minU * 60);
                }
            });
            this.detail.Days = fullDays;
            this.detail.StartDate = moment(this.detail.StartDate);
            this.hasChanges = false;
        },

        timeTextChanged(input: any, d: any, type: string) {
            let pickedTime: any;
            if (input.target.value.includes(":")) {
                pickedTime = moment(input.target.value, "HH:mm");
            } else {
                pickedTime = moment(input.target.value, "Hmm");
            }

            if (type === "FinishTime") {
                if (!pickedTime.isValid()) {
                    input.target.value = "InValid Time";
                    d.FinishTime = null;
                    return;
                }

                if (
                    d.FinishTime &&
                    pickedTime.hour() === d.FinishTime.hour() &&
                    pickedTime.minute() === d.FinishTime.minute()
                ) {
                    return;
                }

                if (input === "") {
                    d.FinishTime = null;
                    return;
                }

                d.FinishTime = pickedTime;
            } else {
                if (
                    d.StartTime &&
                    pickedTime.hour() === d.StartTime.hour() &&
                    pickedTime.minute() === d.StartTime.minute()
                ) {
                    return;
                }

                if (!pickedTime.isValid()) {
                    input.target.value = "InValid Time";
                    d.StartTime = null;
                    return;
                }

                if (input === "") {
                    d.StartTime = null;
                    return;
                }

                d.StartTime = pickedTime;
            }

            this.timePickerTargetDay = d;
            this.autoCalcTime(type);

            this.hasChanges = true;
            // this.timeModal = false;
        },

        autoCalcTime(type: any) {
            let targetDay = this.timePickerTargetDay;
            if (
                targetDay.FinishTime &&
                targetDay.FinishTime.format("HH:mm") !== "00:00" &&
                targetDay.StartTime &&
                targetDay.StartTime.format("HH:mm") !== "00:00" &&
                (!targetDay.Hours || targetDay.Hours === 0)
            ) {
                // auto calc hours
                // set year month day to same
                targetDay.FinishTime.year(targetDay.StartTime.year());
                targetDay.FinishTime.month(targetDay.StartTime.month());
                targetDay.FinishTime.day(targetDay.StartTime.day());
                let timeDiff = targetDay.FinishTime.startOf("minute").diff(
                    targetDay.StartTime.startOf("minute"),
                    "hours",
                    true
                );
                if (targetDay.BreakHours) {
                    timeDiff = timeDiff - targetDay.BreakHours;
                }
                targetDay.Hours = Math.round(timeDiff * 100) / 100;
            } else if (
                targetDay.FinishTime &&
                targetDay.Hours &&
                (!targetDay.StartTime ||
                    targetDay.StartTime.format("HH:mm") === "00:00")
            ) {
                // auto calc start time

                const hoursDiff = targetDay.BreakHours
                    ? targetDay.Hours + targetDay.BreakHours
                    : targetDay.Hours;
                targetDay.StartTime = targetDay.FinishTime.clone().subtract(
                    hoursDiff,
                    "hours"
                );
            } else if (
                targetDay.StartTime &&
                targetDay.Hours &&
                (!targetDay.FinishTime ||
                    targetDay.FinishTime.format("HH:mm") === "00:00")
            ) {
                // auto calc finish time

                const hoursDiff = targetDay.BreakHours
                    ? targetDay.Hours + targetDay.BreakHours
                    : targetDay.Hours;
                targetDay.FinishTime = targetDay.StartTime.clone().add(
                    hoursDiff,
                    "hours"
                );
            } else if (
                targetDay.FinishTime &&
                targetDay.FinishTime.format("HH:mm") !== "00:00" &&
                targetDay.StartTime &&
                targetDay.StartTime.format("HH:mm") !== "00:00" &&
                (targetDay.Hours || targetDay.Hours !== 0)
            ) {
                // otherwise all blank filled.
                const hoursDiff = targetDay.BreakHours
                    ? targetDay.Hours + targetDay.BreakHours
                    : targetDay.Hours;
                if (type === "StartTime") {
                    targetDay.FinishTime = targetDay.StartTime.clone().add(
                        hoursDiff,
                        "hours"
                    );
                } else if (type === "FinishTime") {
                    targetDay.StartTime = targetDay.FinishTime.clone().subtract(
                        hoursDiff,
                        "hours"
                    );
                }
            }
        },

        updateDateText(event: any, target: any) {
            this.startDateMenu = false;
            target.set(moment(event, "DD/MM/YYYY").toObject());
            // todo: add check for is correct format
            this.remapDays();
        },

        startDateDatePickerHandler(date: any) {
            if (typeof date === "string") date = moment(date);
            this.detail.StartDate = date;
            this.remapDays();
        },

        isFirstDayOfWeek: function (dayNo: any) {
            var startDate = moment(this.detail.StartDate);
            return dayNo == startDate.day();
        },
        // loadShiftTypes: function () {
        //     var list = this.shiftTypeList;
        //
        //     for (var i = 0; i < list.length; i++) {
        //         if (list[i].ShiftType !== undefined &&
        //             list[i].ShiftType !== null &&
        //             list[i].ShiftType.length <= 5) {
        //             var shiftType: any = {
        //                 name: list[i].ShiftType
        //             };
        //             this.shifts.push(shiftType);
        //         }
        //     }
        //
        //     this.shifts.sort(function (a, b) {
        //         var x = a.name.toLowerCase();
        //         var y = b.name.toLowerCase();
        //         if (x < y) {
        //             return -1;
        //         }
        //         if (x > y) {
        //             return 1;
        //         }
        //         return 0;
        //     });
        // },
        calcBreakHour(d: any) {
            if (d.breakH && d.breakM) {
                d.BreakHours =
                    Number(d.breakH) +
                    Math.round((Number(d.breakM) / 60) * 1000) / 1000;
            } else if (d.breakH) {
                d.BreakHours = Number(d.breakH);
            } else if (d.breakM) {
                d.BreakHours =
                    Math.round((Number(d.breakM) / 60) * 1000) / 1000;
            } else if (d.breakHours === 0 && d.breakM === 0) {
                d.BreakHours = 0;
            } else {
                // otherwise assume value deleted.
                d.BreakHours = 0;
            }

            // if other column present, calculate hours.
            if (
                d.StartTime &&
                d.StartTime.format("HH:mm") !== "00:00" &&
                d.FinishTime &&
                d.FinishTime.format("HH:mm") !== "00:00"
            ) {
                d.Hours =
                    d.FinishTime.startOf("minute").diff(
                        d.StartTime.startOf("minute"),
                        "hours",
                        true
                    ) - d.BreakHours;
            }
        },
        getDayOfWeek: function (dayNo: number) {
            if (dayNo < 0 || dayNo > 6) {
                console.error("day No not correct");
                return "unknown";
            }

            var days = ["Sun", "Mon", "Tue", "Wed", "Thur", "Fri", "Sat"];
            return days[dayNo];
        },

        checkHour(targetDay: any) {
            this.hasChanges = true;

            if (targetDay.Hours > 24) {
                Dialog.alert("You can only enter up to 24 hours.");
                targetDay.Hours = 24;
                return;
            }

            const hoursDiff = targetDay.BreakHours
                ? targetDay.Hours + targetDay.BreakHours
                : targetDay.Hours;

            if (
                targetDay.StartTime &&
                targetDay.Hours &&
                targetDay.FinishTime
            ) {
                targetDay.FinishTime = targetDay.StartTime.clone().add(
                    hoursDiff,
                    "hours"
                );
            } else if (targetDay.StartTime && targetDay.Hours) {
                targetDay.FinishTime = targetDay.StartTime.clone().add(
                    hoursDiff,
                    "hours"
                );
            } else if (targetDay.Hours && targetDay.FinishTime) {
                targetDay.StartTime = targetDay.FinishTime.clone().add(
                    hoursDiff,
                    "hours"
                );
            }
        },

        updateNoOfWeeks($event?: any) {
            let dayNum = this.detail.Days.length;
            let currentWeek = Math.floor(dayNum / 7);

            if (this.detail.NoOfWeeks > currentWeek) {
                this.createNewDays(currentWeek + 1, this.detail.NoOfWeeks);
            } else {
                let newLength = this.detail.NoOfWeeks * 7;
                this.detail.Days.splice(newLength);
            }
        },

        numLimitKeyDown: function (
            event: any,
            allowDecimal: boolean,
            allowColon: boolean
        ) {
            let allow = [8, 9, 13, 46, 16, 17]; // backspace, del, tab, shift, ctrl, dot
            allow.push(65, 67, 86, 88); // a, c, v, x

            if (allowDecimal) allow.push(190, 110); //.

            if (allowColon) allow.push(186);

            if (
                (event.keyCode >= 48 && event.keyCode <= 57) || //key board numbers
                (event.keyCode >= 96 && event.keyCode <= 105) || //key pad numbers
                allow.indexOf(event.keyCode) > -1
            ) {
                //various keys
                // all good!
            } else {
                event.preventDefault();
                event.stopPropagation();
            }
        },

        cancelChanges() {
            if (!this.hasChanges) {
                this.$emit("cancel");
                return;
            }

            let msg =
                "You haven't saved your changes.  <br /> Are you sure you want to cancel?";
            Dialog.checkChange({
                message: msg,
                confirmButtonText: "Yes",
                cancelButtonText: "No",
                onOk: () => {
                    this.hasChanges = false;
                    this.$emit("cancel");
                },
            });
        },

        // todo: use reload to clean the changes
        reloadDetail: function () {
            if (!this.hasChanges) return;
            // toodo: add has change check
            this.$emit("reload");
        },

        createNewDays: function (fromWeek: any, toWeek: any) {
            if (fromWeek > toWeek) return;

            var week = fromWeek;
            var dayNum = this.detail.Days.length;
            while (week <= toWeek) {
                var date = moment(this.detail.StartDate);
                date.add(dayNum, "day");
                this.detail.Days.push({
                    DayNumber: dayNum,
                    DayOfWeek: date.day(),
                    Week: week,
                    Date: date,
                    Hours: 0,
                    StartTime: null,
                    FinishTime: null,
                    ShiftId: null,
                    startDateModal: false,
                    endDateModal: false,
                });
                dayNum++;
                week = Math.floor(dayNum / 7) + 1;
            }
        },

        archiveSchedule: function () {
            if (this.hasChanges) {
                return;
            }

            if (this.detail.Archive) {
                Dialog.alert(
                    "Unable to archive schedule, it's already archived."
                );
            } else {
                Dialog.confirmOrCancel({
                    message:
                        "Are you sure you want to archive this work schedule?",
                }).then(() => {
                    this.detail.Archive = true;
                    this.doUpdateDetail();
                });
            }
        },

        deleteSchedule: function () {
            if (this.hasChanges) return;

            if (this.detail.EmpAllocations.length > 0) {
                Dialog.alert(
                    "Unable to delete schedule, it's currently allocated to employees"
                );
            } else {
                emitter.emit("dialog", {
                    message:
                        "Are you sure you want to permanently delete this work schedule?",
                    showButtons: true,
                    showCancel: true,
                    okColor: "red",
                    onOk: () => {
                        this.doDeleteSchedule();
                    },
                    onCancel: () => {},
                });
            }
        },

        doDeleteSchedule: function () {
            Dialog.loading();

            Remote.DeleteSchedule(this.detail.Id).then((res) => {
                this.$emit("schedule-deleted");
                Dialog.clear();
            });
        },

        updateDetail: function () {
            if (!this.hasChanges) return;

            if (!this.doUpdateValidation()) {
                return;
            }

            this.doUpdateDetail();
        },
        doUpdateValidation: function () {
            if (this.createType === "team") {
                if (
                    (!this.detail.Name && !this.detail.Description) ||
                    (this.detail.Name === "" && this.detail.Description === "")
                ) {
                    Dialog.alert(
                        "Schedule name cannot be blank. <br /> Schedule description cannot be blank."
                    );
                    return false;
                }

                if (!this.detail.Name || this.detail.Name === "") {
                    Dialog.alert("Schedule name cannot be blank.");
                    return false;
                }
            }

            if (!this.detail.Description || this.detail.Description === "") {
                Dialog.alert("Schedule description cannot be blank.");
                return false;
            }

            if (
                this.detail.Name !== undefined &&
                this.detail.Name !== null &&
                this.detail.Name.length > 50
            ) {
                Dialog.alert(
                    "Schedule names must be 50 characters or fewer (including spaces)."
                );
                return false;
            }

            if (
                this.detail.Description !== undefined &&
                this.detail.Description !== null &&
                this.detail.Description.length > 50
            ) {
                Dialog.alert(
                    "Schedule descriptions must be 50 characters or fewer (including spaces)."
                );
                return false;
            }

            return true;
        },
        doUpdateDetail: function () {
            var postData = {
                Id: this.detail.Id,
                NoOfWeeks: this.detail.NoOfWeeks,
                StartDate: moment(this.detail.StartDate).format("DD/MMM/YYYY"),
                Name:
                    this.createType === "team"
                        ? this.detail.Name
                        : "Personal_ " + this.detail.EmployeeNo,
                EmployeeNo: this.detail.EmployeeNo
                    ? this.detail.EmployeeNo
                    : null,
                ScheduleType: this.detail.EmployeeNo ? 1 : 0,
                IsPersonal: !!this.detail.EmployeeNo,
                Description: this.detail.Description,
                Archive: this.detail.Archive,
                Days: <any[]>[],
            };

            this.detail.Days.map(function (day: any) {
                if (
                    day.Hours > 0 ||
                    (day.ShiftId !== null && day.ShiftId !== "")
                ) {
                    if (day.ShiftId === "") day.ShiftId = null;
                    postData.Days.push({
                        DayNumber: day.DayNumber,
                        StartTime: day.StartTime
                            ? day.StartTime.format("HH:mm")
                            : null,
                        FinishTime: day.FinishTime
                            ? day.FinishTime.format("HH:mm")
                            : null,
                        Hours: day.Hours > 24 ? 24 : day.Hours,
                        ShiftId: day.ShiftId,
                        BreakHours: day.BreakHours
                            ? Number(day.BreakHours)
                            : null,
                    });
                }
            });

            Dialog.loading();
            Remote.PostSchedule(postData)
                .then((res) => {
                    if (res.Data) {
                        this.$emit("schedule-saved", res.Data);
                        Dialog.clear();
                    } else if (!res.Data && res.Messages && res.Messages.length > 0) {
                        if (res.Messages[0].Message === "That name is taken. Please use a different name.") {
                            Dialog.alert("This name is already in use. Please give your schedule a different name.");
                        } else {
                            Dialog.alert("An error occurred while saving the schedule.");
                        }
                    }
                })
                .catch((err) => {
                    Dialog.alert("An error occurred while saving the schedule.");
                })
        },
        formUpdated() {
            this.hasChanges = true;
        },

        async openDatePicker($event: any) {
            let element = $event.target.getClientRects()[0];
            this.dateModalTop = element.bottom;
            this.dateModalLeft = element.left;
            try {
                this.detail.StartDate = await (
                    this.$refs.DatePickerRef as any
                ).toggleShow(this.detail.StartDate);
            } catch (e) {
                e ? console.error(e) : null;
                return;
            }

            this.remapDays();
            this.hasChanges = true;
        },
        selectedEmployee(employee: any) {
            this.detail.EmployeeNo = employee.employeeNo;
            this.detail.Description = `${employee.commonName} ${employee.employeeNo} Personal Schedule`;
        },
    },
});
</script>

