<template>
    <div>
        <span>
            <div class="custom-select" tabindex="1" ref="selectRef">
                <div class="custom-select-selected">
                    <span>{{ getSelectedOptions() }}</span>
                </div>
                <div class="custom-select-items" v-bind:style="getLeft()">
                    <div
                        class="custom-select-item"
                        v-for="(option, index) of options"
                        :key="index"
                        @click="selectOptionAction(option)"
                    >
                        <div class="cell-config">
                            <input
                                type="checkbox"
                                v-model="option.isSelected"
                            />
                            <label class="ml-2">{{
                                option[targetString]
                            }}</label>
                        </div>
                    </div>
                </div>
            </div>
        </span>
    </div>
</template>

<script lang="ts">
import { defineComponent, ref } from "vue";

export default defineComponent({
    name: "CustomSelect",
    props: {
        selectOptionInput: {
            type: Array,
        },
        targetString: {
            type: String,
        },
        allowAll: {
            type: Boolean,
        },
        fixedLeft: {
            type: Boolean,
        },
    },
    data() {
        return {
            options: <any>[],
            left: 0,
            selected: [],
        };
    },
    mounted() {
        if ((this.$refs as any).selectRef.getClientRects()[0]) {
            this.left = (this.$refs as any).selectRef.getClientRects()[0].left;
        }

        this.options = this.selectOptionInput!;
        // console.log('options', this.options)

        let containSelected = false;
        // console.log('this.options.find((option: any) => option.isSelected)', this.options.find((option: any) => option.isSelected))
        if (this.options.find((option: any) => option.isSelected)) {
            containSelected = true;
        }

        if (this.allowAll) {
            const allOption = {
                commonName: "All",
                isAll: true,
                isSelected: !containSelected,
            };
            this.options.unshift(allOption);
        }

        this.options.find((val: any, index: number) => {
            if (index > 0) {
                if (!val.isSelected) {
                    return true;
                }
            }
        }) ? this.options[0].isSelected = false
            : (this.options[0].isSelected = true);
    },

    methods: {
        getSelectedOptions() {
            let selectedOptions = this.options.filter(
                (option: any) => option.isSelected
            );
            let finalOutput = "";
            selectedOptions.map((option: any, index: number) => {
                if (index > 0) {
                    finalOutput =
                        finalOutput + ", " + option[this.targetString as any];
                } else {
                    finalOutput = option[this.targetString as any];
                }
            });
            return finalOutput;
        },

        getLeft() {
            if (this.fixedLeft) {
                return { left: this.left + "px" };
            } else {
                return { position: "absolute" };
            }
        },
        selectOptionAction(option: any) {
            option.isSelected = !option.isSelected;
            if (option.isAll && option.isSelected == true) {
                this.options.forEach((val: any, index: number) => {
                    if (index > 0) {
                        val.isSelected = true;
                    }
                });
            } else if (option.isAll && option.isSelected === false) {
                this.options.forEach((val: any, index: number) => {
                    if (index > 0) {
                        val.isSelected = false;
                    }
                });
            } else if (!option.isAll && this.options[0].isAll) {
                // if all options are selected, select is all, otherwise deselect all
                this.options.find((val: any, index: number) => {
                    if (index > 0) {
                        if (!val.isSelected) {
                            return true;
                        }
                    }
                }) ? this.options[0].isSelected = false
                 : (this.options[0].isSelected = true);

            } else {
                //
            }
        },
    },
});
</script>

<style lang="scss" scoped>
.custom-select-selected:after {
    top: calc(50% - 3px);
    right: 10px;
}

.custom-select-selected {
    padding: 8px 20px 8px 10px;
    min-height: 35px;
    max-width: 141px;
    max-height: 35px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.custom-select-items {
    position: fixed;
    width: 150px;

    .custom-select-item {
        line-height: 1.2em;
        font-size: 12px;
    }
}

.custom-select-empty-selected {
    display: inline-block;
    width: 1px;
    height: 15px;
}

.cell-config {
    label {
        cursor: pointer;
    }
    input[type="checkbox"] {
        margin: 0;
    }
}

.vert-mid {
    vertical-align: middle;
}
</style>
