function GetManagedLeaves(params: any): Promise<Leave[]> {
    return axios.get(`GetManagedLeaves`, {
        params: params
    }).then(res => {
        if (typeof res.data === "string") {
            if (res.data === "") {
                res.data = []
                return res.data
            }
            return JSON.parse(res.data)
        }
        res.data.forEach((leave: any) => {
            leave.dateFrom = leave.datefrom; 
            leave.dateTo = leave.dateto 
        })
        return res.data
    })
}

