<template>
    <div class="schedules">
        <div class="sch-nav justify-space-between align-center body-2">
            <div class="sch-heading-text"></div>
            <div class="d-flex sch-toolbar align-center" v-if="user">
                <a tabindex="1" @click="toggleMobileMenu()" :class="[isOpen ? 'open' : '', 'mobile-menu']">
                    <span class="burger-menu"></span>
                </a>
                <ul class="sch-nav-options mr-1 pa-0">
                    <li class="nav-box mr-1 py-1 px-2 my-1 caption"
                        v-for="menu in authorizedMenus" @click="selectMenu(menu)" :class="{ selected: menu.selected }">
                        <div>{{ menu.display }}</div>
                    </li>
                </ul>
                <!--  todo: data-tooltip="Help" data-tooltip-dir="bottom,left"-->
                <a href="/help/index" class="sch-nav-help ss-help-link align-center mr-2"
                   v-tooltip.top="{ content: 'Help' }" target="_blank">
                    <span class="icon-help-round"></span>
                </a>
            </div>
        </div>
    </div>
</template>

<script lang="ts">
import { defineComponent, inject } from "vue";
import Remote from "@/remote/scheduleRequest";

declare global {
    interface Window {
        Affinity: any;
    }
}

export default defineComponent({
    name: "scheduleNavigation",
    data() {
        return {
            user: <any>null,
            menus: [
                {
                    text: "Work Schedule",
                    display: "My Schedules",
                    route: "/my-schedules",
                    selected: false,
                    default: true,
                },
                {
                    text: "Availability",
                    display: "My Availability",
                    route: "/Availability",
                    selected: false,
                    default: false,
                },
                {
                    text: "Employee Team",
                    display: "My Team",
                    route: "/my-team",
                    selected: false,
                    default: false,
                },
                {
                    text: "Flex Schedule",
                    display: "Flexi Schedules",
                    route: "/flex-schedule",
                    selected: false,
                    default: false,
                },
                {
                    text: "Schedule Management",
                    display: "Team Schedules",
                    route: "/team-schedules",
                    selected: false,
                    default: false,
                },
                {
                    text: "Employee Allocation",
                    display: "Employee Allocations",
                    route: "/employee-allocations",
                    selected: false,
                    default: false,
                },
                {
                    text: "Cost Report",
                    display: "Cost Analytics",
                    route: "/cost-reports",
                    selected: false,
                    default: false,
                },
            ],
            selectedMenu: <any>null,
            isOpen: false, // For mobile only
        };
    },
    computed: {
        userFullName() {
            // bug: can not use this.user in return, throw missing typing error.
            let user: any = this.user;
            return user ? `${user.FirstName} ${user.LastName}` : "";
        },
        authorizedMenus() {
            var filteredMenus = [];
            var accessibleMenus = this.user.AccessibleMenus;
            // accessibleMenus.push('Employee Team')

            // bug: can not use this.user in return, throw missing typing error.
            let menus: any[] = this.menus;
            for (var i = 0; i < this.menus.length; i++) {
                var currentMenuIteration = menus[i];
                if (accessibleMenus.indexOf(currentMenuIteration.text) != -1) {
                    filteredMenus.push(currentMenuIteration);
                }
            }
            return filteredMenus;
        },
    },
    methods: {
        selectMenu: function (menu: any) {
            // TODO: remove the following redirect when remove the costing in schedule.
            if (menu.route === '/cost-reports') {
                const costingApp = window.Affinity.config.Apps.find((app: any) => {
                    return app.Link?.Url?.split('.')[0].toLowerCase().includes('costing') && app.Link?.Enabled
                })
                 
                if (costingApp) {
                    window.location.href = costingApp.Link.Url;
                    return;
                }
            }


            this.$router.push(menu.route);
            this.isOpen = false;

            this.menus.forEach((m) => {
                m.selected = false;
            });
            menu.selected = true;
        },
        toggleMobileMenu: function () {
            this.isOpen = !this.isOpen;
        },
        isSelected: function (menuText: string) {
            return this.selectedMenu && this.selectedMenu.text == menuText;
        },
        assignAccessibleFeatures: function () {
            const manageCompanyConfig: any = inject("MANAGE_COMPANY_CONFIG");
            // PLEASE DO NOT MODIFY "GAReady" EVENT DISPATCHERS BELOW
            // If you MUST move or remove this, you MUST dispatch "GAReady" whenever this app has an authorised user with a valid companyNumber and employeeNumber.
            // This is vital for Affinity Management Analytics reporting.

            if (
                !window.Affinity ||
                (window.Affinity && !window.Affinity.user)
            ) {
                console.log("no affinity requesting authInfo");
                // call request to get user
                Remote.AuthGetInfo().then((res) => {
                    this.user = res;
                    try {
                        window.dispatchEvent(
                            new CustomEvent("GAReady", {
                                bubbles: true,
                                detail: {
                                    companyNumber: this.user.CompanyNo,
                                    employeeNumber: this.user.EmployeeNo,
                                    appVersion: "V2",
                                },
                            })
                        );
                    } catch (err) {
                        console.warn("GA tracking failed. Contact BenK.");
                    }

                    // console.log('set to true')
                    manageCompanyConfig.setAllowPersonal(
                        res.AccessibleFeatures.includes("Personal Schedule")
                    );
                    manageCompanyConfig.setAllowSundayFirst(
                        res.AccessibleFeatures.includes("Sunday First")
                    );
                });
            } else {
                console.log("loading user from Affinity lib");
                this.user = window.Affinity.user;

                try {
                    window.dispatchEvent(
                        new CustomEvent("GAReady", {
                            bubbles: true,
                            detail: {
                                companyNumber: this.user.CompanyNo,
                                employeeNumber: this.user.EmployeeNo,
                                appVersion: "V2",
                            },
                        })
                    );
                } catch (err) {
                    console.warn("GA tracking failed. Contact BenK.");
                }

                manageCompanyConfig.setAllowPersonal(
                    this.user.AccessibleFeatures.includes("Personal Schedule")
                );
                manageCompanyConfig.setAllowSundayFirst(
                    this.user.AccessibleFeatures.includes("Sunday First")
                );

                // todo: move this logic to router
                // let selected = this.menus.find((m: any) => {
                //     // console.log('m', m)
                //     if (m.route === "/flex-schedule") {
                //         return this.$route.path.indexOf(m.route) > -1;
                //     } else {
                //         return m.route === this.$route.path;
                //     }
                // });
                //
                // if (!selected) {
                //     let defaultMenu = this.menus.find((menu) => {
                //         return menu.default === true;
                //     });
                //     this.selectMenu(defaultMenu);
                // }
            }
        },
    },
    mounted: function () {
        this.assignAccessibleFeatures();
        document.addEventListener("DBWDomReady", (e) => {
            this.assignAccessibleFeatures();
        });
    },
});
</script>

<style lang="scss" scoped>
.sch-nav {
    display: flex;
    padding: 5px 0 5px 8px;

    @media only screen and (max-width: 768px) {
        display: block;
    }
}

.sch-nav-options {
    display: flex;
    @media only screen and (max-width: 600px) {
        display: block;
        width: 100%;
    }
}

.sch-nav-help {
    border-radius: 50%;
    width: 30px;
    height: 30px;
    text-decoration: none;
    font-size: 25px;
    vertical-align: middle;
    .icon-help-round {
        color: #2d72b3;
    }

    @media only screen and (max-width: 600px) {
        display: none;
    }
}

.sch-toolbar {
    position: relative;
    text-align: right;
    z-index: 10;
    @media screen and (max-width: 991px) {
        display: block;
    }
}

ul.sch-nav-options {
    @media screen and (min-width: 1281px) {
        li.nav-box {
            border-bottom: 1px solid #44b5ec;
            background-color: #2d72b3;
            color: white;
            list-style-type: none;
            cursor: pointer;
            margin: 0;
            padding: 8px 20px;
            // min-width: 125px;
            line-height: 24px;
            box-shadow: 0 5px 20px #00378033;
            border-bottom: 0;

            &:first-child {
                border-top-left-radius: 30px;
                border-bottom-left-radius: 30px;
            }

            &:last-child {
                border-top-right-radius: 30px;
                border-bottom-right-radius: 30px;
            }

            &.selected {
                background-color: #448ccf;
            }

            > div {
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
            }
        }
    }
    @media screen and (max-width: 1280px) {
        display: none;
        position: absolute;
        flex-flow: column;
        border-radius: 8px;
        background-color: #fff;
        top: 100%;
        right: 0;
        box-shadow: 0 5px 20px #00378033;
        padding: 8px 12px;
        min-width: 180px;

        > li {
            text-align: left;
            box-shadow: none;
            border-radius: 8px;
            padding: 8px;
        }

        &.selected {
            color: #fff;
        }
    }
}
.mobile-menu {
    @media screen and (min-width: 1281px) {
        display: none;
    }
    @media screen and (max-width: 1280px) {
        display: inline-block;
        padding: 6px;
        width: 36px;
        height: 32px;
        vertical-align: middle;

        > span {
            display: block;
            padding: 5px 0;
            border-top: 3px solid #7e7e7e;
            border-bottom: 3px solid #7e7e7e;

            &:before {
                content: " ";
                display: block;
                height: 3px;
                background-color: #7e7e7e;
            }
        }

        &.open ~ ul {
            display: flex;
        }
    }
}
.sch-heading-text {
    font-size: 20px;

    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

}
</style>
