<template>
    <Modal ref="ModalRef" @cancel="cancelModal" :modalIndex="modalIndex">
        <template v-slot:header>
            <div class="d-flex justify-center">
                <h2>
                    <b>Period Analysis</b>
                </h2>
            </div>
            <div class="d-flex justify-center mt-2">
                <h3>{{weekStart}} - {{weekEnd}}</h3>
            </div>
        </template>
        <template v-slot:body>
            <div class="mt-4" v-if="!data[0]">
                <h3>No Period Analysis Data</h3>
            </div>
            <div class="mt-4 full-width costAnalaysisBody" v-else>
                <div class="display-option">
                    <multi-select class="mt-4 " :selectOptionInput="displayOptions" :targetString="'description'"
                                  :fixed-left="false"></multi-select>
                </div>
                <div class="cost-grid mt-4">
                    <div class="day-arr cost-grid-heading"
                         v-bind:style="getGridTemplateStyle()">
                        <div class="table-cell">

                        </div>
                        <div class="table-cell" v-for="val in displayList">
                            {{val.heading}}
                        </div>
                    </div>
                    <div class="day-arr" v-for="(day, index) in data"
                        v-bind:style="getGridTemplateStyle()">
                        <div class="table-cell" v-if="day.day">
                            {{day.day}}
                        </div>
<!--                        <div class="table-cell" v-for="[key, value] of Object.entries(displayList)">-->
<!--                            <span>${{day[key]}} {{ key }}</span>-->
<!--                        </div>-->
                        <div class="table-cell" v-for="[key, value] of Object.entries(displayList)"
                             v-bind:class="getDataStyle(calculatedData[index], key, value)">
                            <span v-if="!value.isCalculated">$</span>
                            <span>{{day[key]}}</span>
                            <span v-if="value.isCalculated">%</span>
                        </div>
                    </div>
                    <div class="day-arr"
                         v-bind:style="getGridTemplateStyle()">
                        <div class="table-cell bold">
                            Total
                        </div>
                        <div class="table-cell bold" v-for="[key, val] of Object.entries(displayList)"
                             v-bind:class="getDataStyle(totalInNumber, key, val)">
                            <span v-if="!val.isCalculated">$</span>
                            <span>{{total[key]}}</span>
                            <span v-if="val.isCalculated">%</span>
                        </div>
                    </div>
                </div>
            </div>
        </template>
        <template v-slot:footer>
            <div class="mt-4">
                <button class="orange small white--text" @click="toggleShow">Close</button>
            </div>
        </template>

    </Modal>

</template>

<script lang="ts">
import 'chartjs-adapter-moment';

import Modal from "@/components/common/Modal.vue";

import {defineComponent, inject} from "vue";
import moment, {Moment} from "moment";
import Remote from "@/remote/scheduleRequest";
import {EmployeeGetters} from "@/store/employeeModule";
import Dialog from "@/components/dialog";
import MultiSelect from "@/components/common/MultiSelect.vue";
import {entries} from "lodash";
import currency from 'currency.js';
import { CompanyConfigInterface } from '@/plugins/companyConfig';

declare var Affinity: any

export interface CostDay {
    scheduled: number;
    budgeted: number;
    actual: number;
    sales: number;
    date: string;
    weekDay: string;
    scheduledVSBudgeted: number;
    scheduledVSSales: number;
    actualsVSScheduled: number;
    actualsVSBudgeted: number;
    actualsVSSales: number;
}

export interface CostDayConverted {
    scheduled: string;
    budgeted: string;
    actual: string;
    sales: string;
    date: string;
    weekDay: string;
    scheduledVSBudgeted: string;
    scheduledVSSales: string;
    actualsVSScheduled: string;
    actualsVSBudgeted: string;
    actualsVSSales: string;
}

export default defineComponent ({
    name: "CostAnalysisDialog",
    components: {
        Modal,
        MultiSelect,
    },
    props: {
        dayFocus: {
            type: Object as () => Moment
        },
        modalIndex: {
            type: Number
        }
    },
    data() {
        return {
            selectedDate: <Moment | null> null,
            data: <any[]>[],
            calculatedData: <any[]>[],
            total: <any>{},
            totalInNumber: <any>{},
            displayN: 6, // number of column to display,

            displayList: <any>{},
            sourceDisplayList: <any>{
                scheduled: {
                    heading: 'Schedule',
                    isDisplay: true,
                    isCalculated: false,
                },
                actual: {
                    heading: 'Actual',
                    isDisplay: false,
                    isCalculated: false,
                },
                budgeted: {
                    heading: 'Budget',
                    isDisplay: false,
                    isCalculated: false,
                },
                sales: {
                    heading: 'Sales',
                    isDisplay: false,
                    isCalculated: false,
                },
                scheduledVSBudgeted: {
                    heading: 'Schedule vs Budget',
                    isDisplay: false,
                    isCalculated: true,
                },
                actualsVSBudgeted: {
                    heading: 'Actual vs Budget',
                    isDisplay: false,
                    isCalculated: true,
                },
                actualsVSScheduled: {
                    heading: 'Actual vs Schedule',
                    isDisplay: false,
                    isCalculated: true,
                },
                scheduledVSSales: {
                    heading: 'Schedule vs Sales',
                    isDisplay: false,
                    isCalculated: true,
                },
                actualsVSSales: {
                    heading: 'Actual vs Sales',
                    isDisplay: false,
                    isCalculated: true,
                },
            },
          
            weekStart: '',
            weekEnd: '',
            displayOptions: [
                {
                    key: 'scheduled',
                    description: 'Schedule',
                    isSelected: true,
                },
                {
                    key: 'actual',
                    description: 'Actual',
                    isSelected: false,
                },
                {
                    key: 'budgeted',
                    description: 'Budget',
                    isSelected: true,
                },
                {
                    key: 'sales',
                    description: 'Sales',
                    isSelected: true,
                },
                {
                    key: 'scheduledVSBudgeted',
                    description: 'Schedule vs Budget',
                    isSelected: false,
                },
                {
                    key: 'actualsVSBudgeted',
                    description: 'Actual vs Budget',
                    isSelected: false,
                },
                {
                    key: 'actualsVSScheduled',
                    description: 'Actual vs Schedule',
                    isSelected: false,
                },
                {
                    key: 'scheduledVSSales',
                    description: 'Schedule vs Sales',
                    isSelected: false,
                },
                {
                    key: 'actualsVSSales',
                    description: 'Actual vs Sales',
                    isSelected: false,
                },
            ],
            manageCompanyConfig: <any>null,

        }
    },

    setup() {
        //
    },

    computed: {
        comparedValue(): any {
            let returnList = []
            for (let [key, value] of Object.entries(this.displayList)) {
                if ((value as any).isDisplay) {
                    returnList.push(value)
                }
            }
            return returnList
        }
    },
    mounted() {
        const localSetting = JSON.parse(localStorage.getItem('costAnalysisSetting_v1.3')!);
        this.manageCompanyConfig = inject('MANAGE_COMPANY_CONFIG') as CompanyConfigInterface;

        if (localSetting) {
            this.displayOptions = localSetting;
        }

    },
    watch: {
        displayOptions: {
            handler(val, oldVal) {
                this.displayList = {};
                for (const [key, value] of Object.entries(this.sourceDisplayList)) {
                    const found = val.find((v: any) => v.key === key);
                    if (found && found.isSelected) {
                        this.displayList[key] = value;
                        this.displayList[key].isDisplay = found.isSelected;
                    } else {
                        console.error('key not found', key)
                    }

                    // if (found) {

                    // }
                }
                localStorage.setItem('costAnalysisSetting_v1.3', JSON.stringify(val))
            },
            deep: true
        },
    },

    methods: {
        getDataStyle(day: any, key: any, value: any) {
            if (!value.isCalculated) {
                return;
            }

            if (key === 'scheduledVSBudgeted' || key === 'actualsVSBudgeted') {
                if (day[key] > 0) {
                    return 'positive-cell'
                } else {
                    return 'negative-cell'
                }
            }
        },

        getGridTemplateStyle() {
            this.calculateDisplayRow();
            if (this.displayN > 1) {
                return `grid-template-columns: 100px repeat( ${this.displayN} , 100px)`;
            } else {
                return `grid-template-columns: 100px repeat( ${this.displayN} , auto)`;
            }

        },
        open() {
            if (!this.dayFocus) {
                throw new Error('dayFocus is not set')
            }
            Object.keys(this.displayList).forEach((v: any) => {
                this.displayList[v].isDisplay = false
            })
            let weekStart
            let weekEnd
            if (this.manageCompanyConfig.getAllowSundayFirst()) {
                weekStart = this.dayFocus!.clone().startOf('week').format("DD/MMM/YYYY");
                this.weekStart = weekStart;
                weekEnd =  this.dayFocus!.clone().endOf('week').format("DD/MMM/YYYY");
                this.weekEnd = weekEnd;
            } else {
                weekStart = this.dayFocus!.clone().startOf('isoWeek').format("DD/MMM/YYYY");
                weekEnd =this.dayFocus.clone().endOf('isoWeek').format("DD/MMM/YYYY");
            }

        
            let employeeNumbers = this.$store.getters[EmployeeGetters.getFilteredEmployees].map((emp: any) => {
                return emp.employeeNo
            }) ;

            let data = {
                employeeNumbers: employeeNumbers,
                dateFrom: weekStart,
                dateTo: weekEnd
            }

            Dialog.loading()

            for (const [key, value] of Object.entries(this.sourceDisplayList)) {
                const found: any = this.displayOptions.find((v: any) => v.key === key);
                if (found && found.isSelected === true) {
                    this.displayList[key] = value;
                    this.displayList[key].isDisplay = found.isSelected;
                } else {
                    console.error('key not found', key)
                }

                // if (found) {

                // }
            }

            Remote.GetCostAnalysis(data).then(res => {

                if (!res[0]) {
                    this.data = [];
                    this.total = [];
                    (this.$refs.ModalRef as any).toggleShow();
                    Dialog.clear();
                    return;
                }

                let costDays = res.map((element: any) => {
                    return Object.fromEntries(
                        Object.entries(element).map(([k,v]) => [k.charAt(0).toLowerCase() + k.slice(1), v])
                    );
                })

                // this.calculateDisplayRow(costDays);
                this.calculatedData = [];

                this.data = costDays.map((costDay: CostDay) => {
                    let newDay: CostDay = {...costDay};
                    let convertedDays: CostDayConverted = {} as any;
                    newDay.weekDay = moment(costDay.date).format('dddd');

                    // newDay.sales = 10000costDay.sales * 100) / 100;

                    if (this.sourceDisplayList.scheduledVSBudgeted) {
                        newDay.scheduledVSBudgeted = this.calculateRatioPercentage(costDay.scheduled,costDay.budgeted)
                    }

                    if (this.sourceDisplayList.actualsVSBudgeted) {
                        newDay.actualsVSBudgeted = this.calculateRatioPercentage(costDay.actual,costDay.budgeted)
                    }

                    if (this.sourceDisplayList.actualsVSScheduled) {
                        newDay.actualsVSScheduled = this.calculateDiffPercentage(costDay.actual,costDay.scheduled)
                    }
                    if (this.sourceDisplayList.actualsVSSales) {
                        newDay.actualsVSSales = this.calculateRatioPercentage(costDay.actual,costDay.sales)
                    }

                    if (this.sourceDisplayList.scheduledVSSales) {
                        newDay.scheduledVSSales = this.calculateRatioPercentage(costDay.scheduled,costDay.sales)
                    }

                    for (let [key, val] of Object.entries(newDay)) {
                        if (Number.isNaN(val)) {
                            (newDay as any)[key] = 0;
                        }
                        if (key === 'day' || key === 'dayNumber') {
                            convertedDays[key as keyof typeof costDay] = val;
                        } else {
                            convertedDays[key as keyof typeof costDay] = currency(newDay[key as keyof typeof costDay]).format({symbol: '' });
                        }

                    }
                    this.calculatedData.push(newDay)

                    return convertedDays;
                })

                this.calculateTotal();

                Dialog.clear();
                (this.$refs.ModalRef as any).toggleShow();

            }).catch(err => {
                this.data = [];
                this.total = [];
            })
        },

        calculateTotal() {
            const keyList = []
            // let totalNumbers: any = {};
            this.totalInNumber = {};

            for (const [key, value] of Object.entries(this.sourceDisplayList)) {
                // if (!(value as any).isCalculated && (value as any).isDisplay) {
                keyList.push(key)
                // }
            }

            if (keyList.length === 1) {
                this.totalInNumber[keyList[0]] = Math.round(this.calculatedData[0][keyList[0]] * 100) / 100;
                return;
            }

            for (let k of keyList) {
                this.totalInNumber[k] = 0;
                for (let data of this.calculatedData) {
                    if (!data[k]) {
                        data[k] = 0;
                    }
                    this.totalInNumber[k] =  this.totalInNumber[k] + data[k];
                }
            }

            if (this.sourceDisplayList.scheduledVSBudgeted) {
                this.totalInNumber.scheduledVSBudgeted = this.calculateRatioPercentage(this.totalInNumber.scheduled,this.totalInNumber.budgeted)
            }
            if (this.sourceDisplayList.actualsVSBudgeted) {
                this.totalInNumber.actualsVSBudgeted = this.calculateRatioPercentage(this.totalInNumber.actual,this.totalInNumber.budgeted)
            }
            if (this.sourceDisplayList.actualsVSScheduled) {
                this.totalInNumber.actualsVSScheduled = this.calculateDiffPercentage(this.totalInNumber.actual,this.totalInNumber.scheduled)
            }
            if (this.sourceDisplayList.actualsVSSales) {
                this.totalInNumber.actualsVSSales = this.calculateRatioPercentage(this.totalInNumber.actual,this.totalInNumber.sales)
            }
            if (this.sourceDisplayList.scheduledVSSales) {
                this.totalInNumber.scheduledVSSales = this.calculateRatioPercentage(this.totalInNumber.scheduled,this.totalInNumber.sales)
            }

            for (let [key, val] of Object.entries(this.totalInNumber)) {
                if (Number.isNaN(val)) {
                    this.totalInNumber[key] = 0;
                }
                this.total[key as any] = currency(this.totalInNumber[key]).format({symbol: '' });

            }
        },

        calculateDiffPercentage(numerator: number, denominator: number) {
            if (!denominator) {
                return 0;
            }
            return (numerator - denominator) / denominator * 100
        },

        calculateRatioPercentage(numerator: number, denominator: number) {
            if (!denominator) {
                return 0;
            }
            return numerator / denominator * 100
        },

        calculateDisplayRow() {
            // console.log('day', days)
            // let displayDef = 1;
            this.displayN = this.displayOptions.filter((option: any) => option.isSelected ).length
        },

        toggleShow() {
            this.data = [];
            this.total = [];
            this.calculatedData = [];
            (this.$refs.ModalRef as any).toggleShow();
        },


        cancelModal() {
            this.data = [];
            this.total = [];
            this.calculatedData = [];
            // (this.$refs.ModalRef as any).toggleShow();
        }
    },
})
</script>

<style scoped lang="scss">
@use '../../style/colors';
$border-color: #00378033;

.costAnalaysisBody {
    overflow: auto;
    min-height: 260px;
}

.grid-background {
    background: $border-color;
}

.cost-grid {
    display: grid;
    //grid-template-rows: repeat(auto-fill, 60px);
    //grid-template-rows: min-content;
    grid-gap: 1px;
    width: fit-content;
    border: 1px solid $border-color;
    background: $border-color;
}

.day-arr {
    display: grid;
    width: 100%;
    grid-template-columns: 100px repeat(6, 80px);
    //grid-template-rows: min-content;
    //grid-template-rows: repeat(auto-fit, 60px);
    grid-gap: 1px;
}

.table-cell {
    padding: 10px 5px;
    text-align: center;
    background:white;
}

.positive-cell {
    color: red;
}

.negative-cell {
    color: #558115;
}

:deep(.custom-select) {
    width: 220px;
    .custom-select-selected {
        max-width: 220px;
        width: 100%;
    }

    .custom-select-items {
        width: 220px;
    }

}

</style>