<template>
    <div class="day-cell-wrapper">
        <div class="day-info" v-if="!isEmployee">
            <div class="display-day" v-bind:class="{'team-color-bg': scheDay.scheduleType === 'Team',
                'flexi-color-bg': scheDay.scheduleType === 'Flex',
                'personal-color-bg': scheDay.scheduleType === 'Personal'}" @click.prevent="clickDayCell">
                <!--        <div class="display-day">-->
                <!--            <div class="schedule-type-icon">-->
                <!--                <span v-if="scheDay.scheduleType === 'Team'" class="team-color">T</span>-->
                <!--                <span v-else-if="scheDay.scheduleType === 'Flex'" class="flexi-color">F</span>-->
                <!--                <span v-else-if="scheDay.scheduleType === 'Personal'" class="personal-color">P</span>-->
                <!--            </div>-->
                <div v-if="!scheDay.startTime && !scheDay.endTime
                    && !scheDay.hours && scheDay.allowCreateOrEdit && checkMobileShowCreate()
                    && allowCreate" class="create-day" v-bind:class="{'create-day-show': !isMobileG()}">
                    <button class="blue small" @click="createSche(scheDay)">
                        <span class="white">Create</span>
                    </button>
                </div>
                <div v-else-if="scheDay.startTime || scheDay.endTime || scheDay.hours"  class="full-width">
                    <div class="full-width">
                        <div v-if="(scheDay.startTime || scheDay.endTime)">
                            <div>{{ scheDay.startTime }} - {{ scheDay.endTime }}</div>
                        </div>
                        <div v-if="shouldDisplayHours(scheDay)">
                            <div>Net Hours: {{Math.round(scheDay.hours * 100) / 100}} </div>
                        </div>
                        <div v-if="cellSettings.showBreakLength.value && scheDay.breakLength && !isEmployee">
                            <div>Break Length: {{Math.round(scheDay.breakLength * 100) / 100}}</div>
                        </div>
                        <div v-if="cellSettings.showShiftId.value && scheDay.shiftId && !isEmployee">
                            <div>Shift ID: {{scheDay.shiftId}}</div>
                        </div>
                        <div class="notes-ellips full-width" v-if="cellSettings.showNotes.value && scheDay.notes">
                            <div  v-tooltip.right="{
                                    content: scheDay.notes,
                                    placement: 'top',
                                    triggers: ['click'],
                                    autoHide: true
                                }">Notes: {{ scheDay.notes }}</div>
                        </div>
                    </div>

                    <button class="detail-button blue small mt-2"
                        v-bind:class="{'detail-button-auto-hide': !isMobileG()}"
                        v-if="scheDay.allowCreateOrEdit && !scheDay.isPaid && checkMobileShowEdit() && allowEdit"
                        @click="moreDetail(scheDay)">
                        <span class="white caption">Edit</span>
                    </button>

                </div>
                <div v-if="scheDay.leaveUnit && !isEmployee" class="leave-unit mt-2">
                    <span v-if="scheDay.leaveUnit.calcUnit === 'D'">Leave: {{scheDay.leaveUnit.daysAppliedFor}}
                        day</span>
                    <span v-else>Leave: {{scheDay.leaveUnit.hoursAppliedFor}} hours</span>
                </div>
                <div v-else-if="scheDay.leaveUnit" class="leave-unit mt-2">
                    <span>Leave</span>
                </div>

                <div v-if="scheDay.unAvailability && !isEmployee" class="leave-unit mt-2">
                    <div>Unavailable</div>
                    <span>{{scheDay.unAvailability.startTime}} - {{scheDay.unAvailability.endTime}}</span>
                    <div>{{scheDay.unAvailability.reason}}</div>
                </div>

            </div>
        </div>
        <div class="day-info full-width" v-if="isEmployee">
            <div class="display-day" v-bind:class="{'team-color-bg': scheDay.scheduleType === 'Team',
                'flexi-color-bg': scheDay.scheduleType === 'Flex',
                'personal-color-bg': scheDay.scheduleType === 'Personal'}" v-if="scheDay.publishStatus === 'published'"
                @click.prevent="clickDayCell">

                <div v-if="scheDay.startTime || scheDay.endTime || scheDay.hours" class="full-width">
                    <div class="full-width">
                        <div v-if="(scheDay.startTime || scheDay.endTime)">
                            <div>{{ scheDay.startTime }} - {{ scheDay.endTime }}</div>
                        </div>
                        <div v-if="shouldDisplayHours(scheDay)">
                            <div>Net Hours: {{Math.round(scheDay.hours * 100) / 100}} </div>
                        </div>
                        <div class="notes-ellips full-width" v-if="scheDay.notes">
                            <div v-tooltip.right="{
                                    content: scheDay.notes,
                                    placement: 'top',
                                    triggers: ['click'],
                                    autoHide: true
                                }">Notes: {{ scheDay.notes }}</div>
                        </div>
                    </div>
                </div>
            </div>
            <div v-if="scheDay.leaveUnit" class="leave-unit mt-2">
                <span>Leave</span>
            </div>
        </div>
    </div>

</template>

<script lang="ts">
import {ScheduleDay} from "@/components/FlexSchedule/FlexScheduleInterface";
import {computed, defineComponent, PropType, reactive} from "vue";
import {FlexScheduleCellSetting, FlexScheduleMutation, ScheduleDayInView} from "@/store/flexScheduleModule";
import {useStore} from "vuex";
import {LookupActions, ShiftType} from "@/store/lookupModule";
import _ from "lodash";
import {isMobile} from "@/components/common/script";

export default defineComponent({
    name: "DayCell",

    props: {
        scheDay: {
            type: Object as PropType<ScheduleDay>
        },
        employeeNo: {
            type: Number
        },
        employeeName: {
            type: String
        },
        allowEdit: {
            type: Boolean
        },
        allowCreate: {
            type: Boolean
        },
        isEmployee: {
            type: Boolean
        }
    },

    data() {
        return {
            mobileShowCreate: false,
            mobileShowEdit: false,
        }
    },

    setup() {
        const store = useStore()

        const cellSettings: FlexScheduleCellSetting = store.state.FlexSchedule.flexScheduleCellSetting

        const shiftOptions = computed(() => {
            return store.state.LookUp.shifts
        })

        const positionOptions = computed(() => {
            return store.state.LookUp.positions
        })

        // const selectedShift = computed((shiftId: string) => {
        //     return shiftOptions.value.find((option: ShiftType) => {
        //         return option.shiftType === shiftId
        //     })
        // })


        return {
            cellSettings,
            shiftOptions,
            positionOptions,
            // selectedShift
        }
    },
    methods: {
        moreDetail(day: ScheduleDay) {
            this.$store.dispatch(LookupActions.getPositions, this.employeeNo)
            const result = this.$store.state.FlexSchedule.flexScheduleRemoteData.find((data: any) => {
                return data.date === day.date && data.employeeNo === this.employeeNo
            })

            if (!result) {
                console.error('can not find selected schedule')
                // todo: add correct error message
                return;
            }

            //console.log('result', result)

            this.$store.commit(FlexScheduleMutation.setScheduleDay, result)
            this.$emit('openEditDialog', true)
        },

        createSche(scheDay: ScheduleDay) {
            // this.$store.commit(FlexScheduleMutation.setTargetEmployee)
            this.$store.dispatch(LookupActions.getPositions, this.employeeNo)
            let scheDayInView: any = _.clone(scheDay)
            scheDayInView.employeeNo = this.employeeNo;
            scheDayInView.employeeName = this.employeeName;
            this.$store.commit(FlexScheduleMutation.createNewScheduleDay, scheDayInView)
            this.$emit('openEditDialog', false)
        },
        shouldDisplayHours(scheDay: any) {
            if (!this.cellSettings.showHours.value) {
                return false;
            }

            if ( (scheDay.startTime || scheDay.endTime) && scheDay.hours === 0 ) {
                return true;
            }
            return !!scheDay.hours
        },

        isMobileG() {
            return isMobile();
        },

        clickDayCell() {
            this.mobileShowCreate = !this.mobileShowCreate;
            this.mobileShowEdit = !this.mobileShowEdit
        },

        checkMobileShowEdit() {
            if (isMobile()) {
                return this.mobileShowEdit
            } else {
                return true;
            }
        },

        checkMobileShowCreate() {
            if (isMobile()) {
                return this.mobileShowCreate
            } else {
                return true;
            }
        }
    }
})
</script>

<style scoped lang="scss">

@use '../../style/colors';

.schedule-type-icon {
    position: absolute;
    top: 5px;
    right: 5px;
}

.day-cell-wrapper {
    width: 100%;
    height: 100%;
    overflow: hidden;
}
.day-info {
    //min-width: 100px;
    //min-height: 30px;
    width: 100%;
    height: 100%;
}

.display-day {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    height: 100%;
    padding: 10px;


    .create-day {
        transition: opacity linear 0.15s
    }

    .create-day-show {
        display: block;
        opacity: 0;
    }

    .detail-button {
        position: absolute;
        bottom: 0;
        right: 0;
        transition: opacity linear 0.15s
    }

    &:hover .create-day-show {
        opacity: 1;
    }

    &:hover .detail-button {
        display: block;
    }
}

.detail-button-auto-hide {
    display: none;
}

.leave-unit {
    width: 100%;
    padding: 2px 5px;
    background-color: #E0E0E0
}

.time-input {
    max-width: 120px;
}

.team-color {
    color:  darken(#cfefc6, 40%);
}
.flexi-color {
    color: darken(#d1f0ff, 40%);
}
.personal-color {
    color: darken(#FFF6CC, 40%);
}

.team-color-bg {
    background: #cfefc6;
}
.flexi-color-bg {
    //background: #d1f0ff;
}
.personal-color-bg {
    background: #d1f0ff;
}

.notes-ellips {
    max-width: 210px;
    &:hover {
        cursor: pointer;
        color: colors.$aff-dark-blue;
        text-decoration: underline;
    }
}

.v-popper--has-tooltip {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    max-width: 210px;
}
</style>