<template>
    <div class="cell-config-list multi-column-wrapper">
        <div>
            <div class="custom-select" tabindex="1" >
                <div class="custom-select-selected">
                    Add Extra Columns
                </div>
                <div class="custom-select-items">
                    <div class="custom-select-item"
                        v-for="(option, index) of selectionOption" :key="index">
                        <div class="cell-config">
                            <label>{{option.optionName}}</label>
                            <input type="checkbox" v-model="option.selected" @click="checkOptions(option);" />
                            <template v-if="option.selected">
                                <Icon :icon="$icons.chevronUp" height="15"
                                      v-if="option.sort === 'Ascend' || option.sort === ''"
                                      v-bind:class="{'sort-selected': option.sort === 'Ascend'}"
                                      @click="selectSort(option)">
                                </Icon>
                                <Icon :icon="$icons.chevronDown" height="15"
                                      v-if="option.sort === 'Descend'"
                                      v-bind:class="{'sort-selected': option.sort === 'Descend'}"
                                      @click="selectSort(option)">
                                </Icon>
                            </template>

                        </div>
                    </div>

                </div>
            </div>
        </div>
    </div>
</template>

<script lang="ts">
import {defineComponent, ref, watch} from "vue";
import {useStore} from "vuex";
import {FlexScheduleCellSetting, SortOptions} from "@/store/flexScheduleModule";
import {nextTick} from "@vue/runtime-core";
import {Icon} from "@iconify/vue";

export default defineComponent({
    name: "FlexScheduleMultiColumn",
    components: {
        Icon
    },
    props: {
        isEmployee: {
            type: Boolean,
            default: false
        }
    },

    setup(props) {
        const store = useStore()
        const open = ref(false)
        const optionSelected = ref([])

        const selectionOption = ref(<SortOptions[]>[]);
        
        if (props.isEmployee) {
            selectionOption.value = store.state.FlexSchedule.employeeSelectableColumn;
        } else {
            selectionOption.value = store.state.FlexSchedule.selectableColumn
        }

        return {open,  optionSelected, selectionOption}
    },
    methods: {
        checkOptions(option: any) {
            // todo: trigger when user click on the row
            option.selected = !option.selected

            if (!option.selected ) {
                option.sort = "";
            }
            this.$emit('optionChanged')
            localStorage.setItem(
                "flexScheduleSelectableColumn",
                JSON.stringify(this.selectionOption)
            );
        },
        selectSort(selectedOption: any) {
            this.selectionOption.forEach(option => {
                if (option.optionName !== selectedOption.optionName) option.sort = '';
            })

            switch (selectedOption.sort) {
                case 'Ascend':
                    selectedOption.sort = 'Descend'
                    break;
                case 'Descend':
                    selectedOption.sort = ''
                    break;
                case '':
                    selectedOption.sort = 'Ascend'
                    break;
                default:
                    console.error('select sort error state')

            }
            localStorage.setItem(
                "flexScheduleSelectableColumn",
                JSON.stringify(this.selectionOption)
            );
            this.$emit('sortChanged')
        }
    }

})
</script>

<style scoped lang="scss">
@use '../../style/_colors';
@use "../../style/layouts";

.custom-select:focus-within {
    .custom-select-selected {
        border: none;
        border-radius: 0;
        border-bottom: 1px solid #c8d1db;
    }

    .custom-select-selected:after {
        transform: rotate(180deg);
        top: 8px;
    }
}

.custom-select-selected {
    background: transparent;
    border: none;
    border-radius: 0;
    border-bottom: 1px solid #c8d1db;
}

.custom-select-items {
    width: fit-content;
}

.custom-select-selected:after {
    top: 15px;
    right: 0;
}

.cell-config {
    display: grid;
    grid-template-columns: 103px 20px;
    padding: 0;
    align-items: center;
}

.cell-config-list {
    font-size: 14px;

    @media screen and (max-width: layouts.$xs-screen) {
        font-size: 12px;
    }
}

.multi-column-wrapper {
    @media screen and (min-width: 768px) {
        width: 200px;

        :deep(.custom-select-items) {
            width: 220px;
        }
    }
    @media screen and (max-width: 767px) {
        width: 200px;

        :deep(.custom-select-items) {
            width: 220px;
        }
    }

    :deep(.custom-select-selected) {
        border-bottom: 1px solid #c8d1db;
    }

    :deep(.cell-config) {
        display: grid;
        grid-template-columns: 160px 20px 15px;
        align-items: center;
        padding: 0;

        label {
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }
}

.sort-selected {
    color: colors.$aff-dark-blue
}

</style>