.sched-empSched-table {
    overflow-y: auto;
    @media screen and (min-width: 640px) {
        overflow-x: hidden;
    }

    marg table {
        @media only screen and (max-width: 600px) {
            width: 100%;
        }
    }
}

.shift-select-td {
    position: relative;
    width: 100px;
}

.days-width {
    display: inline-block;
    width: 14px;
}

.full-width {
    width: 100%;
}

.schedule-textarea {
    width: 100%;
    height: 54px;
}

body.newui .dashboard .pagecontent .mt-4 {
    margin-top: 16px;
}

.aff-table-head,
.aff-table-body-row {
    display: grid;
    grid-template-columns: 40px 75px 50px 50px 50px 80px 110px 1fr;
}

.aff-table-body-row {
    justify-content: start;
}

.aff-table-body-row {
    padding-top: 2px;
}
.head-cell {
    padding: 4px 8px;
    white-space: nowrap;
    font-weight: bold;
    font-size: 10px;

    &:first-child {
        padding-left: 0;
    }
    &:last-child {
        padding-right: 0;
    }
}

.aff-table-body {
    max-height: 240px;
}

.aff-table-body-cell {
    padding: 4px 8px;
    border-top: 1px solid #c8d1db;

    &:first-child {
        padding-left: 0;
    }
    &:last-child {
        padding-right: 0;
    }
}

.date-input-warpper {
    width: 50%;
    max-width: 198px;
    margin-right: 24px;

    &:last-child {
        margin-right: 0;
    }
}
:deep(.custom-select) {
    width: 100px;
    .custom-select-items {
        width: 100px;
    }
}
</style>
