import moment from "moment";
import {Moment} from "moment";

export const DiALOG_DATE_FORMAT = 'DD/MM/YYYY';
export const DATE_FOR_REQUEST_FORMAT = 'MM/DD/YYYY';

export function getDialogDate(calendarFocusDate: Moment, dayInWeek: number, sundayFirst: boolean): any {
    if (window.innerWidth < 768) {
       return calendarFocusDate.format(DiALOG_DATE_FORMAT)
    }
    if (dayInWeek === 0 && !sundayFirst) {
        return calendarFocusDate.clone().day(dayInWeek).add(7, 'day').format(DiALOG_DATE_FORMAT)
    }  else {
        return calendarFocusDate.clone().day(dayInWeek).format(DiALOG_DATE_FORMAT)
    }
}

export function getDialogDateForRequest(calendarFocusDate: Moment, dayInWeek: number, sundayFirst: boolean): any {
    if (window.innerWidth < 768) {
        return calendarFocusDate.format(DATE_FOR_REQUEST_FORMAT)
    }
    if (dayInWeek === 0 && !sundayFirst) {
        return calendarFocusDate.clone().day(dayInWeek).add(7, 'day').format(DATE_FOR_REQUEST_FORMAT)
    } else {
        return calendarFocusDate.clone().day(dayInWeek).format(DATE_FOR_REQUEST_FORMAT)
    }
    
}

export function getStartAndEndTimeWithDate(
    date: string,
    startTime: string,
    endTime: string
) {
    console.log('start time', startTime)
    if (!startTime && endTime) {
        const dateM = moment(date, DATE_FOR_REQUEST_FORMAT);
        const endTimeToday = moment(endTime, "HH:mm");
        const endTimeM = dateM
                        .clone()
                        .hours(endTimeToday.hour())
                        .minutes(endTimeToday.minutes());
        return [null, endTimeM];
    } else if (startTime && !endTime) {
        const dateM = moment(date, DATE_FOR_REQUEST_FORMAT);
        const startTimeToday = moment(startTime, "HH:mm");
        const startTimeM = dateM
                            .clone()
                            .hours(startTimeToday.hour())
                            .minutes(startTimeToday.minutes());
        return [startTimeM, null];
    } else if (!startTime && !endTime) {
        return [null, null];
    } else {
        const dateM = moment(date, DATE_FOR_REQUEST_FORMAT);
        const startTimeToday = moment(startTime, "HH:mm");
        const endTimeToday = moment(endTime, "HH:mm");
        const startTimeM = dateM
            .clone()
            .hours(startTimeToday.hour())
            .minutes(startTimeToday.minutes());
        const endTimeM = dateM
            .clone()
            .hours(endTimeToday.hour())
            .minutes(endTimeToday.minutes());

        let startEndDiffTrue = endTimeM.diff(startTimeM, "hours", true);

        if (startEndDiffTrue <= 0) {
            endTimeM.add(1, "day");
        }
        
        return [
            startTimeM.format("YYYY-MM-DDTHH:mm:ss"),
            endTimeM.format("YYYY-MM-DDTHH:mm:ss"),
        ];
    }
}