<template>
    <div class="dbw-box">
        <p class="mt-4">I am <b>unavailable</b> during these times:</p>
        <div class="available-table">
            <div>
                <div class="service-grid">
                    <div class="grid-heading">Day of Week</div>
                    <div class="grid-heading">Start Time</div>
                    <div class="grid-heading">End Time</div>
                    <div class="grid-heading">Repeat</div>
                    <div class="grid-heading">Starts On</div>
                    <div class="grid-heading">Ends On</div>
                    <div class="grid-heading">Actions</div>
                    <div class="grid-heading">Reason</div>
                    <template v-for="data of gridData">
                        <div class="grid-item">{{ data.dayOfWeek }}</div>
                        <div class="grid-item">{{ data.startTime }}</div>
                        <div class="grid-item">{{ data.endTime }}</div>
                        <div class="grid-item">{{ data.frequencyType }}</div>
                        <div class="grid-item">{{ data.startDate }}</div>
                        <div class="grid-item">{{ data.endDate }}</div>
                        <div class="grid-item">
                            <button
                                class="green white--text xsmall"
                                @click="openEditDialog(true, data)"
                            >
                                Edit
                            </button>
                            <button
                                class="grey white--text xsmall ml-2"
                                @click="confirmDelete(data)"
                            >
                                Delete
                            </button>
                        </div>
                        <div class="grid-item">{{ data.reason }}</div>
                    </template>
                </div>
            </div>
        </div>
        <div class="d-flex justify-end mt-4 mb-4">
            <button
                class="green small white--text"
                @click="openEditDialog(false)"
            >
                Create
            </button>
        </div>
    </div>
    <!--    popups  -->
    <edit-availability
        ref="EditAvailabilityRef"
        :isEdit="isEditingDialog"
        @reload="loadAvailability"
        :modalIndex="1999"
    ></edit-availability>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref } from "vue";
import EditAvailability from "@/components/Availability/EditAvailability.vue";
import Remote from "@/remote/scheduleRequest";
import moment from "moment";
import AvailabilityLookup from "@/components/Availability/AvailabilityLookUp";
import Dialog from "@/components/dialog";
declare var Affinity: any;

export default defineComponent({
    components: { EditAvailability },
    data() {
        return {
            // gridData: [{
            //         dayOfWeek: 'Monday',
            //         startTime: '9:00',
            //         endTime: '12:00',
            //         recurrence: 'Every week',
            //         startDay: '12/02/2021',
            //         endDay: '02/05/2021',
            //         reason: 'UniversityClasses'
            //     }, {
            //         dayOfWeek: 'Monday',
            //         startTime: '9:00',
            //         endTime: '12:00',
            //         recurrence: 'Every week',
            //         startDay: '12/02/2021',
            //         endDay: '02/05/2021',
            //         reason: 'UniversityClasses'
            //     }
            // ],
        };
    },

    setup() {
        const isEditingDialog = ref(false);
        const EditAvailabilityRef =
            ref<InstanceType<typeof EditAvailability>>();
        const gridData = ref([]);
        const selectedAva = ref();

        function openEditDialog(isEdit: boolean, selectedData?: any) {
            isEditingDialog.value = isEdit;
            EditAvailabilityRef.value!.openEditDialog(selectedData);
        }

        function loadAvailability() {
            Remote.GetAvailabilities(Affinity.user.EmployeeNo).then((res) => {
                let availabilities = res.map((availability: any) => {
                    return Object.fromEntries(
                        Object.entries(availability).map(([k, v]) => [
                            k.charAt(0).toLowerCase() + k.slice(1),
                            v,
                        ])
                    );
                });

                gridData.value = availabilities.map((availabilities: any) => {
                    availabilities.startDate = moment(
                        availabilities.startDate
                    ).format("DD/MM/YYYY");
                    if (availabilities.endDate) {
                        availabilities.endDate = moment(
                            availabilities.endDate
                        ).format("DD/MM/YYYY");
                    }
                    availabilities.dayOfWeek =
                        AvailabilityLookup.findFullDaysByShort(
                            availabilities.dayOfWeek
                        );

                    return availabilities;
                });
            });
        }

        onMounted(() => {
            loadAvailability();
        });

        // openEditDialog()
        return {
            gridData,
            openEditDialog,
            selectedAva,
            loadAvailability,
            isEditingDialog,
            EditAvailabilityRef,
        };
    },

    mounted() {},

    methods: {
        confirmDelete(data: any) {
            Dialog.confirmOrCancel({
                message:
                    "Are you sure you want to delete this period of unavailability?",
            }).then(() => {
                this.deleteAva(data.id);
            });
        },

        deleteAva(id: number) {
            Remote.DeleteAvailabilities(id).then(
                (res) => {
                    this.loadAvailability();
                },
                (err) => {
                    Dialog.alert(
                        "Something went wrong. Delete Availability failed"
                    );
                }
            );
        },
    },
});
</script>

<style scoped lang="scss">
$border-color: #00378033;

.available-table {
    margin: 24px -24px;

    > div {
        overflow-x: auto;
        padding: 16px 12px;
    }
}
.service-grid {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    //border: 1px solid #fff;
    grid-gap: 1px;
    border-collapse: collapse;
}

.grid-heading {
    padding: 4px 8px;
    text-align: center;
    box-sizing: border-box;
    border-collapse: collapse;
}

.grid-item {
    padding: 4px 8px;
    text-align: center;
    box-sizing: border-box;
    border-collapse: collapse;
    display: flex;
    justify-content: center;
    align-items: center;
    border-top: 1px solid #c8d1db;
}
</style>
