<template>
    <div class="mt-4">
        <div class="left-box">
            <div class="background-legend">
                <div>
                    <div class="flexi-legend"></div>
                    <div class="legend-label">Flexi Schedule</div>
                </div>
                <div>
                    <div class="team-legend"></div>
                    <div class="legend-label">Team Schedule</div>
                </div>
                <div>
                    <div class="personal-legend"></div>
                    <div class="legend-label">Personal Schedule</div>
                </div>
            </div>
        </div>
        <div class="weekDetail d-flex align-center justify-center">
            <span class="arrow-height mr-4" @click="getLastDate()">
                <Icon
                    :icon="$icons.chevronLeft"
                    height="30"
                    :style="{ color: '#0000008a' }"
                ></Icon>
            </span>
            <span @click="chooseCalendarDate"
                >{{ $filters.ordinalSuffix(dayFocus.week()) }} Week,
                {{ dayFocus.format("DD/MM/YYYY") }}</span
            >
            <span class="arrow-height ml-4" @click="getNextDate()">
                <Icon
                    :icon="$icons.chevronRight"
                    height="30"
                    :style="{ color: '#0000008a' }"
                ></Icon>
            </span>
        </div>
    </div>

    <div class="mt-4" style="overflow: auto">
        <div class="flex-sche-table">
            <div class="table-head">
                <div class="schedule-head-grid">
                    <div class="table-cell">Employee</div>
                    <div class="table-cell">Schedule</div>
                </div>
            </div>
            <perfect-scrollbar>
                <div class="table-body">
                    <div
                        class="schedules-arr"
                        v-for="empNo in scheduleWeekMap.keys()">
                        <div
                            class="content-cell content-employee-name">
                            <span>{{scheduleWeekMap.get(empNo).employeeName}}</span>
                        </div>
                        <div class="content-cell">
                            <day-cell
                                :scheDay="scheduleWeekMap.get(empNo).sche"
                                :employeeNo="scheduleWeekMap.get(empNo).employeeNo"
                                :employeeName="scheduleWeekMap.get(empNo).employeeName"
                                @openEditDialog="openEditDialog"
                            >
                            </day-cell>
                        </div>
                    </div>
                </div>
            </perfect-scrollbar>
        </div>
    </div>
</template>

<script lang="ts">
// mobile version for EmployeeTeam

import { defineComponent, onMounted, ref } from "vue";
import moment, { Moment } from "moment";
import { ScheduleDay } from "../FlexSchedule/FlexScheduleInterface";
import DatePicker from "@/components/common/DatePicker.vue";
import EditFlexSchedule from "@/components/FlexSchedule/EditFlexSchedule.vue";
import DayCell from "@/components/FlexSchedule/DayCell.vue";
import {
    FlexScheduleAction,
    FlexScheduleMutation,
} from "@/store/flexScheduleModule";
import FlexScheduleCellControl from "@/components/FlexSchedule/FlexScheduleCellControl.vue";
import TimePicker from "@/components/common/timePicker/TimePicker.vue";

import { Icon } from "@iconify/vue";
import AutoComplete from "@/components/common/AutoComplete.vue";
import Remote from "@/remote/scheduleRequest";
import { LookupActions } from "@/store/lookupModule";
import Dialog from "@/components/dialog";
import {
    EmployeeActions,
    EmployeeGetters,
    ManagedEmployee,
} from "@/store/employeeModule";
import {
    CFRemoteData,
    FlexScheduleRequest,
    FlexScheduleRequestCap,
} from "@/components/FlexSchedule/FlexScheduleInterface";
import SetupGridData from "@/components/FlexSchedule/setupGridData";

import { useRouter } from "vue-router";
import { isMobileV2 } from "@/components/common/script";

declare var Affinity: any;

export enum WeeklyPublishStatus {
    empty = "",
    draft = "draft",
    published = "published",
    partial = "partial",
}

export default defineComponent({
    name: "FlexScheduleM",
    components: {
        DatePicker,
        TimePicker,
        EditFlexSchedule,
        DayCell,
        FlexScheduleCellControl,
        Icon,
        AutoComplete,
    },
    data() {
        return {
            dayFocus: moment(),
            weekDays: <any[]>[], // calendar week days name
            // scheduleWeek: <any[]>[], // list of data per week
            datePickerTop: 0,
            datePickerLeft: 0,
            employeeList: <any>[],
            scheduleWeekMap: new Map(),
            weekStatus: WeeklyPublishStatus.empty,

            //
            showControl: false,
            extraEmpDetail: [],
            isAdmin: false,
            hasFuturePaidDate: false,
        };
    },
    setup() {
        const router = useRouter();

        console.log('window.innerWidth', window.innerWidth)
        if (!isMobileV2()) {
            router.push({
                name: "EmployeeTeamSchedule",
            });
        }

        return {
           
        };
    },
    async mounted() {
        this.$store.dispatch(
            LookupActions.getPositions,
            Affinity.user.EmployeeNo
        );
        this.$store.dispatch(LookupActions.getShifts);
        this.$store.dispatch(LookupActions.getCostElement);
        this.$store.dispatch(LookupActions.getWorkTypes);

        this.loadDate();

        Remote.GetDefaultWorkTypes().then((res) => {
            // note default worktypes is a array
            this.$store.state.FlexSchedule.defaultWorkTypes = res;
        });

        if (Affinity.user.EmployeeNo >= 5000000) {
            this.isAdmin = true;
        }
    },

    methods: {
        showCopy() {
            if (this.hasFuturePaidDate) {
                return false;
            }
            return moment().diff(this.dayFocus.endOf("week"), "day") <= 0;
        },
        reloadAfterEdit() {
            this.loadDate();
        },

        getCurrentWeekStatus() {
            if (
                this.weekStatus === WeeklyPublishStatus.empty ||
                this.weekStatus === WeeklyPublishStatus.draft
            ) {
                return "Draft";
            } else if (this.weekStatus === WeeklyPublishStatus.partial) {
                return "Partially Published";
            } else if (this.weekStatus === WeeklyPublishStatus.published) {
                return "Published";
            }

            return this.weekStatus;
        },

        async loadDate() {
            this.weekStatus = WeeklyPublishStatus.empty;
            this.showControl = false;
            this.hasFuturePaidDate = false;

            let param = {
                managerEmployeeNo: Affinity.user.EmployeeNo,
                dateFrom: this.dayFocus.format("YYYY-MM-DD"), // dateFrom: '2020-01-01',
                dateTo: this.dayFocus.format("YYYY-MM-DD"), // dateTo: '2021-01-01',
            };

            Dialog.loading();
            let [dateData, employeeList] = await Promise.all([
                this.$store.dispatch(FlexScheduleAction.loadFlexiSchedules, {
                    startDate: this.dayFocus,
                    endDate: this.dayFocus,
                    employeeNo: Affinity.user.EmployeeNo
                }),
                this.$store
                    .dispatch(
                        EmployeeActions.getEmployees,
                        Affinity.user.EmployeeNo
                    )
                    .then((res: any) => {
                        return res.map((data: any) => {
                            return {
                                employeeName: data.commonName,
                                employeeNo: data.employeeNo,
                                stdHoursPerWeek: data.stdHoursPerWeek,
                                positionCode: data.positionCode,
                            };
                        });
                    }),
            ]).finally(() => {
                Dialog.clear();
            });
            Dialog.clear();

            let gridDataSet: Map<string, any> =
                SetupGridData.prepareGridDataMobile(
                    this.dayFocus,
                    employeeList,
                    dateData,
                );
            this.scheduleWeekMap = gridDataSet;
        },

        async chooseCalendarDate($event: any) {
            let element = $event.target.getClientRects()[0];
            this.datePickerTop = element.bottom;
            this.datePickerLeft = element.left;
            let datePickerResult;
            try {
                datePickerResult = await (
                    this.$refs.DatePickerRef as any
                ).toggleShow(this.dayFocus);
            } catch (e) {
                return;
            }
            await this.selectDate(datePickerResult);
        },

        async selectDate(date: Moment) {
            this.dayFocus = date;
            this.$store.state.FlexSchedule.calendarFocusDate = date;
            await this.loadDate();
        },
        getSelectedWeek(selectedDate: any): { momentDay: Moment; day: string }[] {
            var weekStart = selectedDate.clone().startOf("week");
            var weekEnd = selectedDate.clone().endOf("week");
            let weekDays = [];
            // this.weekDays = [];

            for (let i = 0; i <= 6; i++) {
                let momentDay = moment(weekStart).add(i, "days");
                weekDays.push({
                    momentDay: momentDay,
                    day: momentDay.format("ddd, MMM Do"),
                });
            }

            return weekDays;
        },

        getLastDate() {
            this.dayFocus = this.dayFocus.clone().subtract(1, "days");
            this.$store.state.FlexSchedule.calendarFocusDate = this.dayFocus;
            this.loadDate();
        },

        getNextDate() {
            this.dayFocus = this.dayFocus.clone().add(1, "days");
            this.$store.state.FlexSchedule.calendarFocusDate = this.dayFocus;
            this.loadDate();
        },

        moreDetail(day: ScheduleDay) {
            this.$store.commit(FlexScheduleMutation.setScheduleDay, day);
        },

        getTotalHour(sche: ScheduleDay[]) {
            let totalHours = 0;
            for (let day of sche) {
                if (day.hours) {
                    totalHours += day.hours;
                }
            }
            return totalHours.toFixed(2);
        },        
    },
});
</script>

<style scoped lang="scss">
