    display: grid;
    width: 100%;
    //grid-template-columns: 12% 12% 12% 12% 12% 12% 12% 12%;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1px;
    height: 23px;
    border-bottom: 1px solid $border-color;
}

.weekDetail {
    font-size: 16px;
    font-weight: bold;
    .arrow-height {
        height: 30px;
        margin-top: 1px;
        cursor: pointer;
    }

    @media screen and (max-width: layouts.$xs-screen) {
        margin-top: 24px;
        font-size: 14px;
    }
}

.table-body {
    display: grid;
    grid-gap: 1px;
