}

.flex-sche-table {
    border-collapse: collapse;
}

.content-cell {
    display: grid;
    justify-items: center;
    align-items: center;
    background: white;
    text-align: center;
    min-height: 70px;
}

.table-head {
    //background: linear-gradient(to bottom, #fcfcfc 0%, #efefef 49%, #e2e2e2 51%, #d6d6d6 100%);
    //padding: 3px 5px 3px 5px;
    height: 23px;
}

.ps {
    max-height: calc(100vh - 390px);
    overflow: auto;
}

.status-section {
    position: relative;
}

.inline-block-middle {
    display: inline-block;
    vertical-align: middle;
}

.content-employee-name {
    position: relative;
}

.left-box {
    flex: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.right-box {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.background-legend {
    display: inline-flex;

    > div {
        white-space: nowrap;
        margin-right: 12px;
    }

    @media screen and (max-width: 420px) {
        flex-wrap: wrap;
        font-size: 14px;
        > div {
            width: 50%;
            margin-right: 0;

            &:last-child {
                margin-top: 12px;
            }
        }
    }
}

.legend-label {
    display: inline-block;
    vertical-align: middle;
    margin-left: 4px;
}

.flexi-legend,
.team-legend,
.personal-legend {
    display: inline-block;
    width: 10px;
    padding: 5px;
    border: 1px solid $border-color;
    vertical-align: middle;
    margin-right: 8px;
}

.flexi-legend {
    background: white;
}

.team-legend {
    background: #cfefc6;
}

.personal-legend {
    background: #d1f0ff;
}
</style>
