<template>
    <div class="mt-4" v-if="!data[0]">
        <h3>No data found.</h3>
    </div>
    <div class="mt-4 costAnalaysisBody" v-else>
        <div class="d-flex align-center">
            <span>Select Data</span>
            <multi-select
                class="ml-2"
                :selectOptionInput="selectableOption"
                :targetString="'value'"
                @selectOption="selectChartOption">
            </multi-select>
        </div>
        <div class="cost-grid mt-4">
            <div class="cost-grid-heading" :style="headingStyle">
                <div class="table-cell"></div>
                <div class="table-cell" v-for="(val, index) in heading" :key="index">
                    {{val}}
                </div>
            </div>
            <div class="cost-grid-second-heading" :style="secondHeadingStyle">
                <div class="table-cell"></div>
                <div class="table-cell" v-for="(val, index) in secondHeading" :key="index">
                    {{val}}
                </div>
            </div>
            <perfect-scrollbar>
                <div class="day-arr" v-for="(day, index) in data" :key="index" :style="secondHeadingStyle">
                    <span class="day-cell" v-for="(val, index) in day" :key="index">
                        <div class="table-cell" v-if="val !== null && val !== undefined">
                            {{val}}
                        </div>
                    </span>

                </div>
                <div class="day-arr" :style="secondHeadingStyle">
                    <span class="day-cell" v-for="(val, index) in totals" :key="index">
                        <div class="table-cell bold" v-if="val !== null && val !== undefined">
                            {{val}}
                        </div>
                    </span>

                </div>
            </perfect-scrollbar>
        </div>
        <div class="main-canvas-wrapper" v-show="currentEmployeeList!.length <= 20">
            
            <div class="myChart-wrapper" v-bind:style="{'min-height': 600 + 'px'}">
                <canvas class="main-canvas" ref="myChartRef" id="payDetailChart" colours="[ { fillColor: '#ffff00' }, { fillColor: '#0066ff' } ]" ></canvas>
            </div>
        </div>
        <div class="mt-4" v-show="currentEmployeeList!.length > 20">Graphs can only show up to 20 employees.</div>

    </div>
    
</template>

<script lang="ts">
import 'chartjs-adapter-moment';

import Modal from "@/components/common/Modal.vue";

import {defineComponent, Suspense, ref, watch, onMounted, nextTick, onUnmounted} from "vue";
import moment, {Moment} from "moment";
import Remote from "@/remote/scheduleRequest";
import {EmployeeGetters} from "@/store/employeeModule";
import Dialog from "@/components/dialog";
import {entries} from "lodash";
import currency from 'currency.js';
import Chart from "chart.js/auto/auto.mjs";
import {
    employeePayDetailData, employeePayDetailDataValue,
    getDateMockEmployeeUnit,
    getDateMockEmployeeValue
} from "@/components/Analysis/AnalysisMockData";
import {nameOfDays} from "@/components/common/script";
import CustomSelect from "@/components/common/CustomSelect.vue";
import {AnalysisActions} from "@/store/analysisModule";
import MultiSelect from "@/components/common/MultiSelect.vue";
import { useStore } from 'vuex';
import {PayDetailViewCSVHeader, UnitDropDown} from "@/components/Analysis/preset";
import _ from 'lodash';

let MyChart = <any>null;

declare var Affinity: any

interface Option {
    key: string;
    value: string;
    isSelected: boolean;
}


enum DataKeys {
    Z1Scheduled01NormalHours = "z1Scheduled 01 - Normal Hours",
    Z1Scheduled02TimeAndHalf = "z1Scheduled 02 - Time & Half",
    Z1Scheduled03Double = "z1Scheduled 03 - Double",
    Z2Actual01NormalHours = "z2Actual 01 - Normal Hours",
    Z2Actual02TimeAndHalf = "z2Actual 02 - Time & Half",
    Z2Actual03Double = "z2Actual 03 - Double",
    Z3Variance01NormalHours = "z3Variance 01 - Normal Hours",
    Z3Variance02TimeAndHalf = "z3Variance 02 - Time & Half",
    Z3Variance03 = "z3Variance 03 - Double",
}

export default defineComponent ({
    name: "PayDetailView",
    components: {
        Dialog,
        CustomSelect,
        Modal,
        MultiSelect,
    },
    props: {
        dayFocus: {
            type: Object
        },
        fromDate: {
            type: Object,
        },
        toDate: {
            type: Object,
        },
        selectedUnit: {
            type: String,
        },
        employeeList: {
            type: Array
        },
        modalIndex: {
            type: Number
        }
    },

    setup(props) {
        const store = useStore();
        const selectedDate = ref<Moment | null>(null);
        const sourceData = ref<any[]>([]);
        const data = ref<any[]>([]);
        const graphData = ref<any[]>([]);
        const dataHours = ref<any[]>([]);
        const dataUnits = ref<any[]>([]);
        const unitOptions = ref<string[]>(['hours', 'unit']);
        const selectableOption = ref<Option[]>([
            {
                key: 'normalHours',
                value: 'Ordinary',
                isSelected: true
            }, 
            {
                key: 'timeAndHalf',
                value: 'Time & Half',
                isSelected: true
            }, 
            {
                key: 'double',
                value: 'Double',
                isSelected: true
            }
        ]);
        const calculatedData = ref<any[]>([]);
        
        const displayN = ref<number>(6);
        const displayList = ref<any>({});
        const heading = ref<string[]>(['Schedule', 'Actual', 'Variance']);
        const secondHeading = ref<string[]>(['Ordinary',	'Time & Half', 'Double', 'Ordinary', 'Time & Half', 'Double', 'Ordinary', 'Time & Half', 'Double']);
        const weekStart = ref<string>('');
        const weekEnd = ref<string>('');
        const fromDate = ref();
        const toDate = ref();
        const noDataError = ref(false);
        const headingStyle = ref({"grid-template-columns": `100px repeat(3, 242px)`});
        const secondHeadingStyle = ref({"grid-template-columns": `repeat(3, 80px)`});
        const unitFormat = ref();
        const tableUnit = ref(props.selectedUnit);
        const sourceTotal = ref();
        const totals = ref();
        const currentEmployeeList = ref(props.employeeList);

        onMounted(() => {
        // load initial data
            console.log(' pay detailed view mounted')
            
            nextTick(() => {
                setGraphData();
            })
        });

        onUnmounted(() => {
            if (!MyChart) return;
            MyChart.destroy();
            MyChart = null;
        });

        watch(
            selectableOption, async (selectedUnit) => {
                await selectChartOption();
            }, { deep: true }
        );

        loadDataBySelection();

        async function loadDataBySelection() {
            Dialog.loading();
            let weekStart = props.fromDate!.clone().format("YYYY-MM-DD");
            let weekEnd = props.toDate!.clone().format("YYYY-MM-DD");
            tableUnit.value = props.selectedUnit;
            currentEmployeeList.value = props.employeeList;
            if (tableUnit.value === UnitDropDown.value) {
                unitFormat.value = {symbol: '$', precision: 2}
            } else {
                unitFormat.value = {symbol: '', precision: 2}
            }

            const res = await store.dispatch(AnalysisActions.fetchPayDetailViewData, {
                weekStart,
                weekEnd,
                employeeNumbers: props.employeeList!.filter((employeeNumber: any) => employeeNumber !== null && employeeNumber !== undefined),
                selectedUnit: props.selectedUnit?.toLowerCase(),
                pageNumber: 1,
                pageSize: 1000,
                payCodes: ["01", "02", "03", "04", "05"],
            });

            if (!res) {
                return;
            }

            graphData.value = res.response.map((data: any) => [
                data.CommonName,
                data[DataKeys.Z1Scheduled01NormalHours],
                data[DataKeys.Z1Scheduled02TimeAndHalf],
                data[DataKeys.Z1Scheduled03Double],
                data[DataKeys.Z2Actual01NormalHours],
                data[DataKeys.Z2Actual02TimeAndHalf],
                data[DataKeys.Z2Actual03Double],
                data[DataKeys.Z3Variance01NormalHours],
                data[DataKeys.Z3Variance02TimeAndHalf],
                data[DataKeys.Z3Variance03]
            ]);

            data.value = res.response.map((data: any) => [
                data.CommonName,
                currency(data[DataKeys.Z1Scheduled01NormalHours], {precision: 2}).format(unitFormat.value),
                currency(data[DataKeys.Z1Scheduled02TimeAndHalf], {precision: 2}).format(unitFormat.value),
                currency(data[DataKeys.Z1Scheduled03Double], {precision: 2}).format(unitFormat.value),
                currency(data[DataKeys.Z2Actual01NormalHours], {precision: 2}).format(unitFormat.value),
                currency(data[DataKeys.Z2Actual02TimeAndHalf], {precision: 2}).format(unitFormat.value),
                currency(data[DataKeys.Z2Actual03Double], {precision: 2}).format(unitFormat.value),
                currency(data[DataKeys.Z3Variance01NormalHours], {precision: 2}).format(unitFormat.value),
                currency(data[DataKeys.Z3Variance02TimeAndHalf], {precision: 2}).format(unitFormat.value),
                currency(data[DataKeys.Z3Variance03], {precision: 2}).format(unitFormat.value)
            ]);

            sourceData.value = _.clone(res.response)
            const totalArray = graphData.value.reduce((acc, curr) => {
                for (let i = 1; i < curr.length; i++) {
                    acc[i - 1] += curr[i];
                }
                return acc;
            }, new Array(10).fill(0));
            sourceTotal.value = totalArray;
            console.log('sourceTotal', sourceTotal.value)
            totals.value = totalArray.map((total: any) => {
                return currency(total, {precision: 2}).format(unitFormat.value)
            });
            totals.value.unshift('Total')
            totals.value.pop();
            let selectedOption: any[] = selectableOption.value.filter((option: Option) => {
               return option.isSelected === true
            })

            getHeadingStyle(selectedOption.length);
            getSecondHeadingStyle(selectedOption.length);
            nextTick(() => {
                selectChartOption()
                setGraphData();
                Dialog.clear();
            })
        }

        function selectChartOption() {
            let newSecondHeadingArray = ['Ordinary','Time & Half', 'Double', 'Ordinary', 'Time & Half', 'Double', 'Ordinary', 'Time & Half', 'Double'];
            selectableOption.value.map((selectedOption: Option) => {
                if (!selectedOption.isSelected && selectedOption.value) {
                    newSecondHeadingArray = newSecondHeadingArray.filter(option => selectedOption.value !== option)
                }
            })
            
            secondHeading.value = newSecondHeadingArray

            data.value = sourceData.value.map((data: any) => {
                let newData = []
                newData.push(data.CommonName)
                if (selectableOption.value[0].isSelected) {
                    newData.push(currency(data[DataKeys.Z1Scheduled01NormalHours], {precision: 2}).format(unitFormat.value))
                }

                if (selectableOption.value[1].isSelected) {
                    newData.push(currency(data[DataKeys.Z1Scheduled02TimeAndHalf], {precision: 2}).format(unitFormat.value))
                }

                if (selectableOption.value[2].isSelected) {
                    newData.push(currency(data[DataKeys.Z1Scheduled03Double], {precision: 2}).format(unitFormat.value))
                }

                if (selectableOption.value[0].isSelected) {
                    newData.push(currency(data[DataKeys.Z2Actual01NormalHours], {precision: 2}).format(unitFormat.value))
                }

                if (selectableOption.value[1].isSelected) {
                    newData.push(currency(data[DataKeys.Z2Actual02TimeAndHalf], {precision: 2}).format(unitFormat.value))
                }

                if (selectableOption.value[2].isSelected) {
                    newData.push(currency(data[DataKeys.Z2Actual03Double], {precision: 2}).format(unitFormat.value))
                }

                if (selectableOption.value[0].isSelected) {
                    newData.push(currency(data[DataKeys.Z3Variance01NormalHours], {precision: 2}).format(unitFormat.value))
                }

                if (selectableOption.value[1].isSelected) {
                    newData.push(currency(data[DataKeys.Z3Variance02TimeAndHalf], {precision: 2}).format(unitFormat.value))
                }

                if (selectableOption.value[2].isSelected) {
                    newData.push(currency(data[DataKeys.Z3Variance03], {precision: 2}).format(unitFormat.value))
                }

                return newData
            })

            let newTotal: any[] = [];
            newTotal.push('Total')
            if (selectableOption.value[0].isSelected) {
                newTotal.push(currency(sourceTotal.value[0], {precision: 2}).format(unitFormat.value))
            }

            if (selectableOption.value[1].isSelected) {
                newTotal.push(currency(sourceTotal.value[1], {precision: 2}).format(unitFormat.value))
            }

            if (selectableOption.value[2].isSelected) {
                newTotal.push(currency(sourceTotal.value[2], {precision: 2}).format(unitFormat.value))
            }

            if (selectableOption.value[0].isSelected) {
                newTotal.push(currency(sourceTotal.value[3], {precision: 2}).format(unitFormat.value))
            }

            if (selectableOption.value[1].isSelected) {
                newTotal.push(currency(sourceTotal.value[4], {precision: 2}).format(unitFormat.value))
            }

            if (selectableOption.value[2].isSelected) {
                newTotal.push(currency(sourceTotal.value[5], {precision: 2}).format(unitFormat.value))
            }

            if (selectableOption.value[0].isSelected) {
                newTotal.push(currency(sourceTotal.value[6], {precision: 2}).format(unitFormat.value))
            }

            if (selectableOption.value[1].isSelected) {
                newTotal.push(currency(sourceTotal.value[7], {precision: 2}).format(unitFormat.value))
            }

            if (selectableOption.value[2].isSelected) {
                newTotal.push(currency(sourceTotal.value[8], {precision: 2}).format(unitFormat.value))
            }

            console.log('newTotal', newTotal)
            totals.value = newTotal;
            let selectedOption: any[] = selectableOption.value.filter((option: Option) => {
               return option.isSelected === true
            })
            
            getHeadingStyle(selectedOption.length);
            getSecondHeadingStyle(selectedOption.length);
            updateGraphData();
        }

        
        // other helper methods...
        function getGraphData() {
            let labelAndColor = {
                schedule: {
                    ordinary: {
                        label: 'Schedule - Ordinary',
                        backgroundColor: '#ff6384',
                        data: <any[]>[]
                    },
                    half: {
                        label: 'Schedule - Time & Half',
                        backgroundColor: '#464531',
                        data: <any[]>[]
                    },
                    double: {
                        label: 'Schedule - Double',
                        backgroundColor: '#1B5583',
                        data: <any[]>[]
                    }
                },
                actual: {
                    ordinary: {
                        label: 'Actual - Ordinary',
                        backgroundColor: '#93e093',
                        data: <any[]>[]
                    },
                    half: {
                        label: 'Actual - Time & Half',
                        backgroundColor: '#1f77b4',
                        data: <any[]>[]
                    },
                    double: {
                        label: 'Actual - Double',
                        backgroundColor: '#ff7f0e',
                        data: <any[]>[]
                    }
                },
                variance: {
                    ordinary: {
                        label: 'Variance - Ordinary',
                        backgroundColor: '#2ca02c',
                        data: <any[]>[]
                    },
                    half: {
                        label: 'Variance - Time & Half',
                        backgroundColor: '#d62728',
                        data: <any[]>[]
                    },
                    double: {
                        label: 'Variance - Double',
                        backgroundColor: '#9467bd',
                        data: <any[]>[]
                    }
                }
            }

            for (const dayData of graphData.value) {
                labelAndColor.schedule.ordinary.data.push(dayData[1])
                labelAndColor.actual.half.data.push(dayData[2])
                labelAndColor.variance.double.data.push(dayData[3])

                labelAndColor.actual.ordinary.data.push(dayData[4])
                labelAndColor.schedule.half.data.push(dayData[5])
                labelAndColor.schedule.double.data.push(dayData[6])

                labelAndColor.variance.ordinary.data.push(dayData[7])
                labelAndColor.variance.half.data.push(dayData[8])
                labelAndColor.variance.double.data.push(dayData[9])
            }

            let selectedGraphData: any[] = []
        
            // update graph data
            // normal hours
            if (selectableOption.value[0].isSelected) {
                selectedGraphData.push(labelAndColor.schedule.ordinary)
                selectedGraphData.push(labelAndColor.actual.ordinary)
                selectedGraphData.push(labelAndColor.variance.ordinary)
            } 

            // time and half
            if (selectableOption.value[1].isSelected) {
                selectedGraphData.push(labelAndColor.schedule.half)
                selectedGraphData.push(labelAndColor.actual.half)
                selectedGraphData.push(labelAndColor.variance.half)
            }

            // double
            if (selectableOption.value[2].isSelected) {
                selectedGraphData.push(labelAndColor.schedule.double)
                selectedGraphData.push(labelAndColor.actual.double)
                selectedGraphData.push(labelAndColor.variance.double)
            }

            return selectedGraphData
        }
        
        function setGraphData() {
            let chartLabels = []
            
            chartLabels = data.value.map((data: any) => {
                return data[0] // employee name
            })
            let payDetailChart = (document as any).getElementById('payDetailChart')
            if (!payDetailChart) {
                return
            }
            let ctx = payDetailChart.getContext('2d');

            let config = {
                type: 'bar',
                data: {
                    labels: chartLabels,
                    datasets: getGraphData(),
                },

                options: {
                    responsive: true,
                }
            }
            if (MyChart) {
                updateGraphData();
            } else {
                MyChart = new Chart(ctx!, config as any ) as any;
            }

        }

        async function exportCSV() {
            if (noDataError.value) {
                Dialog.alert("No data to export");
                return;
            }

            Dialog.loading();
            // todo may be handle get all request
            const allData: any = data.value;
            Dialog.clear();
            let csvContent = "data:text/csv;charset=utf-8,";

            csvContent += PayDetailViewCSVHeader[0].join(",") + "\r\n" + PayDetailViewCSVHeader[1].join(",") + "\r\n";

            allData.map((rowArray: any) => {
                csvContent += rowArray.map((value: string) => `"${value}"`).join(",") + "\r\n";
            });
            
            csvContent += [...totals.value].slice().map((value: string) => `"${value}"`).join(",") + "\r\n";

            var encodedUri = encodeURI(csvContent);
            var link = document.createElement("a");
            link.setAttribute("href", encodedUri);
            link.setAttribute("download", "Analytic.csv");
            document.body.appendChild(link); // Required for FF

            link.click();
        }

        function updateGraphData() {
            if (!MyChart) {
                return
            }
            const chartLabels = data.value.map((data: any) => {
                return data[0] // employee name
            })
            MyChart.data.labels = chartLabels;
            MyChart.data.datasets = getGraphData();
            MyChart.update();
        }

        function getHeadingStyle(length: number) {
            headingStyle.value = {
                "grid-template-columns": `100px repeat(3, ${length * 80 + length - 1}px)`,
            };
        }

        function getSecondHeadingStyle(length: number) {
            secondHeadingStyle.value = {
                "grid-template-columns": `100px repeat(${length * 3}, 80px)`,
            };
        }

        return {
            selectableOption,
            fromDate,
            toDate,
            
            graphData,
            data,
          
            selectedDate,
            dataHours,
            dataUnits,
            unitOptions,
            calculatedData,
            totals,
            displayN,
            displayList,
            heading,
            secondHeading,
            weekStart,
            weekEnd,
            headingStyle,
            secondHeadingStyle,
            currentEmployeeList,
            loadDataBySelection,
            selectChartOption,
            setGraphData,
            exportCSV,
        };
    },
})
</script>

<style scoped lang="scss">
@use '../../style/colors';
@import '../../style/costing-table';

$border-color: #00378033;

.costAnalaysisBody {
    overflow: auto;
    width: 100%;
    min-height: 260px;
}

.main-canvas-wrapper {
    margin-top: 40px;
    width: 80%;
}
.grid-background {
    background: $border-color;
}

.cost-grid {
    display: grid;
    grid-gap: 1px;
    width: fit-content;
    border: 1px solid $border-color;
    background: $border-color;
    max-height: 600px;
    overflow: auto;
}

.day-arr {
    display: grid;
    width: 100%;
    grid-template-columns: 100px repeat(9, 80px);
    grid-gap: 1px;
}

.cost-grid-heading {
    display: grid;
    width: 100%;
    // grid-template-columns: 100px repeat(3, 242px);
    grid-gap: 1px;
}

.cost-grid-second-heading {
    display: grid;
    width: 100%;
    // grid-template-columns: 100px repeat(9, 80px);
    grid-gap: 1px;
}

.table-cell {
    padding: 10px 5px;
    text-align: center;
    background:white;
}

.positive-cell {
    color: red;
}

.negative-cell {
    color: #558115;
}

.myChart-wrapper {
    margin-top: 20px;
}

:deep(.custom-select) {
    width: 220px;
    .custom-select-selected {
        max-width: 230px;
        width: 100%;
        min-height: auto;
    }

    .custom-select-items {
        width: 220px;
    }

}

.ps {
    display: grid;
    grid-gap: 1px;
    height: 520px;
}

</style>