<template>
    <div>
        <div>
            <div class="cost-report-controls">
                <div class="select-view">
                    <custom-select
                        :options="options"
                        :selected="selectedViewFromDropdown"
                        @selectOption="selectGridData">
                    </custom-select>
                </div>
                <div class="select-unit ml-2" v-if="selectedViewFromDropdown !== SelectableView.simple && selectedViewFromDropdown !== SelectableView.detailed">
                    <custom-select
                        :options="unitOptions"
                        :selected="selectedUnit"
                        :width="100"
                        @selectOption="selectUnitOption">
                    </custom-select>
                </div>
                <div class="select-employee ml-2" v-if="selectableEmployeeList.length > 0">
                    <multi-select
                        :selectOptionInput="selectableEmployeeList"
                        :targetString="'commonName'"
                        :allowAll="true">
                    </multi-select>
                </div>
                <div class="ml-2">
                    <label>From</label>
                    <input v-model="fromDateText"
                            class="input-box date-box ml-2"
                            type="text"
                            @click="updateFromDateTime($event, fromDate)"
                    />
                </div>

                <div class="ml-2">
                    <label>To</label>
                    <input v-model="toDateText"
                            class="input-box date-box ml-2"
                            type="text"
                            @click="updateToDateTime($event, toDate)"
                    />
                </div>
                <div class="btn-show-wrap ml-2">
                    <button class="light-blue small action-button white"
                            @click="loadGridDataBySelection()">
                        Show
                    </button>
                </div>
                <button class="export-csv light-blue small action-button white ml-2"
                    @click="exportCSVByView">
                    Export CSV
                </button>
            </div>
        </div>
        <div v-if="employeeList.length > 0">
            <div>
                <div class="cost-report-wrap"
                    v-if="selectedView === SelectableView.detailed">
                    <DetailCostReport
                        ref="CostDetailAnalysisRef"
                        :fromDate="fromDate"
                        :toDate="toDate"
                        :employee-list="selectedEmployeeList()">
                    </DetailCostReport>
                </div>

                <div class="cost-report-wrap"
                    v-if="selectedView === SelectableView.simple">
                    <SimpleAnalysis
                        ref="SimpleAnalysisRef"
                        :fromDate="fromDate"
                        :toDate="toDate"
                        :employee-list="selectedEmployeeList()">
                    </SimpleAnalysis>
                </div>
                <div class="cost-report-wrap"
                    v-if="selectedView === SelectableView.weekly">
                    <CostAnalysisWeekly
                        ref="CostAnalysisWeeklyRef"
                        :fromDate="fromDate"
                        :toDate="toDate"
                        :selectedUnit="selectedUnit"
                        :employee-list="selectedEmployeeList()">
                    </CostAnalysisWeekly>
                </div>
                <div class="cost-report-wrap"
                    v-if="selectedView === SelectableView.employeeWeekly">
                    <EmployeeWeekly
                        ref="EmployeeWeeklyRef"
                        :fromDate="fromDate"
                        :toDate="toDate"
                        :selectedUnit="selectedUnit"
                        :employee-list="selectedEmployeeList()">
                    </EmployeeWeekly>
                </div>
                <div class="cost-report-wrap"
                    v-if="selectedView === SelectableView.payDetailView">
                <!-- <div class="cost-report-wrap"> -->
                    <PayDetailView
                        ref="PayDetailViewRef"
                        :fromDate="fromDate"
                        :toDate="toDate"
                        :selectedUnit="selectedUnit"
                        :employee-list="selectedEmployeeList()">
                    </PayDetailView>
                </div>
            </div>
        </div>

        <date-picker
            ref="DatePickerRef"
            :top="dateModalTop"
            :leftInput="dateModalLeft"
            :max-date="datePickerMax"
            :min-date="datePickerMin"
            :date="datePickerTargetDay"
        ></date-picker>
    </div>
</template>

<script lang="ts">
import "chartjs-adapter-moment";

import Modal from "@/components/common/Modal.vue";

import {defineComponent, getCurrentInstance, onMounted, ref} from "vue";
import CustomSelect from "@/components/common/CustomSelect.vue";
import moment, {Moment} from "moment";
import Dialog from "@/components/dialog";
import {EmployeeActions} from "@/store/employeeModule";
import {useStore} from "vuex";
import DatePicker from "@/components/common/DatePicker.vue";
import DetailCostReport from "@/components/Analysis/DetailCostReport.vue";
import SimpleAnalysis from "@/components/Analysis/SimpleAnalysis.vue";
import {SelectableView} from "@/components/Analysis/CostAnalysisType";
import CostAnalysisWeekly from "@/components/Analysis/CostAnalysisWeekly.vue";
import EmployeeWeekly from "@/components/Analysis/EmployeeWeekly.vue";
import PayDetailView from "@/components/Analysis/PayDetailView.vue";
import MultiSelect from "@/components/common/MultiSelect.vue";
import { UnitDropDown } from "./preset";

declare var Affinity: any;

export default defineComponent({
    name: "Analysis",
    components: {
        PayDetailView,
        DetailCostReport,
        SimpleAnalysis,
        CostAnalysisWeekly,
        EmployeeWeekly,
        Modal,
        CustomSelect,
        MultiSelect,
        DatePicker,
    },

    setup() {
        const store = useStore();
        //
        const data = ref(<any[]>[]);
        const pageNumber = ref(1);
        const pageSize = ref(20);
        const totalPages = ref(5);
        const pageList = ref(<number[]>[]);
        const recordCount = ref(0);
        const noDataError = ref(false);
        const DatePickerRef = ref<InstanceType<typeof DatePicker>>();
        const SimpleAnalysisRef = ref<InstanceType<typeof SimpleAnalysis>>();
        const CostDetailAnalysisRef =ref<InstanceType<typeof DetailCostReport>>();
        const CostAnalysisWeeklyRef = ref<InstanceType<typeof CostAnalysisWeekly>>();
        const EmployeeWeeklyRef = ref<InstanceType<typeof EmployeeWeekly>>();
        const PayDetailViewRef = ref<InstanceType<typeof PayDetailView>>();

        //
        const selectedUnit = ref(UnitDropDown.value);
        const unitOptions = ref([UnitDropDown.value, UnitDropDown.unit]);

        //
        const fromDate = ref(<Moment | null>null);
        const toDate = ref(<Moment | null>null);
        const employeeList = ref(<number[]>[]);
        const selectableEmployeeList = ref(<any[]>[]);
        const fromDateText = ref("");
        const toDateText = ref("");
        const dateModalTop = ref(0);
        const dateModalLeft = ref(0);
        const datePickerMax = ref(<Moment | null>null);
        const datePickerMin = ref(<Moment | null>null);
        const datePickerTargetDay = ref(<Moment | null>moment());

        // const selectedView = ref('Detailed Cost Report')
        const selectedViewFromDropdown = ref<SelectableView>(SelectableView.weekly);
        const selectedView = ref<SelectableView>(SelectableView.weekly);

        const dataAnalysis = ref(<any[]>[]);
        const options = ref([SelectableView.simple,
                SelectableView.detailed,
                SelectableView.weekly,
                SelectableView.employeeWeekly,
                SelectableView.payDetailView]);

        const app = getCurrentInstance();
        const $user = Affinity.user || app!.appContext.config.globalProperties.$user;

        onMounted(() => {
            if (!fromDate.value || !toDate.value) {
                if (!fromDate.value) {
                    fromDate.value = moment().startOf("month");
                    fromDateText.value = fromDate.value.format("DD/MM/YYYY");
                }
                if (!toDate.value) {
                    toDate.value = moment().endOf("month").startOf("day");
                    toDateText.value = toDate.value.format("DD/MM/YYYY");
                }
            }
            initialRequest();
        });

        async function initialRequest() {
            await store.dispatch(EmployeeActions.getEmployees, $user.EmployeeNo)
                .then((res: any) => {
                    selectableEmployeeList.value = res
                    .filter((data: any) => data.payType !== 'B' && data.payType !== 'S')
                    .map((data: any) => {
                        data.isSelected = true
                        return data
                    });

                    employeeList.value = res
                    .filter((data: any) => data.PayType !== 'B' && data.PayType !== 'S')
                    .map(
                        (employeeDetail: any) => employeeDetail.employeeNo
                    );
                });

            await loadGridDataBySelection();
        }

        async function loadGridDataBySelection() {
            try {
                if (fromDate.value && toDate.value) {
                    if (toDate.value.diff(fromDate.value, "weeks") >= 6) {
                        Dialog.alert(
                            "You can only view information up to six weeks in the past."
                        );
                        throw "weeks error";
                    }
                }
                selectedView.value = selectedViewFromDropdown.value;

                if (selectedView.value === SelectableView.simple) {
                    SimpleAnalysisRef.value!.loadGridData();
                }
                else if (selectedView.value === SelectableView.detailed) {
                    CostDetailAnalysisRef.value!.loadGridData();
                }
                else if (selectedView.value === SelectableView.weekly) {
                    CostAnalysisWeeklyRef.value!.loadGridData();
                }
                else if (selectedView.value === SelectableView.employeeWeekly) {
                    EmployeeWeeklyRef.value!.loadDataBySelection();
                }

                else if (selectedView.value === SelectableView.payDetailView) {
                    PayDetailViewRef.value!.loadDataBySelection();
                }
                

            } catch (e) {
                //
                // console.log('e', e)
            }
        }

        async function updateToDateTime($event: any, date: any) {
            let element = $event.target.getClientRects()[0];
            dateModalTop.value = element.bottom;
            dateModalLeft.value = element.left;
            if (fromDate) {
                datePickerMin.value = fromDate
                    .value!.clone()
                    .subtract(1, "day");
                datePickerMax.value = null;
            }
            datePickerTargetDay.value = toDate.value ? toDate.value : moment();

            const d = await openDatePicker();
            toDate.value = d || undefined;
            toDateText.value = toDate.value!.format("DD/MM/YYYY");
        }

        async function updateFromDateTime($event: any, date: any) {
            let element = $event.target.getClientRects()[0];
            dateModalTop.value = element.bottom;
            dateModalLeft.value = element.left;

            if (toDate) {
                datePickerMin.value = null;
                datePickerMax.value = toDate.value!.clone().add(1, "day");
            }

            datePickerTargetDay.value = fromDate.value
                ? fromDate.value
                : moment();

            const d = await openDatePicker();
            fromDate.value = d || undefined;
            fromDateText.value = fromDate.value!.format("DD/MM/YYYY");
        }

        async function openDatePicker() {
            return (await DatePickerRef.value!.toggleShow(
                datePickerTargetDay.value!
            )) as Promise<any>;
        }

        async function exportCSVByView() {
            if (selectedView.value === SelectableView.simple) {
                await SimpleAnalysisRef.value!.exportCSV();
            } else if (selectedView.value === SelectableView.detailed) {
                await CostDetailAnalysisRef.value!.exportCSV();
            } else if (selectedView.value === SelectableView.weekly) {
                await CostAnalysisWeeklyRef.value!.exportCSV();
            } else if (selectedView.value === SelectableView.employeeWeekly) {
                await EmployeeWeeklyRef.value!.exportCSV();
            } else if (selectedView.value === SelectableView.payDetailView) {
                await PayDetailViewRef.value!.exportCSV();
            }
        }

        async function selectGridData(option: SelectableView) {
            selectedViewFromDropdown.value = option;
        }

        function selectUnitOption(newUnit: string) {
            selectedUnit.value = newUnit as UnitDropDown;
        }

        function selectedEmployeeList(): number[] {
            return selectableEmployeeList.value.filter(
                (employee: any) => employee.isSelected
            ).map((employee: any) => employee.employeeNo);
        }


        return {
            DatePickerRef,
            CostDetailAnalysisRef,
            SimpleAnalysisRef,
            CostAnalysisWeeklyRef,
            EmployeeWeeklyRef,
            PayDetailViewRef,
            data,
            dataAnalysis,
            options,
            selectedView,
            selectedViewFromDropdown,
            pageNumber,
            pageSize,
            totalPages,
            pageList,
            noDataError,
            recordCount,
            selectedUnit,
            unitOptions,
            dateModalTop,
            dateModalLeft,
            datePickerMax,
            datePickerMin,
            datePickerTargetDay,
            fromDate,
            toDate,
            fromDateText,
            toDateText,
            employeeList,
            selectableEmployeeList,
            updateToDateTime,
            updateFromDateTime,
            exportCSVByView,
            selectGridData,
            loadGridDataBySelection,
            selectUnitOption,
            selectedEmployeeList,
            SelectableView,
        };
    },
});
</script>

<style scoped lang="scss">
@use "../../style/colors";
$border-color: #00378033;

.input-box {
    font-family: Arial, "Helvetica Neue", Helvetica, Sans-Serif;
    font-size: 12px;
    border-radius: 6px;
    background-color: #f7f9fe;
    border: 1px solid #c8d1db;
    color: #344356;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    padding: 0 5px;
    line-height: normal;
    height: 30px;
}

.date-box {
    width: 100px;
}

.cost-report-wrap {
    display: flex;
    flex-wrap: wrap;
}

.cost-report-controls {
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
    flex-wrap: wrap;
}

.control-left {
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;

}

.grid-extra-width {
    width: 2000px;
}

.grid-heading {
    display: grid;
    width: 100%;
    grid-template-columns: repeat(3, 1fr) 100px 150px repeat(8, 0.8fr) repeat(
            2,
            1fr
        );
    grid-gap: 1px;
}

.heading-cell {
    padding: 3px 5px 3px 5px;
    background: linear-gradient(
        to bottom,
        #fcfcfc 0%,
        #efefef 49%,
        #e2e2e2 51%,
        #d6d6d6 100%
    );
    text-align: center;
}

.grid-body {
    display: grid;
    grid-gap: 1px;
    width: 100%;
    border-top: none;
    border-bottom: 1px solid #c8d1db;
}

.grid-body-row {
    display: grid;
    width: 100%;
    grid-template-columns: repeat(3, 1fr) 100px 150px repeat(8, 0.8fr) repeat(
            2,
            1fr
        );
    grid-gap: 1px;
    text-align: center;
}

.grid-body-cell {
    display: grid;
    align-items: center;
    min-height: 30px;
    border-bottom: 1px solid #c8d1db;
}

.total-page-small {
    display: flex;
    align-items: center;
    justify-content: center;
    .selected {
        background: #0c96e4;
        color: white;
    }
}
.page-button {
    box-sizing: border-box;
    padding: 0;
    margin-right: 5px;
    text-align: center;
    border-radius: 5px;
    cursor: pointer;
    height: 24px;
    width: 24px;
    line-height: 24px;
    font-weight: bold;
    display: inline-block;

    &:last-child {
        margin-right: 0;
    }
}

.action-button {
    box-shadow: none;
}

//.select-view {
.cost-report-controls{
    :deep(.custom-select) {
        @media screen and (min-width: 768px) {
            width: 230px;
            font-size: 12px;

            .custom-select-selected {
                min-height: auto;
            }

            .custom-select-items {
                width: 220px;
            }
        }
        @media screen and (max-width: 767px) {
            width: 100%;
            max-width: 275px;
        }
    }
}

//}

.select-employee {
    :deep .custom-select {
        @media screen and (min-width: 768px) {
            width: 140px;
        }
    }
}

</style>
