<template>
    <Modal ref="ModalRef" :top="top" :left="calculateLeft(left)" :customPosition="!!(top || left)" @cancel="cancelPickDate">
        <template v-slot:body>
            <div class="v3-calendar">
                <section class="dp-heading">
                    <span class="cursor-pointer icon-arrow-left px-4" @click="previousMonth"></span>
                    <div>{{ selectedDate.format('YYYY MMM') }}</div>
                    <span class="cursor-pointer icon-arrow-right px-4" @click="nextMonth"></span>
                </section>
                <section class="db-content">
                    <div class="dp-week-of-day">
                        <span v-for="weekDay of weekOfDays">{{ weekDay.shortName }}</span>
                    </div>
                    <div class="dp-week-days">
                        <span class="date-wrapper" v-for="calendarDay in calendarArr">
                              <span class="date-number-cell"
                                    @click="selectDate(calendarDay)"
                                    v-bind:class="{'today-target': colorToday(calendarDay),
                                     'not-valid': !calendarDay.isValid,
                                     'last-month-day': calendarDay.lastMonth,
                                     'next-month-day': calendarDay.nextMonth,
                                     'selected': calendarDay.selected}">{{ calendarDay.dateNumber }}
                              </span>
                        </span>
                    </div>
                </section>
            </div>
        </template>
    </Modal>

</template>

<script lang="ts">
import {defineComponent, inject, ref} from "vue";
import moment, {Moment} from "moment";
import Modal from "@/components/common/Modal.vue";
import { CompanyConfigInterface } from "@/plugins/companyConfig";

export interface CalendarDay {
    dateNumber: number,
    month: number,
    lastMonth?: boolean,
    nextMonth?: boolean,
    isValid?: boolean,
    selected: boolean,
    year: number
}

export default defineComponent({
    name: "DatePicker",
    components: {
        Modal
    },
    props: {
        top: {
            type: Number,
        },
        leftInput: {
            type: Number
        },
        date: {
            type: Object
        },
        maxDate: {
            type: Object
        },
        minDate: {
            type: Object
        }
    },
    data() {
        return {
            left: 0,
            currentYear: 2021,
            datePickerPromise: <any>null,
            resolve: <any>null,
            reject: <any>null,
            selectedDate: moment(),
            manageCompanyConfig: <any>null,
            
        }
    },

    setup() {
        const calendarArr = ref<CalendarDay[]>([]);
        const currentMonth = ref();
        currentMonth.value = moment().month();

        const weekOfDays = ref([
                {
                    fullName: "Monday",shortName: "Mon", dayNumber: 1,
                },
                {
                    fullName: "Tuesday", shortName: "Tue", dayNumber: 2,
                },
                {
                    fullName: "Wednesday",shortName: "Wed",dayNumber: 3,
                },
                {
                    fullName: "Thursday",shortName: "Thu", dayNumber: 4,
                },
                {
                    fullName: "Friday", shortName: "Fri", dayNumber: 5,
                },
                {
                    fullName: "Saturday", shortName: "Sat", dayNumber: 6
                },
                {
                    fullName: "Sunday", shortName: "Sun", dayNumber: 0,
                },
            ]);
        
        return {
            currentMonth,
            calendarArr,
            weekOfDays,
        }

    },
    mounted() {
        this.manageCompanyConfig = inject('MANAGE_COMPANY_CONFIG') as CompanyConfigInterface;
        console.log('company config', this.manageCompanyConfig?.getAllowSundayFirst())
        if (this.manageCompanyConfig?.getAllowSundayFirst()) {
            this.weekOfDays = [
                { fullName: "Sunday", shortName: "Sun", dayNumber: 0 },
                { fullName: "Monday", shortName: "Mon", dayNumber: 1 },
                { fullName: "Tuesday", shortName: "Tue", dayNumber: 2 },
                { fullName: "Wednesday", shortName: "Wed", dayNumber: 3 },
                { fullName: "Thursday", shortName: "Thu", dayNumber: 4 },
                { fullName: "Friday", shortName: "Fri", dayNumber: 5 },
                { fullName: "Saturday", shortName: "Sat", dayNumber: 6 }
            ];
        }
        this.calculateMonth();
        this.datePickerPromise = new Promise((resolve, reject) => {
          this.resolve = resolve;
          this.reject = reject;
        })
    },

    watch: {
        maxDate () {
            if (this.selectedDate) {
                this.calculateBaseOnInput(this.selectedDate)
            } else {
                this.calculateBaseOnInput()
            }
        },
        minDate() {
            if (this.selectedDate) {
                this.calculateBaseOnInput(this.selectedDate)
            } else {
                this.calculateBaseOnInput()
            }
        }
    },

    methods: {
        cancelPickDate() {
            this.reject();
        },
        calculateLeft() {
            if (!this.leftInput) return 0;
            // this.left = <any>this.leftInput
            if (window.innerWidth < this.leftInput + 370) {
                this.left = window.innerWidth - 400;
            } else {
                this.left = this.leftInput
            }
            return this.left
        },
        nextMonth() {
            this.currentMonth = this.currentMonth + 1
            this.calculateMonth()
        },

        previousMonth() {
            this.currentMonth = this.currentMonth - 1
            this.calculateMonth()
        },

        colorToday(calendarDay: CalendarDay) {
            const today = moment();
            return today.year() === calendarDay.year
                && today.month() + 1 === calendarDay.month
                && today.date() === calendarDay.dateNumber;
        },

        selectDateOnly(selectedDate: Moment) {
            // this.selectedDate = calendarDay;
            for (let day of this.calendarArr) {
                if (day.year === selectedDate.year()
                    && day.month === selectedDate.month() + 1
                    && day.dateNumber === selectedDate.date()) {
                    day.selected = true
                } else {
                    day.selected = false;
                }

            }
            // calendarDay.selected = true;
        },

        selectDate(calendarDay: CalendarDay) {
            this.selectedDate = this.selectedDate.date(calendarDay.dateNumber);
            for (let day of this.calendarArr) {
                day.selected = false;
            }
            calendarDay.selected = true;
            // todo: fix method sequence. should toggle modal after send resolve
            (this.$refs.ModalRef as any).toggleShow();
            // this.$emit('selectDate', this.selectedDate.clone())
            this.resolve(this.selectedDate.clone())
        },

        calculateMonth(date?: Moment) {
            if(!date) {
                date = moment();

                if (this.currentYear) {
                    date.year(this.currentYear)
                }
            }

            let currentMonth = date.clone().month(this.currentMonth);
            let monthStart = currentMonth.startOf('month').day()
            let monthEnd = currentMonth.clone().endOf('month').day()
            currentMonth.startOf('day')
            let calendarArr: any[] = [];

            // add last month day appear in calendar
            let lastMonthEndDate = currentMonth.clone().date(0).date()
            // todo change this 2 1
            let lastMonthBeginDate 

            // todo: fix this logic. so we count last month day by subtraction loop
            if (this.manageCompanyConfig.getAllowSundayFirst()) {
                lastMonthBeginDate = lastMonthEndDate - (monthStart - 1);
            } else {
                if (monthStart === 0) {
                    lastMonthBeginDate = lastMonthEndDate - 5;
                } else if (monthStart === 1) {
                    lastMonthBeginDate = null // no last month day
                } else {
                    lastMonthBeginDate = lastMonthEndDate - (monthStart - 2);
                }
            }
            
            if (lastMonthBeginDate) {
                for (lastMonthBeginDate; lastMonthBeginDate <= lastMonthEndDate; lastMonthBeginDate++) {
                    calendarArr.push({
                        year: currentMonth.clone().date(0).year(),
                        month: currentMonth.clone().date(0).month() + 1,
                        dateNumber: lastMonthBeginDate,
                        selected: false,
                        lastMonth: true
                    });
                }
            }
            

            // add this month day appear in calendar
            let mStart = 1
            let mEnd = currentMonth.clone().endOf('month').date();

            for (mStart; mStart <= mEnd; mStart++) {
                // calendarArr.push(mStart);
                calendarArr.push({
                    year: currentMonth.year(),
                    month: currentMonth.month() + 1,
                    isValid: this.validDateRange(this.maxDate, this.minDate, currentMonth.clone().startOf('month').date(mStart)),
                    dateNumber: mStart,
                    selected: false
                });
            }

            // add next month day appear in calendar
            let mNextStart = 1;
            let mNextEnd;
            if (this.manageCompanyConfig.getAllowSundayFirst()) {
                mNextEnd = monthEnd === 0 ? 6 : 6 - monthEnd;
            } else {
                mNextEnd = monthEnd === 0 ? 0 : 6 - (monthEnd - 1);
            }

            for (mNextStart; mNextStart <= mNextEnd; mNextStart++) {
                let nextMonth = currentMonth.clone().add(1, 'month');
                calendarArr.push({
                    year: nextMonth.year(),
                    month: nextMonth.month() + 1,
                    dateNumber: mNextStart,
                    selected: false,
                    nextMonth: true
                });
            }

            this.calendarArr = calendarArr;

            if (currentMonth.clone().endOf('month').date() <= this.selectedDate.date()) {
                this.selectedDate = currentMonth.clone().endOf('month').startOf('day')
            } else {
                this.selectedDate = currentMonth.date(this.selectedDate.date())
            }

        },
        
        validDateRange(maxDate: any, minDate: any, currentDay: Moment) {
            if (maxDate && minDate) {
                return currentDay.diff(maxDate, 'day') < 0 && currentDay.diff(minDate, 'day') > 0
            } else if (maxDate) {
                return currentDay.diff(maxDate, 'day') < 0
            } else if (minDate) {
                return currentDay.diff(minDate, 'day') > 0
            } else {
                return true
            }
        },

        calculateBaseOnInput(newDate?: Moment) {
            if (!newDate) {
                newDate = moment()
                this.currentMonth = newDate.month();
                this.selectedDate = newDate.clone();
                this.calculateMonth(newDate);
                this.currentYear = newDate.year();
            } else {
                this.currentMonth = newDate.month();
                this.selectedDate = newDate.clone();
                this.calculateMonth(newDate);
                this.currentYear = newDate.year();
                this.selectDateOnly(newDate);
            }
        },

        toggleShow(newDate?: Moment) {
            if (newDate) {
                this.calculateBaseOnInput(newDate)
            } else {
                this.calculateBaseOnInput()
            }

            // newDate = (this.date as Moment) ? (this.date as Moment).clone() : moment();


            (this.$refs.ModalRef as any).toggleShow();
            // this.selectDate(newDate)
            this.datePickerPromise = new Promise((resolve, reject) => {
                this.resolve = resolve;
                this.reject = reject;
            })

            return this.waitPickDate();
        },

        async waitPickDate() {
            return this.datePickerPromise;
        }
    }
})
</script>

<style scoped lang="scss">

.v3-calendar {
    display: block;
    width: 290px;
    box-sizing: border-box;
}

.dp-heading {
    display: flex;
    justify-content: space-around;
    height: 30px;
}

.dp-week-of-day {
    display: flex;
    justify-content: space-around;
    width: 100%;

    span {
        display: inline-block;
        width: 14.2%;
        height: 30px;
        text-align: center;
        font-size: 14px;
    }
}

.dp-week-days {
    //display: flex;
    flex-wrap: wrap;
    justify-content: space-around;

    .date-wrapper {
        display: inline-block;
        width: 14.2%;
        text-align: center;
    }

    .date-number-cell {
        display: inline-block;
        width: 30px;
        height: 30px;
        line-height: 30px;
        font-size: 14px;
        text-align: center;
        cursor: pointer;
        border: 1px solid transparent;

        &:hover {
            background: orange;
            color: white;
            border: 1px solid orange;
        }
    }

    .today-target {
        color: orange;
        border: 1px solid orange;
    }

    .last-month-day, .next-month-day, .not-valid {
        color: grey;
        border-color: transparent;
        pointer-events: none;
    }

    .selected {
        background-color: darkorange;
        color: white;
        border: 1px solid darkorange;

    }
}

// firefox patch for same height but trigger overflow
.db-content {
    overflow: hidden;
}
</style>