<template>
    <div class="filters-legends">
        <div class="left-box">
            <div class="background-legend">
                <div>
                    <div class="flexi-legend mr-2"></div>
                    <div class="legend-label">Flexi Schedule</div>
                </div>
                <div class="ml-8">
                    <div class="team-legend mr-2"></div>
                    <div class="legend-label">Team Schedule</div>
                </div>
                <div class="ml-8">
                    <div class="personal-legend mr-2"></div>
                    <div class="legend-label">Personal Schedule</div>
                </div>
            </div>
        </div>
        <div class="weekDetail">
            <span
                class="arrow-height button icon small mr-4"
                @click="getLastWeek">
                <Icon
                    :icon="$icons.chevronLeft"
                    height="30"
                    :style="{ color: '#FFFFFF' }"></Icon>
            </span>
            <span @click="chooseCalendarDate">
                {{ $filters.ordinalSuffix(dayFocus.week()) }} Week,
                {{ dayFocus.format("DD/MM/YYYY") }}</span>
            <span
                class="arrow-height button icon small ml-4"
                @click="getNextWeek">
                <Icon
                    :icon="$icons.chevronRight"
                    height="30"
                    :style="{ color: '#FFFFFF' }">
                </Icon>
            </span>
        </div>
        <div class="status-section right-box">
            <span class="copy2 ml-2 text-capitalize" v-if="!isEmployee">
                Status: {{ getCurrentWeekStatus() }}
            </span>
        </div>
    </div>
    <div class="flex-sched-filter" v-if="!isEmployee">
        <div class="d-flex align-center">
            <flex-schedule-cell-control class="inline-block" @checkOptions="changeCellControl"></flex-schedule-cell-control>
            <flex-schedule-cost-center
                class="ml-2"
                :costCenterOptions="costCenterOptions"
                @selectOption="selectCostCenter">
            </flex-schedule-cost-center>
            <flex-Schedule-Multi-Column
                class="ml-2"
                :isEmployee="isEmployee">
            </flex-Schedule-Multi-Column>
        </div>

        <div v-if="weekStatus === 'published'">
            <div class="custom-select" tabindex="1" ref="selectRef">
                <div>
                    <button class="grey small copy2 white--text action-button-width"
                        @click="changeDisplayControl">
                        <span class="inline-block-middle white">Actions</span>
                        <span class="inline-block-middle ml-1"
                            style="height: 15px">
                            <Icon
                                :icon="$icons.chevronDown"
                                height="15"
                                :style="{ color: '#FFFF' }">
                            </Icon>
                        </span>
                    </button>
                </div>
                <div class="custom-select-items"  v-if="!isEmployee">
                    <div class="action-buttons">
                        <button class="ghost small copy2 mt-2" @click="openAnalysisChart">
                            Coverage
                        </button>
                        <button class="ghost small copy2 mt-2" @click="openCostAnalysis">
                            Cost
                        </button>
                        <button class="ghost small copy2 mt-2" @click="printFlexSchedule">
                            Print
                        </button>
                    </div>
                </div>
            </div>
        </div>
        <div v-else>
            <div v-if="!isAdmin">
                <span>
                    <div class="custom-select" tabindex="1" ref="selectRef">
                        <div>
                            <button
                                class="grey small copy2 white--text action-button-width"
                                @click="changeDisplayControl">
                                <span class="inline-block-middle">Actions</span>
                                <span
                                    class="inline-block-middle ml-1"
                                    style="height: 15px">
                                    <Icon
                                        :icon="$icons.chevronDown"
                                        height="15"
                                        :style="{ color: '#FFFF' }">
                                    </Icon>
                                </span>
                            </button>
                        </div>
                        <div class="custom-select-items" >
                            <div class="action-buttons">
                                <!--                                    <button class="blue small copy2 white&#45;&#45;text" v-if="weekStatus === 'draft'" @click="openDraftDialog">Copy</button>-->
                                <button class="ghost small copy2"
                                    v-if="showCopy()" @click="openDraftDialog">
                                    Copy
                                </button>
                                <button class="ghost small copy2"
                                    v-if="weekStatus === 'draft' || weekStatus === 'partial'"
                                    @click="selectEmployeeAndPublish">
                                    Publish
                                </button>
                                <button class="ghost small copy2" @click="openAnalysisChart">
                                    Coverage
                                </button>
                                <button class="ghost small copy2" @click="openCostAnalysis">
                                    Costs
                                </button>
                                <button class="ghost small copy2" @click="printFlexSchedule">
                                    Print
                                </button>
                            </div>
                        </div>
                    </div>
                </span>
            </div>
        </div>
    </div>
    <!-- Popups -->
    <date-picker ref="DatePickerRef" :top="datePickerTop" :left="datePickerLeft"></date-picker>
</template>

<script setup lang="ts">
import { ref, computed, onBeforeMount, ComputedRef, inject } from 'vue';
import { useStore } from 'vuex';
import {LookupActions} from "@/store/lookupModule";
import { ManagedEmployee } from "@/store/employeeModule";
import { WeeklyPublishStatus, TopFilterEmitEvents as EmitEvents } from "@/components/FlexSchedule/FlexScheduleInterface";

import moment, { Moment } from 'moment';
import { FlexScheduleGetter } from '@/store/flexScheduleModule';
import FlexScheduleCellControl from "@/components/FlexSchedule/FlexScheduleCellControl.vue";
import FlexScheduleCostCenter from "@/components/FlexSchedule/FlexScheduleCostCenter.vue";
import FlexScheduleMultiColumn from "@/components/FlexSchedule/FlexScheduleMultiColumn.vue";
import DatePicker from "@/components/common/DatePicker.vue";

import {Icon} from "@iconify/vue";
import { CompanyConfigInterface } from '@/plugins/companyConfig';


const $store = useStore();

let showControl = ref(false);
let dayFocus = ref(moment());
// let weekStatus = ref(WeeklyPublishStatus.empty);
let isAdmin = ref(false);
let datePickerTop = ref(0);
let datePickerLeft = ref(0);
const DatePickerRef = ref<InstanceType<typeof DatePicker>>();

const props = defineProps({
    isEmployee: {
        type: Boolean,
        required: true,
    }
});
const emit = defineEmits([
  EmitEvents.OPEN_DRAFT_DIALOG,
  EmitEvents.SELECT_EMPLOYEE_AND_PUBLISH,
  EmitEvents.OPEN_ANALYSIS_CHART,
  EmitEvents.OPEN_COST_ANALYSIS,
  EmitEvents.PRINT_FLEX_SCHEDULE,
  EmitEvents.CHANGE_CELL_CONTROL,
  EmitEvents.SELECT_COST_CENTER,
  EmitEvents.CHANGE_WEEK
  // EmitEvents.CHOOSE_CALENDAR_DATE
]);

onBeforeMount(() => {
    if (window.Affinity.user.EmployeeNo >= 5000000) {
        isAdmin.value = true;
    }
});

const costCenterOptions = computed(() => {
    // return $store.state.LookUp.costCenters;
    let costCenterData = $store.state.LookUp.costCenters;
    costCenterData = costCenterData.map(
        (option: any) => {
            option.selected = false;
            return option;
        }
    );

    costCenterData.unshift({
        costCentreId: -1,
        description: "All",
        selected: true,
    });

    return costCenterData;
});

const weekStatus: ComputedRef<WeeklyPublishStatus> = computed(() => {
    return $store.getters[FlexScheduleGetter.getWeekStatus];
});


const changeDisplayControl = () => {
    showControl.value = !showControl.value;
};

const showCopy = () => {
    if ($store.getters[FlexScheduleGetter.hasFuturePaidDate] === true) {
        return false;
    }
    return moment().diff(dayFocus.value.clone().endOf("week"), "day") <= 0;
    
};

const openDraftDialog = () => {
    emit(EmitEvents.OPEN_DRAFT_DIALOG);
};

const selectEmployeeAndPublish = () => {
    emit(EmitEvents.SELECT_EMPLOYEE_AND_PUBLISH);
};

const openAnalysisChart = () => {
    emit(EmitEvents.OPEN_ANALYSIS_CHART);
};

const openCostAnalysis = () => {
    emit(EmitEvents.OPEN_COST_ANALYSIS);
};

const printFlexSchedule = () => {
    console.log('printFlexSchedule', 123)
    emit(EmitEvents.PRINT_FLEX_SCHEDULE);
};

const changeCellControl = () => {
    emit(EmitEvents.CHANGE_CELL_CONTROL);
};

// const selectCostCenter = (costCenter: string) => {
//     emit(EmitEvents.SELECT_COST_CENTER, costCenter);
// };

const getLastWeek = () => {
    console.log('dayFocus.value', dayFocus.value)
    dayFocus.value = dayFocus.value.subtract(7, 'days').clone();
    emit(EmitEvents.CHANGE_WEEK, dayFocus.value);
};

const getNextWeek = () => {
    dayFocus.value = dayFocus.value.add(7, 'days').clone();
    emit(EmitEvents.CHANGE_WEEK, dayFocus.value);
};

// const chooseCalendarDate = () => {
//     emit('chooseCalendarDate');
// };
const chooseCalendarDate = async ($event: any) => {
    let element = $event.target.getClientRects()[0];
    datePickerTop.value = element.bottom + 10;
    datePickerLeft.value = element.left - 80;
    let datePickerResult;
    try {
        datePickerResult = await DatePickerRef.value?.toggleShow(dayFocus.value);
    } catch (e) {
        return;
    }
    await selectDate(datePickerResult);
}

const selectDate = (date: Moment) => {
    console.log('date', date)
    dayFocus.value = date;
    $store.state.FlexSchedule.calendarFocusDate = date;
    emit(EmitEvents.CHANGE_WEEK, dayFocus.value);
}

const getCurrentWeekStatus = () => {
    if (
        weekStatus.value === WeeklyPublishStatus.empty ||
        weekStatus.value === WeeklyPublishStatus.draft
    ) {
        return "Draft";
    } else if (weekStatus.value === WeeklyPublishStatus.partial) {
        return "Partially Published";
    } else if (weekStatus.value === WeeklyPublishStatus.published) {
        return "Published";
    }

    return weekStatus.value;
};

const selectCostCenter = (selectedCostCentres: any) => {
    const weekMap = $store.state.FlexSchedule.scheduleWeek
    const selected = costCenterOptions.value.filter((option: any) => {
        return option.selected;
    });

    const selectedIds = selectedCostCentres.map((c: any) => {
        return c.selected ? c.costCentreId : false;
    });

    if (!!selectedIds.find((e: any) => e === -1)) {
        for (const [key, employeeData] of weekMap.entries()) {
            employeeData.show = true;
        }
        $store.state.EmployeeModule.filteredEmployees = $store.state.EmployeeModule.employees;
        return;
    }

    let newEmployeeList = $store.state.EmployeeModule.employees.filter(
            (e: ManagedEmployee) => {
                return !!selectedIds.find((id: any) => id === e.cost1);
            }
        );
    $store.state.EmployeeModule.filteredEmployees = newEmployeeList;

    for (const [key, employeeData] of weekMap.entries()) {
        employeeData.show = false;
    }

    for (const employee of newEmployeeList) {
        const employeeWeek = weekMap.get(
            employee.employeeNo
        );
        employeeWeek.show = true;
    }
}
</script>

<style scoped lang="scss">
@use "../../style/layouts";

$aff-blue: #00aeef;
$aff-dark-blue: #0c96e4;
$border-color: #c8d1db;
.weekDetail {
    font-size: 16px;
    font-weight: bold;

    .arrow-height {
        height: 30px;
        margin-top: 1px;
        border: none;
        cursor: pointer;
    }

    @media screen and (min-width: 992px) {
        font-size: 14px;
    }

    @media screen and (max-width: 991px) {
        padding: 12px 0;
        display: inline-block;
    }

    > * {
        vertical-align: middle;
    }
}
.background-legend {
    display: inline-flex;

    > div {
        white-space: nowrap;
    }

    @media screen and (max-width: 560px) {
        flex-flow: column;

        > div {
            margin: 0 auto 8px;
            width: 100%;
        }
    }
}

.legend-label {
    display: inline-block;
    vertical-align: middle;
    margin-left: 2px;
}

.flexi-legend {
    display: inline-block;
    width: 10px;
    padding: 5px;
    border: 1px solid $border-color;
    background: white;
    vertical-align: middle;

    > div {
        white-space: nowrap;
    }
}

.team-legend {
    display: inline-block;
    width: 10px;
    padding: 5px;
    background: #cfefc6;
    border: 1px solid $border-color;
    vertical-align: middle;
}

.personal-legend {
    display: inline-block;
    width: 10px;
    padding: 5px;
    background: #d1f0ff;
    border: 1px solid $border-color;
    vertical-align: middle;
}


.filters-legends {
    @media screen and (min-width: 1181px) {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .left-box {
            flex: 1;
            display: flex;
            justify-content: flex-start;
            align-items: center;
        }

        .right-box {
            flex: 1;
            display: flex;
            justify-content: flex-end;
            align-items: center;
        }
    }
    @media screen and (max-width: 1180px) {
        > div:nth-child(2),
        > div:last-child {
            display: inline-block;
        }

        .right-box {
            display: inline-block;
            width: calc(100% - 300px);
            text-align: right;
        }
    }
    @media screen and (max-width: 560px) {
        > div:nth-child(2),
        > div:last-child {
            display: block;
            text-align: center;
            width: 100%;
        }
    }
}

.flex-sched-filter {
    @media screen and (min-width: 768px) {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 24px;
    }
    @media screen and (max-width: 767px) {
        position: relative;
        margin-top: 64px;

        > div:last-child {
            position: absolute;
            bottom: calc(100% + 16px);
            right: 0;
        }
    }
}

.action-buttons {
    display: flex;
    flex-direction: column;
    padding: 5px 10px;

    button {
        color: #000;
        border-bottom: 1px solid #c8d1db;
        border-radius: 0 !important;

        &:last-child {
            border-bottom-color: transparent;
        }
    }
}

.action-button-width {
    width: 120px;
}


.inline-block-middle {
    display: inline-block;
    vertical-align: middle;
}
</style>