import Remote from "@/remote/scheduleRequest";
import { ScheduleDay } from "../FlexScheduleInterface";
import { ManagedEmployee } from "@/store/employeeModule";
import _ from "lodash";
import { DATE_FOR_REQUEST_FORMAT, getStartAndEndTimeWithDate } from "../dialogHelper";
import moment from "moment";

export type ScheduleDaySwap = ScheduleDay & {
    employee: ManagedEmployee;
    employeeName: string;
    employeeNo: number;
    scheduleType: string;
    publishStatus: string;
}
export async function swapSche(sourceScheInput: ScheduleDaySwap, targetScheInput: ScheduleDaySwap) {
    console.log("sourceScheInput", sourceScheInput)
    const sourceSche = _.cloneDeep(sourceScheInput)
    const targetSche = _.cloneDeep(targetScheInput)


    
    await deleteSche(sourceScheInput)
    await deleteSche(targetScheInput)
    

    sourceSche.date = targetSche.date;
    sourceSche.employeeNo = targetSche.employeeNo;
    targetSche.date = sourceScheInput.date;
    targetSche.employeeNo = sourceScheInput.employeeNo;

    await createSche(sourceSche)
    await createSche(targetSche)
    
    return true;
}

export function updateSche(selectedFlexDay: any) {
    const date = moment(selectedFlexDay.date).format(DATE_FOR_REQUEST_FORMAT)
    const [startTime, endTime] = getStartAndEndTimeWithDate(
        date,
        selectedFlexDay.startTime,
        selectedFlexDay.endTime
    );

    const putData = {
        Id: selectedFlexDay.id,
        EmployeeNo: selectedFlexDay.employeeNo,
        Date: date,
        StartTime: startTime,
        EndTime: endTime,
        Hours: selectedFlexDay.hours,
        PublishStatus: selectedFlexDay.publishStatus,
        ScheduleType: "Flex",
        ScheduleData: {
            ShiftId: selectedFlexDay.scheduleDetail.shiftId ,
            BreakLength: selectedFlexDay.scheduleDetail.breakLength,
            Position: selectedFlexDay.scheduleDetail.position,
            CostElement1: selectedFlexDay.scheduleDetail.costElement1
                ? selectedFlexDay.scheduleDetail.costElement1.id
                : undefined,
            CostElement2: selectedFlexDay.scheduleDetail.costElement2
                ? selectedFlexDay.scheduleDetail.costElement2.id
                : undefined,
        },
        Notes: selectedFlexDay.notes
    };

    return Remote.PatchFlexSchedule([putData])
}

export function createSche(selectedFlexDay: any) {
    const date = moment(selectedFlexDay.date).format(DATE_FOR_REQUEST_FORMAT)
    const [startTime, endTime] = getStartAndEndTimeWithDate(
        date,
        selectedFlexDay.startTime,
        selectedFlexDay.endTime
    );
    const postData = {
        EmployeeNo: selectedFlexDay.employeeNo,
        Date: date,
        StartTime: startTime,
        EndTime: endTime,
        Hours: selectedFlexDay.hours,
        PublishStatus: selectedFlexDay.publishStatus,
        ScheduleType: "Flex",
        ScheduleData: {
            ShiftId: selectedFlexDay.scheduleDetail.shiftId ? selectedFlexDay.scheduleDetail.shiftId : "",
            BreakLength: selectedFlexDay.scheduleDetail.breakLength,
            Position: selectedFlexDay.scheduleDetail.position,
            CostElement1: selectedFlexDay.scheduleDetail.costElement1
                ? selectedFlexDay.scheduleDetail.costElement1.id
                : "",
            CostElement2: selectedFlexDay.scheduleDetail.costElement2
                ? selectedFlexDay.scheduleDetail.costElement2.id
                :  "",
        },
        Notes: selectedFlexDay.notes
    }
    //
    return Remote.CreateFlexSchedule([postData])
};

export function deleteSche(selectedFlexDay: any) {
    const date = moment(selectedFlexDay.date).format(DATE_FOR_REQUEST_FORMAT)
    const [startTime, endTime] = getStartAndEndTimeWithDate(
        date,
        selectedFlexDay.startTime,
        selectedFlexDay.endTime
    );

    let deleteData = {
        Id: selectedFlexDay.id,
        EmployeeNo: selectedFlexDay.employeeNo,
        Date: date,
        StartTime: startTime,
        EndTime: endTime,
        Hours: selectedFlexDay.hours,
        PublishStatus: selectedFlexDay.publishStatus,
        ScheduleData: {
            ShiftId: selectedFlexDay.scheduleDetail.shiftId ,
            BreakLength: selectedFlexDay.scheduleDetail.breakLength,
            Position: selectedFlexDay.scheduleDetail.position,
            CostElement1: selectedFlexDay.scheduleDetail.costElement1
                ? selectedFlexDay.scheduleDetail.costElement1.id
                : undefined,
            CostElement2: selectedFlexDay.scheduleDetail.costElement2
                ? selectedFlexDay.scheduleDetail.costElement2.id
                : undefined,
        },
        Notes: selectedFlexDay.notes
    };
    return Remote.DeleteFlexSchedule(deleteData)
    // return Remote.DeleteFlexSchedule([selectedFlexDay.id])
}


