<template>
    <div class="mt-4 full-width grid-section">
        <div class="d-flex justify-center mt-4" v-if="noDataError">
            <h3>No data found.</h3>
        </div>
        <div class="dbw-box" v-else>
            <perfect-scrollbar>
                <div class="grid-extra-width">
                    <div class="grid-heading">
                        <template v-for="heading of dataHeadings">
                            <div class="heading-cell">{{ heading }}</div>
                        </template>
                    </div>

                    <div class="grid-body">
                        <perfect-scrollbar>
                        <template v-for="data in dataAnalysis">
                            <div class="grid-body-row">
                                <div
                                    class="grid-body-cell"
                                    v-for="[key, value] of Object.entries(data)">
                                    {{ value }}
                                </div>
                            </div>
                        </template>
                        </perfect-scrollbar>
                    </div>
                </div>
            </perfect-scrollbar>
        </div>
        <div class="pagination-wrap" v-show="!noDataError">
            <paginator
                ref="PaginatorRef"
                :totalPage="totalPages"
                :currentPage="pageNumber"
                @selectPage="selectPage"
            ></paginator>
            <div v-if="recordCount > 0" class="no-records">
                No. of Records: {{ recordCount }}
            </div>
        </div>
    </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref } from "vue";
import moment from "moment";
import Remote from "@/remote/scheduleRequest";
import Dialog from "@/components/dialog";
import Paginator from "@/components/Analysis/Paginator.vue";
import {useStore} from "vuex";
import {AnalysisActions} from "@/store/analysisModule";

declare var Affinity: any;

export default defineComponent({
    name: "SimpleAnalysis",
    components: {
        Paginator,
    },
    props: {
        fromDate: {
            type: Object,
        },
        toDate: {
            type: Object,
        },
        employeeList: {
            type: Array,
        },
    },

    setup(props: any) {
        const store = useStore();
        const data = ref(<any[]>[]);
        const pageNumber = ref(1);
        const pageSize = ref(20);
        const totalPages = ref(5);
        const recordCount = ref(0);
        const noDataError = ref(false);
        const PaginatorRef = ref<InstanceType<typeof Paginator>>();

        const dataHeadings = ref([
            "Employee Name",
            "Employee Number",
            "Functional Area",
            "Date",
            "Award",
            "Schedule Hours",
            "Actual Hours",
            "Hours Variance",
            "Schedule Cost",
            "Actual Cost",
            "Cost Variance",
            "Actual Start",
            "Actual Finish",
            "Adjusted Start",
            "Adjusted Finish",
        ]);

        const dataAnalysis = ref(<any[]>[]);

        onMounted(async () => {
            dataAnalysis.value = [];
            pageNumber.value = 1;
            totalPages.value = 5;
            noDataError.value = false;

            resetData();
            await loadGridData();
            
        });

        async function loadGridData() {
            console.log('load grid data from cost analysis v2')
            noDataError.value = false;

            if (!props.fromDate || !props.toDate) {
                if (!props.fromDate) {
                    props.fromDate = moment().startOf("month");
                }
                if (!props.toDate) {
                    props.toDate = moment().endOf("month");
                }
            }
            Dialog.loading();
            await store.dispatch(AnalysisActions.fetchSimpleViewData, {
                fromDate: props.fromDate ? props.fromDate.format("YYYY-MM-DD") : null,
                toDate: props.toDate ? props.toDate.format("YYYY-MM-DD") : null,
                employeeNumbers: props.employeeList!.filter((employeeNumber: number | null) => employeeNumber !== null && employeeNumber !== undefined),
                pagingEnabled: true,
                pageNumber: pageNumber.value,
                pageSize: pageSize.value
            }).then((res: any) => {
                if (
                    (res.response && res.response.length === 0) ||
                    !res.response
                ) {
                    resetData();
                    noDataError.value = true;
                    return;
                }

                dataAnalysis.value = res.response.map((e: any) => {
                    const a: any = {};
                    a.commonName = e.commonName;
                    a.employeeNo = e.employeeNo;
                    a.functionalArea = e.functionalArea;
                    a.transDate = moment(e.transDate).format("YYYY-MM-DD");
                    a.awardId = e.awardId;
                    a.scheduledHours = roundNumber(e.scheduledUnits);
                    a.actualHours = roundNumber(e.actualUnits);
                    a.hoursVariance = roundNumber(a.actualHours - a.scheduledHours);
                    a.scheduledCost = roundNumber(e.scheduledCost);
                    a.actualCost = roundNumber(e.actualCost);
                    a.costVariance = roundNumber(a.actualCost - a.scheduledCost);
                    a.actStartTime = e.actStartTime ? moment(e.actStartTime).format("HH:mm") : null;
                    a.actEndTime = e.actEndTime ? moment(e.actEndTime).format("HH:mm") : null;
                    a.adjStartTime = e.adjStartTime ? moment(e.adjStartTime).format("HH:mm") : null;
                    a.adjEndTime = e.adjEndTime ? moment(e.adjEndTime).format("HH:mm") : null;
                    return a;
                });
                totalPages.value = res.totalPages;
                pageNumber.value = res.currentPageNumber;
                recordCount.value = res.recordCount;

                PaginatorRef.value!.calculatePageList(
                    res.currentPageNumber,
                    res.totalPages
                );
                return res.response;
            }).finally(() => {
                Dialog.clear();
            });
        }
        function roundNumber(num: number | null): number | null {
            if (num === null) {
                return null;
            }
            return Math.round(num * 100) / 100;
        }

        async function selectPage(newPageNumber: number) {
            pageNumber.value = newPageNumber;
            Dialog.loading();
            await loadGridData();
            Dialog.clear();
        }

        async function exportCSV() {
            if (noDataError.value) {
                Dialog.alert("No data to export");
                return;
            }

            Dialog.loading();
            const allData: any = await loadAllData();
            Dialog.clear();
            let csvContent = "data:text/csv;charset=utf-8,";

            csvContent += dataHeadings.value.join(",") + "\r\n";

            allData.map((rowArray: any) => {
                csvContent += Object.values(rowArray).join(",") + "\r\n";
            });

            var encodedUri = encodeURI(csvContent);
            var link = document.createElement("a");
            link.setAttribute("href", encodedUri);
            link.setAttribute("download", "Analytic.csv");
            document.body.appendChild(link); // Required for FF

            link.click();
        }

        async function loadAllData() {
            const query = {
                pageNumber: 1,
                pageSize: 10000,
                pagingEnabled: false,
                sortBy: "CommonName",
                sortingType: "asc",
                employeeNumbers: props.employeeList.filter((employee: number | null) => employee !== null && employee !== undefined),
                transDateFrom: props.fromDate
                    ? props.fromDate.format("YYYY-MM-DD")
                    : null,
                transDateTo: props.toDate
                    ? props.toDate.format("YYYY-MM-DD")
                    : null,
            };

            return await Remote.GetCostAnalysisV2(query).then((res: any) => {
                return res.response.map((e: any) => {
                    const a: any = {};
                    a.commonName = e.commonName;
                    a.employeeNo = e.employeeNo;
                    a.functionalArea = e.functionalArea;
                    a.transDate = moment(e.transDate).format("YYYY-MM-DD");
                    a.awardId = e.awardId;
                    a.scheduledHours = roundNumber(e.scheduledUnits);
                    a.actualHours = roundNumber(e.actualUnits);
                    a.hoursVariance = roundNumber(a.actualHours - a.scheduledHours);
                    a.scheduledCost = roundNumber(e.scheduledCost);
                    a.actualCost = roundNumber(e.actualCost);
                    a.costVariance = roundNumber(a.actualCost - a.scheduledCost);
                    a.actStartTime = e.actStartTime ? moment(e.actStartTime).format("HH:mm") : null;
                    a.actEndTime = e.actEndTime ? moment(e.actEndTime).format("HH:mm") : null;
                    a.adjStartTime = e.adjStartTime ? moment(e.adjStartTime).format("HH:mm") : null;
                    a.adjEndTime = e.adjEndTime ? moment(e.adjEndTime).format("HH:mm") : null;

                    return a;
                });
            });
        }

        function resetData() {
            pageNumber.value = 1;
            totalPages.value = 5;
            noDataError.value = false;
        }

        return {
            data,
            dataHeadings,
            dataAnalysis,
            PaginatorRef,
            pageNumber,
            pageSize,
            totalPages,
            noDataError,
            recordCount,
            loadGridData,
            exportCSV,
            selectPage,
        };
    },
});
</script>

<style scoped lang="scss">
@use "../../style/colors";
$border-color: #00378033;

.grid-extra-width {
    width: 2000px;
}

.grid-heading {
    display: grid;
    width: 100%;
    grid-template-columns: repeat(3, 1fr) 100px 150px repeat(8, 0.8fr) repeat(
            2,
            1fr
        );
    grid-gap: 1px;
}

.heading-cell {
    padding: 4px 8px;
    text-align: center;
    border-bottom: 1px solid #c8d1db;
}

.grid-body {
    display: grid;
    grid-gap: 1px;
    width: 100%;
    height: calc(100vh - 300px);
    overflow: auto;
    border-top: none;
    border-bottom: 1px solid #c8d1db;
}

.grid-body-row {
    display: grid;
    width: 100%;
    grid-template-columns: repeat(3, 1fr) 100px 150px repeat(8, 0.8fr) repeat(
            2,
            1fr
        );
    grid-gap: 1px;
    text-align: center;
}

.grid-body-cell {
    display: grid;
    align-items: center;
    min-height: 30px;
    border-bottom: 1px solid #c8d1db;
}

.input-box {
    width: 90px !important;
    padding: 4px 12px;
    border: 1px solid #767676;
    font-size: 12px;
    border-radius: 4px;
}

.total-page-small {
    display: flex;
    align-items: center;
    justify-content: center;
    .selected {
        background: #0c96e4;
        color: white;
    }
}
.page-button {
    box-sizing: border-box;
    padding: 0;
    margin-right: 16px;
    text-align: center;
    border-radius: 5px;
    cursor: pointer;
    height: 24px;
    width: 24px;
    line-height: 24px;
    font-weight: 400;
    display: inline-block;

    &:last-child {
        margin-right: 0;
    }
}

.action-button {
    box-shadow: none;
}

:deep(.custom-select) {
    width: 160px;

    .custom-select-selected {
        min-height: auto;
        font-size: 12px;
        background-color: #f7f9fe;
        height: 32px;
    }

    .custom-select-items {
        width: 160px;
    }
}
</style>
