<template>
    <table class="flex-sche-table " id="FlexSchedules">
        <thead>
            <tr class="table-head">
                <th class="table-cell"></th>
                <template v-for="columnOption in selectableColumns" >
                    <th v-if="columnOption && columnOption.selected" class="table-cell">{{columnOption.optionName}}</th>
                </template>
                <th class="table-cell" v-for="weekDay in weekDays">
                    {{ weekDay.day }}
                </th>

            </tr>
        </thead>
        <tbody class="table-body">
            <template v-for="(empNo) in scheduleWeek.keys()">
                <tr class="schedules-arr"  v-if="scheduleWeek.get(empNo) && scheduleWeek.get(empNo).show">
                    <td class="content-cell content-employee-name">
                        <div>{{ scheduleWeek.get(empNo).employeeName }}</div>
                        <div v-if="cellSettings.showPosition.value">
                            <span>{{scheduleWeek.get(empNo).positionTitle }}</span>
                        </div>
                    </td>
                    <template v-for="columnOption in selectableColumns" >
                        <td class="content-cell" v-if="columnOption && columnOption.selected && columnOption.optionName === 'Std Hours'">
                            {{scheduleWeek.get(empNo).stdHoursPerWeek?.toFixed(2) }}
                        </td>
                        <td class="content-cell" v-if="columnOption && columnOption.selected && columnOption.optionName === 'Net Hours'">
                            <!--                                                {{ getTotalHour(scheduleWeekMap.get(empNo).sche) }}-->
                            {{scheduleWeek.get(empNo).netHours }}
                        </td>
                        <td class="content-cell" v-if="columnOption && columnOption.selected && columnOption.optionName === 'Ta Role'">
                            {{scheduleWeek.get(empNo).taRole}}
                        </td>
                        <td class="content-cell" v-if="columnOption && columnOption.selected && columnOption.optionName === 'Worker Type'">
                            {{scheduleWeek.get(empNo).workTypeName}}
                        </td>
                        <td class="content-cell" v-if="columnOption && columnOption.selected && columnOption.optionName === 'Roster Group'">
                            {{scheduleWeek.get(empNo).defaultRosterGroup}}
                        </td>
                    </template>
                    <td class="content-cell" v-for="scheDay in scheduleWeek.get(empNo).sche" v-bind:style="scheDay.column">
    <!--                    <dynamic-day-cell :scheDay="scheDay" @openEditDialog="openEditDialog"></dynamic-day-cell>-->
                        <div>{{ scheDay.startTime }} - {{ scheDay.endTime }}</div>
                        <div v-if="scheDay.leaveUnit" class="leave-unit mt-2">
                            <span v-if="scheDay.leaveUnit.calcUnit === 'D'">Leave: {{scheDay.leaveUnit.daysAppliedFor}} day</span>
                            <span v-else >Leave: {{scheDay.leaveUnit.hoursAppliedFor}} hours</span>
                        </div>
                    </td>
                </tr>
            </template>
        </tbody>
    </table>
</template>

<script lang="ts">
import {ScheduleDay} from "@/components/FlexSchedule/FlexScheduleInterface";
import {defineComponent} from "vue";
import {useStore} from "vuex";
import {FlexScheduleCellSetting} from "@/store/flexScheduleModule";

export default defineComponent({
    name: "PrintableView",
    props: {
        scheduleWeek: {
            type: Object
        },
        weekDays: {
            type: Array
        },
        selectableColumns: {
            type: Array
        }
    },
    setup() {
        const store = useStore()

        const cellSettings: FlexScheduleCellSetting = store.state.FlexSchedule.flexScheduleCellSetting
        return {
            cellSettings
        }
    },
    methods: {
        getTotalHour(sche: ScheduleDay[]) {
            let totalHours = 0;
            for (let day of sche) {
                if (day.hours) {
                    totalHours += day.hours
                }
            }
            return totalHours.toFixed(2)
        },

        printFlexSchedule() {
            var args = "left=5,top=5,resizable=yes,height=600,width=700,scrollbars=yes,status=1";
            var content = (document as any).getElementById("FlexSchedules").outerHTML;
            var mywindow: any = window.open('', 'Print', args);

            // https://stackoverflow.com/questions/65569790/import-scss-as-string
            const printStyle = require("!!css-loader!sass-loader!./PrintableView.scss").toString();

            mywindow.document.write('<html>');
            mywindow.document.write('<head>');
            mywindow.document.write('<style type="text/css">table {border-collapse: collapse;}</style>');
            mywindow.document.write('<style type="text/css">table, th, td {border: 1px solid #cbcbcb;text-align: center;}</style>');
            mywindow.document.write(`<style type="text/css">${printStyle}</style>`);
            mywindow.document.write('</head>');
            mywindow.document.write('<body>');
            mywindow.document.write(content);
            mywindow.document.write('</body></html>');

            mywindow.document.close();
            mywindow.focus()
            mywindow.print();
            mywindow.close();
        }
    }
})
</script>

<style scoped lang="scss">
@use 'PrintableView';
</style>