<template>
    <Modal ref="ModalRef" :no-overflow-auto-body="true">
        <template v-slot:header>
            <div>
                <h2>
                    <b>Publish Flexi Schedule</b>
                </h2>
            </div>
        </template>
        <template v-slot:body>
            <div class="full-width mt-4">
                <div>
                    <span v-if="showPublishByEmployee === false">Are you sure you want to publish the schedule for week {{selectedDate}}?
                        <br/> This will publish the schedule for all employees.</span>
                    <span v-if="showPublishByEmployee === true">Are you sure you want to publish the schedule for week {{selectedDate}}?</span>
                </div>
<!--                <div class="line-break my-2"></div>-->
                <div v-if="showPublishByEmployee === false">
                    <button class="text-button mt-4" @click="showPublishByEmployee = true">
                        <span>Publish by individual employee(s)</span>
                    </button>
                </div>
                <div v-if="showPublishByEmployee === true">
                    <div class="container-half mt-4">
                        <label>Select Employees</label>
                        <multi-select class="mt-2 "
                                      :targetString="'commonName'"
                                      :allowAll="true"
                                      :selectOptionInput="selectOptions"></multi-select>
                    </div>

                </div>
                <div class="line-break mt-4"></div>

            </div>

        </template>
        <template v-slot:footer>
            <div class="d-flex justify-end full-width mt-4">
                <button class="blue xsmall white--text mr-2" @click="publish">Publish</button>
                <button class="orange xsmall white--text" @click="cancel">Cancel</button>
            </div>
        </template>

    </Modal>
</template>

<script lang="ts">
import {defineComponent} from "vue";
import Modal from "@/components/common/Modal.vue";
import moment, {Moment} from "moment";
import DatePicker from "@/components/common/DatePicker.vue";
import Remote from '@/remote/scheduleRequest'
import Dialog from "@/components/dialog";
import MultiSelect from "@/components/common/MultiSelect.vue";
import {WorkType} from "@/store/lookupModule";
import {EmployeeGetters, ManagedEmployee} from "@/store/employeeModule";

export default defineComponent({
    name: "PublishDialog",
    components: {
        Modal,
        MultiSelect
    },
    data() {
        return {
            selectOptions: <any[]>[],
            selectedDate: '',
            showPublishByEmployee: false
        }
    },
    mounted() {
        // this.toggleShow();
        // this.left = (this.$refs as any).selectRef.getClientRects()[0].left;
        this.showPublishByEmployee = false;
        this.selectOptions = this.$store.getters[EmployeeGetters.getFilteredEmployees].slice()
    },
    methods: {
        selectOption() {

        },
        resetData() {
          //  this.datePickerTop = 0;
            this.showPublishByEmployee = false;
        },

        publish() {
            if (!this.showPublishByEmployee) {
                const selectedEmployees = this.selectOptions.map((employee: any) => {
                    return employee.employeeNo
                })
                this.$emit('publish', selectedEmployees);
                (this.$refs.ModalRef as any).toggleShow();
                return;
            }

            if (this.selectOptions[0].isAll && this.selectOptions[0].isSelected) {
                this.publishAllEmployee();
                return;
            }

            let selectedEmployees = this.selectOptions.filter((option: any) => {
                return option.isSelected
            }).map((employee: any) => {
                return employee.employeeNo
            })

            this.$emit('publish', selectedEmployees);
            (this.$refs.ModalRef as any).toggleShow();
        },

        publishAllEmployee() {
            let publishList = this.selectOptions.slice()
            publishList.shift();
            const selectedEmployees = publishList.map((employee: any) => {
                return employee.employeeNo
            })
            this.$emit('publish', selectedEmployees);
            (this.$refs.ModalRef as any).toggleShow();
        },

        cancel() {
            this.toggleShow();
        },
        setSelectedDate(date: string) {
            this.selectedDate = date
        },
        toggleShow() {
            console.log('toggle show')
            this.resetData();
            this.selectOptions = this.$store.getters[EmployeeGetters.getFilteredEmployees].slice() ;

            (this.$refs.ModalRef as any).toggleShow();
        },

    }
})
</script>

<style scoped lang="scss">
//@use '../../style/colors';
.line-break {
    border-top: 1px solid rgba(#7e7e7e, 0.5);
}
:deep(.custom-select) {
    width: 300px;
    .custom-select-selected {
        max-width: 300px;
        width: 100%;
    }

    .custom-select-items {
        width: 300px;
    }

}

.text-button {
    background: transparent;
    border: none;
    box-shadow: none;
    padding: 0;
    color: #0C96E4;
}

</style>