<template>
    <div class="cell-config-list costCenter-wrapper">
        <div>
            <div class="custom-select" tabindex="1" >
                <div class="custom-select-selected">
                    Select Cost Centre
                </div>
                <div class="custom-select-items">
                    <div class="custom-select-item"
                         v-for="(option, name) of costCenterOptions" :key="name"
                         @click="checkOptions(option);">
                        <div class="cell-config">
                            <label v-tooltip.top="{content: option.description}">
                                {{ option.description }}
                            </label>
                            <input type="checkbox" v-model="option.selected" />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script lang="ts">
import {defineComponent, ref} from "vue";
import {useStore} from "vuex";
import {FlexScheduleCellSetting} from "@/store/flexScheduleModule";
import {nextTick} from "@vue/runtime-core";

export default defineComponent({
    name: "FlexScheduleCostCenter",

    props: {
        costCenterOptions: {
            type: Array,
        }
    },

    setup() {
        const store = useStore()
        const open = ref(false)
        const optionSelected = ref([])

        const costCenters = store.state.LookUp.costCenters;
        return {open, costCenters, optionSelected}
    },
    methods: {
        checkOptions(option: any) {
            // todo: trigger when user click on the row
            option.selected = !option.selected

            if (option.costCentreId === -1 && this.costCenterOptions) {
                for (const option of this.costCenterOptions) {
                    (option as any).selected = false;
                }

                (this.costCenterOptions as any)[0].selected = true;

            } else {
                (this.costCenterOptions as any)[0].selected = false;
            }

            this.$emit('selectOption', this.costCenterOptions)
        }
    }

})
</script>

